import * as constants from './constants.visualization';
import defaultStore from './default-store.visualization';

export default (state = {}, action) => {
	switch (action.type) {
	case constants.FETCH_VISUALIZATION_DATA_SUCCEEDED:
		return {
			...state,
			datasets: action.payload.datasets,
			styleConfigurations: action.payload.styleConfigurations,
			clickBehaviors: action.payload.clickBehaviors,
		};

	case constants.FETCH_VISUALIZATION_DATA_FAILED:
		return {
			...defaultStore,
			error: action.payload,
		};

	default:
		return state;
	}
};
