import getStyleConfiguration from '../../helpers/get-style-configuration';
import {
	SHOW_CHART_TITLE,
	CHART_TITLE,
	CHART_TITLE_SIZE,
	CHART_TITLE_COLOR,
} from './constants.title';
import applyReportingTitlePositionLogic from './apply-reporting-title-position-logic';
import isObjectEmpty from '../../helpers/is-object-empty';

export default (chartConfiguration = {}) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);

	if (!mainStyleConfiguration[SHOW_CHART_TITLE]
		|| isObjectEmpty(mainStyleConfiguration) || !mainStyleConfiguration[CHART_TITLE])
		return {
			title: {
				text: null,
			},
		};

	return {
		title: {
			text: mainStyleConfiguration[CHART_TITLE],
			style: {
				fontSize: mainStyleConfiguration[CHART_TITLE_SIZE],
				color: mainStyleConfiguration[CHART_TITLE_COLOR],
			},
			...applyReportingTitlePositionLogic(mainStyleConfiguration),
		},
	};
};
