/**
 * Returns a unique id for comments series
 * @param {string} type - Could be 'comments' or 'initiatives'
 * @param {number} seriesIndex - the series index the comments series belongs to
 * @param {number} datasetIndex - the datasets index the comments series belongs to
 * @returns {string} - will be used to map each annotationsConfig and annotationsData
 */
export default (type, seriesIndex, datasetIndex) => {
	return type + '_' + seriesIndex.toString() + '_' + datasetIndex.toString();
};
