/**
* Get the css width and height given the xenolith options
* to apply those to the white background the chart is drawn on top of
* @param {object} options - Xenolith options
*/
export default (options) => {
	let width;
	let height;
	if (!options || !options.width || options.width === 'auto')
		width = '100%';
	else
		width = options.width;

	if (!options || !options.height || options.height === 'auto')
		height = '100%';
	else
		height = options.height;

	return {
		height,
		width,
	};
};
