/* eslint no-restricted-globals: ["off", "location"] */
import {
	SHOW_LEGEND,
	LEGEND_HORIZONTAL_ALIGNMENT,
	LEGEND_VERTICAL_ALIGNMENT,
	LEGEND_BACKGROUND_COLOR,
	SHOW_LEGEND_BORDER,
	LEGEND_BORDER_WIDTH,
	LEGEND_BORDER_COLOR,
	LEGEND_LABEL_MAX_LENGTH,
} from './constants.legend';
import getStyleConfigurations from '../../helpers/get-style-configuration';
import applyReportingLegendPositionLogic from './apply-reporting-legend-position-logic';
import isObjectEmpty from '../../helpers/is-object-empty';
import { RTL } from '../../constants';


export default (chartConfiguration = {}) => {
	const mainStyleConfiguration = getStyleConfigurations(chartConfiguration, 0);

	if (isObjectEmpty(mainStyleConfiguration) || !mainStyleConfiguration[SHOW_LEGEND])
		return {
			legend: {
				enabled: false,
			},
		};

	let legendOptions = {
		enabled: true,
		verticalAlign: mainStyleConfiguration[LEGEND_VERTICAL_ALIGNMENT],
		align: mainStyleConfiguration[LEGEND_HORIZONTAL_ALIGNMENT],
		backgroundColor: mainStyleConfiguration[LEGEND_BACKGROUND_COLOR],
		rtl: mainStyleConfiguration[RTL],
	};


	if (mainStyleConfiguration[SHOW_LEGEND_BORDER]) {
		legendOptions.borderWidth = mainStyleConfiguration[LEGEND_BORDER_WIDTH];
		legendOptions.borderColor = mainStyleConfiguration[LEGEND_BORDER_COLOR];
	}

	if (['right', 'left'].indexOf(mainStyleConfiguration[LEGEND_HORIZONTAL_ALIGNMENT]) >= 0)
		legendOptions.layout = 'vertical';

	// applies the legend label formatter
	legendOptions = {
		...legendOptions,
		labelFormatter() {
			const currentDatasetIndex = this.userOptions.datasetIndex || 0;
			const currentStyleConfiguration = getStyleConfigurations(chartConfiguration, currentDatasetIndex);
			if (currentStyleConfiguration &&
				(typeof currentStyleConfiguration[LEGEND_LABEL_MAX_LENGTH] === 'number') &&
				!isNaN(currentStyleConfiguration[LEGEND_LABEL_MAX_LENGTH]))
				return this.name.substring(0, currentStyleConfiguration[LEGEND_LABEL_MAX_LENGTH]);

			return this.name;
		},
	};

	legendOptions = {
		...legendOptions,
		...applyReportingLegendPositionLogic(mainStyleConfiguration),
	};

	return {
		legend: legendOptions,
	};
};
