import { createCustomElement } from '@servicenow/ui-core';
import { preUserData } from 'sn-uxpage-presource';
import { t } from 'sn-translate';
import headerActions from './actions';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './hrBarChart.scss';
import '@servicenow/now-chart-bar';
import 'sn-hr-shared';

const view = ({
	accessibilityMode,
	accessibilityConfig,
	accessibilityTable,
	chartTitle,
	closedHrCases,
	isError,
	xLabel,
	yLabel,
	properties: {
		colorPalette,
		height
	}} = state ) => {

	// if error, show empty
	if (isError)
		return (
			<div className="sn-hr-bar-chart empty-state-wrapper">
				<sn-hr-empty-state type="error" />
			</div>
		);

	// if closedHrCases doesn't exist show loading screen
	if (!closedHrCases)
		return (
			<div className="sn-hr-bar-chart empty-state-wrapper loading">
				<sn-hr-empty-state type="loading" />
			</div>
		);

	// if no items in closedHrCases, show empty
	if (!closedHrCases.length)
		return (
			<div className="sn-hr-bar-chart empty-state-wrapper">
				<sn-hr-empty-state type="empty" />
			</div>
		);

	const chartOption = {
		accessibilityMode,
		accessibilityConfig,
		accessibilityTable,
		width: 'auto',
		height: height,
		type: 'grouped',
		showTooltip: true,
		hideRegister: false,
		interactions: ['showDataOnHover', 'getClosestDataOnClick'],
		xAxisConfig: { title: xLabel },
		yAxisConfig: { title: yLabel },
		seriesConfig: {
			mine: {
				color: (colorPalette.split(',')[0] || '#8686BD'),
				label: t('My Closed Cases'),
				x: 'month',
				y: 'mine'
			},
			team: {
				color: (colorPalette.split(',')[1] || '#72CFE2'),
				label: t('Teams\'s Average'),
				x: 'month',
				y: 'team'
			},
		},
		seriesData: [...closedHrCases],
		registerConfig: { justifyItems: 'end' },
		yFormat: '.0f',
	};
	
	return (
		<section tabindex="0" aria-label={ t("Bar chart {0}",chartTitle) } className={ 'sn-hr-bar-chart' }>
			<h3>{ chartTitle }</h3>
			<now-chart-bar { ...chartOption } />
		</section>
	);
};

createCustomElement('sn-hr-bar-chart', {
	properties: {
		height: {default: '210'},
		colorPalette: {default: '#8686BD,#72CFE2'},
		teamReportSysId: {default: '80c63a12b38c1010f5302ddc16a8dc98'}
	},
	actions: headerActions.actions,
	actionHandlers: headerActions.actionHandlers,
	behaviors: [preUserData],
	renderer: {
		type: snabbdom,
		view
	},
	styles
});
