import {
	INITIATIVES_TOOLTIP_NAME,
	COMMENTS_TOOLTIP_NAME,
	TARGET_NAME,
	PERSONAL_TARGET_NAME,
} from '../series/time-series/constants.time-series';
import { getValueWithUnit } from 'helpers/value/index';
import findNearestTargetValue from './find-nearest-target-value';
import { stringHelper, dateHelper, i18nHelper } from 'par-components';

const { escape, nl2br } = stringHelper;

const TARGET_START_HEADER_TEXT = i18nHelper.t('Target start date');
const TARGET_REVIEW_HEADER_TEXT = i18nHelper.t('Target review date');

const formatTooltipLine = (seriesName, color, yValue) => {
	return '<span style="vertical-align: sub; color:' + color + '; font-size: 22px">\u25CF</span> '
	+ escape(seriesName) + ': <span style="font-weight: 700">' + escape(yValue) + '</span></br>';
};

const buildHeaderText = strArr => ('<div style="font-weight: 700">'
				+ escape(strArr.join(' '))
				+ '</div>');

export default (xValue, points, series, frequency = 'daily', i18Translations, isYoy = false, precision = false, unit = false, fiscalYearStart = false, locale, tooltipText) => {
	let headerText = dateHelper.readableDate(xValue, { frequency: frequency, type: 'long', fiscalYearStart: fiscalYearStart });

	if (frequency === 'biz_calendar_frequency' && tooltipText !== undefined)
		headerText = tooltipText;

	if (isYoy) {
		const regexYear = /,?\s*\d{4}/;
		headerText = headerText.replace(regexYear, '');
	}

	const header = buildHeaderText([headerText]);
	const label = [header];
	const footer = [];

	// if there is a target/personal target series,
	// find the closest target/personal value for the xValue
	const targetSeries = series.filter((singleSeries) => {
		return singleSeries.name === TARGET_NAME;
	});
	let closestTarget;
	if (targetSeries.length > 0 && targetSeries[0].data)
		closestTarget = findNearestTargetValue(xValue, targetSeries[0].data, true);

	const personalTargetSeries = series.filter((singleSeries) => {
		return singleSeries.name === PERSONAL_TARGET_NAME;
	});
	let closestPersonalTarget;
	if (personalTargetSeries.length > 0 && personalTargetSeries[0].data)
		closestPersonalTarget = findNearestTargetValue(xValue, personalTargetSeries[0].data, true);

	points.forEach((currentPoint, index) => {
		let seriesName = '';
		if (currentPoint.series && currentPoint.series.name) {
			seriesName = currentPoint.series.name;
			if (currentPoint.point && currentPoint.point.scoresName)
				seriesName = currentPoint.point.scoresName + ' : ' + seriesName;
		}

		let yValue;
		const formattingOptions = {
			precision,
			unit,
			tooltip: true,
			locale,
		};
		// for band series: tooltip should display a range
		// otherwise the y value
		if (currentPoint.series.type === 'arearange')
			yValue = getValueWithUnit(currentPoint.point.high, formattingOptions) +
				' - ' +
				getValueWithUnit(currentPoint.point.low, formattingOptions);
		else
			yValue = getValueWithUnit(currentPoint.point.y, formattingOptions);

		// for comments and initiatives, add the text to the tooltip
		if ([COMMENTS_TOOLTIP_NAME, INITIATIVES_TOOLTIP_NAME].indexOf(seriesName) >= 0
			&& currentPoint.point.text)
			footer.push('<br />' + nl2br(escape(currentPoint.point.text), false));
		// for all the series but the target, add the y value to the tooltip
		else if ([TARGET_NAME, PERSONAL_TARGET_NAME].indexOf(seriesName) < 0)
			label.push(formatTooltipLine(seriesName, currentPoint.color, yValue));

		if (index === points.length - 1) {
			if (closestTarget)
				label.push(formatTooltipLine(i18Translations.target, targetSeries[0].color, getValueWithUnit(closestTarget.value, formattingOptions)));

			if (closestPersonalTarget)
				label.push(formatTooltipLine(i18Translations.personalTarget, personalTargetSeries[0].color, getValueWithUnit(closestPersonalTarget.value, formattingOptions)));

			if ((closestTarget && closestTarget.hasReviewDate && closestTarget.target.x === xValue) || (closestPersonalTarget && closestPersonalTarget.hasReviewDate && closestPersonalTarget.target.x === xValue))
				label[0] = buildHeaderText([headerText, '-', TARGET_START_HEADER_TEXT]);
			if ((closestTarget && closestTarget.target && closestTarget.target.isReview && closestTarget.target.x === xValue) || (closestPersonalTarget && closestPersonalTarget.target && closestPersonalTarget.target.isReview && closestPersonalTarget.target.x === xValue))
				label[0] = buildHeaderText([headerText, '-', TARGET_REVIEW_HEADER_TEXT]);
		}
	});

	label.push(footer.join(''));
	return '<div id="tooltip" aria-live="assertive">' + label.join('') + '</div>';
};
