import getSeriesYKey from '../helpers/get-series-y-key';
import getSeriesId from '../helpers/get-series-id';

export default (params) => {
	const {
		seriesIndex,
		datasetIndex,
		bandType, // confidence_band or prediction_band
		bandName, // Confidence Band or Prediction Band
		yAxisName,
		singleSeries,
	} = params;
	const { color, name } = singleSeries.derived_series[bandType];
	return {
		[getSeriesId(seriesIndex, datasetIndex, bandType)]: {
			x: 'x',
			y0: getSeriesYKey(seriesIndex, datasetIndex, bandType + '_y0'),
			y1: getSeriesYKey(seriesIndex, datasetIndex, bandType + '_y1'),
			axis: yAxisName,
			label: name || bandName,
			...(color ? { color } : {}),
		},
	};
};
