import { COMPONENT_BOOTSTRAPPED } from '@servicenow/ui-core/lib/constants';
import { ACTIONS, CONSTANTS } from './helper/constants';
import { handleDrillDown } from './helper/effects';
import { createHttpEffect } from "@servicenow/ui-effect-http";
import { createAmbSubscriptionEffect } from '@seismic/effect-amb';

export default {
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state: { properties: { reportSysId } }, dispatch }) => {
			dispatch([ACTIONS.FETCH_REPORT_DATA], { reportSysId });
		},

		[ACTIONS.FETCH_REPORT_DATA]: createHttpEffect(
			CONSTANTS.REPORT_QUERY_URL, {
				method: 'POST',
				pathParams: ['reportSysId'],
				batch: false,
				successActionType: ACTIONS.REPORT_DATA_FETCH_SUCCESS,
				errorActionType: ACTIONS.FAIL,
			}),

		[ACTIONS.REPORT_DATA_FETCH_SUCCESS]: ({
			state: { isSubscribed },
			properties: { height },
			action: { payload: { result: { payload } } },
			updateState,
			dispatch }) => {
				const {
					chartTitle,
					seriesConfig: {
						// dynamic key - need to grab the first key of seriesConfig and rename to 'series1'
						[Object.keys(payload.seriesConfig)[0]]: series1
					}
				} = payload;

				let chartOptions = {
					...payload,
					tooltipConfig: {
						showPercentages: false
					},
					yFormat: '.0f',
				};

				chartOptions.interactions = ['getDataOnClick'];

				// report returns height as 'auto', need to override
				chartOptions.height = height;

				// override key as 'series1', chart will throw error otherwise
				chartOptions.seriesConfig = { series1 };

				// need to remove type: 'donut' for accessibility
				delete chartOptions.seriesConfig.series1.type;

				// need to show the register when user has accessibility on
				chartOptions.hideRegister = !chartOptions.accessibilityMode;

				updateState({
					chartTitle,
					chartOptions,
					isLoading: false
				});

				if (!isSubscribed) {
					// AMB only will work with base64-encoded query
					let filter = btoa(unescape(encodeURIComponent(series1.filterQuery)))
						.replace(/=/g, '-');

					dispatch(ACTIONS.AMB_SUBSCRIPTION_START, {
						table: series1.table_name,
						filter,
						subscribe: true
					});
				}
		},

		[ACTIONS.AMB_SUBSCRIPTION_START]: createAmbSubscriptionEffect(
			CONSTANTS.AMB_SUBSCRIPTION_URL, {
				subscribeSucceededActionType: ACTIONS.RECORD_WATCHER_SUBSCRIBED,
				messageReceivedActionType: ACTIONS.RECORD_WATCHER_ITEM_CHANGED,
				subscribeFailedActionType: ACTIONS.FAIL
			}
		),

		[ACTIONS.RECORD_WATCHER_SUBSCRIBED]: ({ updateState }) => {
			updateState({ isSubscribed: true });
		},

		[ACTIONS.RECORD_WATCHER_ITEM_CHANGED]: ({ state: { properties: { reportSysId }}, dispatch }) => {
			dispatch([ACTIONS.FETCH_REPORT_DATA], { reportSysId });
		},

		[ACTIONS.FAIL]: ({ action: { payload: { data }}, updateState }) => {
			if (data.error && data.message) {
				updateState({ isError: true });
				console.error(data.message);
			}
		},

		[ACTIONS.NOW_CHART_DONUT_PIE_CLICK]: handleDrillDown,

		[ACTIONS.DETAIL_LINK_CLICKED]: handleDrillDown
	}
};
