import getSeriesYKey from '../helpers/get-series-y-key';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
		seriesType,
	} = params;
	const seriesId = seriesType || 'data';
	return singleSeries.data.map((dataPoint) => {
		// support for array data points
		if (Array.isArray(dataPoint))
			return {
				[getSeriesYKey(seriesIndex, datasetIndex, seriesId)]: dataPoint[1],
				x: dataPoint[0],
			};

		return {
			[getSeriesYKey(seriesIndex, datasetIndex, seriesId)]: dataPoint.y,
			x: dataPoint.x,
		};
	});
};
