import getSeriesYKey from '../helpers/get-series-y-key';
import getSeriesId from '../helpers/get-series-id';
import { supportMarker } from '../helpers/support-marker';

export default (params) => {
	const {
		datasetStyleConfig,
		color,
		seriesName,
		seriesIndex,
		datasetIndex,
		seriesType,
		yAxisName,
	} = params;
	const seriesId = seriesType || 'data';
	const chartType = datasetStyleConfig.chart_type;
	let seriesConfig = {};
	let type;

	// stacked bars or grouped bars
	if (chartType === 'bar' && !datasetStyleConfig.stack_datasets)
		type = ['groupedBar'];
	else
		type = [chartType];

	// markers
	if (supportMarker(chartType) && datasetStyleConfig.show_marker)
		type.push('scatter');

	seriesConfig = {
		...seriesConfig,
		y: getSeriesYKey(seriesIndex, datasetIndex, seriesId),
		showGaps: datasetStyleConfig.show_gaps,
		type,
		color,
		strokeWidth: 2,
		x: 'x',
		axis: yAxisName,
		label: seriesName || seriesType,
	};

	return {
		[getSeriesId(seriesIndex, datasetIndex, seriesId)]: seriesConfig,
	};
};
