/**
* Builds the chart size
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API
*/
export default (chartConfiguration) => {
	if (chartConfiguration.clickBehaviors &&
	Array.isArray(chartConfiguration.clickBehaviors) &&
	typeof chartConfiguration.clickBehaviors[0] === 'function') {
		const onClickAction = chartConfiguration.clickBehaviors[0];
		/*
			The actions for mousemove and mouseout are needed
			for the tooltip to keep working
			this.getClosestDatasetsToMouse returns an objects
			that contains all the points for the x where clicked
		*/
		return {
			actions: [
				{
					event: 'mousemove',
					searchCB: 'getMouseCoords',
					resultCB: 'getClosestData',
				},
				{
					event: 'mouseout',
					searchCB: 'clearCoords',
					resultCB: 'clearData',
				},
				{
					event: 'click',
					searchCB: 'getMouseCoords',
					resultCB(value) {
						onClickAction(this.getClosestDatasetsToMouse(value));
					},
				},
			],
		};
	}
	return {};
};
