import buildThresholdData from './build-threshold-data';
import buildThresholdsConfig from './build-threshold-config';

/**
* Builds the threshold series
* @param {object} params - object that contains the following:
* datasetStyleConfig - styleConfiguration of the dataset that contains the current series
* singleSeries - the current series with its derived series (confidence band, target, threshold, etc...)
* color - color for this series, either in hexa or rgba
* seriesIndex - index of the current series in the dataset
* datasetIndex - index of the current dataset in chartConfiguration.datasets
*/
export default (params) => {
	const { singleSeries } = params;
	const thresholds = singleSeries.derived_series.thresholds;

	let globalThresholds = {};
	if (thresholds.global && thresholds.global.length)
		globalThresholds = {
			thresholdConfig: buildThresholdsConfig({
				...params,
				isGlobalThreshold: true,
			}),
			thresholdData: buildThresholdData({
				...params,
				isGlobalThreshold: true,
			}),
		};

	let personalThresholds = {};
	if (thresholds.personal && thresholds.personal.length)
		personalThresholds = {
			thresholdConfig: buildThresholdsConfig({
				...params,
				isGlobalThreshold: false,
			}),
			thresholdData: buildThresholdData({
				...params,
				isGlobalThreshold: false,
			}),
		};

	return {
		thresholdData: [
			...(personalThresholds.thresholdData || []),
			...(globalThresholds.thresholdData || []),
		],
		thresholdConfig: {
			...(personalThresholds.thresholdConfig || {}),
			...(globalThresholds.thresholdConfig || {}),
		},
	};
};
