import {
	HR_CARD_SET_SCROLLED,
	MAX_RECURSIVE_SCROLL_DISPATCHES,
	RECURSIVE_SCROLL_WAIT_MS
} from './hrCardSetConstants';

export const dispatchScroll = (dispatch, cardSetRef, scrollTimeout, scrollTimeoutIterations) => {
	scrollTimeoutIterations = scrollTimeoutIterations || 0;

	const { scrollLeft, offsetWidth, scrollWidth } = cardSetRef.current;

	// This timeout hack is used to fix a bug where
	// the offsetWidth of the outer container of the card set
	// may be 0 when rendering on the landing page, causing
	// the arrows to never render

	// If we've already been waiting for a timeout, clear it
	if (scrollTimeout)
		clearTimeout(scrollTimeout);

	// If the offset width is now bigger than zero, dispatch the scroll event
	// to render the arrows.
	if (offsetWidth > 0) {
		dispatch(HR_CARD_SET_SCROLLED, { scrollLeft, offsetWidth, scrollWidth })
		return;
	}

	// If the offsetWidth is 0 and we've reached out limit
	// of tries, return
	if (scrollTimeoutIterations > MAX_RECURSIVE_SCROLL_DISPATCHES) {
		dispatch(HR_CARD_SET_SCROLLED, { scrollLeft, offsetWidth, scrollWidth })
		return;
	}

	// If we haven't reached out limit of tries, wait, then try
	// to re-render the arrows
	scrollTimeout = setTimeout(() => {
		dispatchScroll(dispatch, cardSetRef, scrollTimeout, scrollTimeoutIterations + 1);
	}, RECURSIVE_SCROLL_WAIT_MS);
};

export const hexToRGB = hexColor => {
	if (!hexColor)
		return null;

	if (!hexColor.startsWith('#') || (hexColor.length !== 4 && hexColor.length !== 7)) {
		const msg = `Invalid hex color: ${hexColor}. Color must be hexadecimal, starting with #`;
		console.error(msg);
		throw msg;
	}

	const rgb = [];
	if (hexColor.length === 4) {
		for(let i = 1; i < hexColor.length; i++) {
			const dec = parseInt(hexColor[i] + hexColor[i], 16)
			if (!isNaN(dec))
				rgb.push(dec);
			else {
				const msg = `Invalid hex color: ${hexColor}`;
				console.error(msg);
				throw msg;
			}
		}
	}
	else {
		for (let i = 1; i < hexColor.length; i += 2) {
			const dec = parseInt(hexColor[i] + hexColor[i + 1], 16);
			if (!isNaN(dec))
				rgb.push(dec);
			else {
				const msg = `Invalid hex color: ${hexColor}`;
				console.error(msg);
				throw msg;
			}
		}
	}

	return rgb;
};
