import getSeriesId from '../helpers/get-series-id';

export default (params) => {
	const {
		seriesIndex,
		datasetIndex,
		isGlobalThreshold,
		singleSeries,
	} = params;
	const thresholdId = isGlobalThreshold ? 'threshold' : 'personal_threshold';
	const stroke = isGlobalThreshold ? singleSeries.derived_series.thresholds.global[0].color : singleSeries.derived_series.thresholds.personal[0].color; // line color
	return {
		[getSeriesId(seriesIndex, datasetIndex, thresholdId)]: {
			for: getSeriesId(seriesIndex, datasetIndex, thresholdId),
			stroke,
			fontSize: 11,
		},
	};
};
