import * as chartLibraries from 'component/visualization/chart-library-names';

/**
* Maps the user provided chart type to Highcharts type or Xenolith type
* @param {string} chartType - chart_type given in the styleConfigurations
* @returns {string} - returns a Highcharts chart type or a Xenolith chart type
*/

// TODO: move to the parser
export default (chartType, chartLibrary = chartLibraries.HIGHCHARTS) => {
	if (chartLibrary === chartLibraries.XENOLITH)
		switch (chartType) {
		case 'column':
		case 'line_bar':
		case 'trend':
			return 'bar';

		case 'step_line':
			return 'step';
		}

	return chartType;
};
