export function includeReviewDates(data) {
	if (Array.isArray(data))
		return data.reduce((result, dataPoint) => {
			result.push(dataPoint);
			if (dataPoint.review_date)
				result.push({ y: dataPoint.y, x: dataPoint.review_date, isReview: true, marker: { enabled: true, symbol: 'diamond', radius: 6 } });
			return result;
		}, []);
	return data;
}
