import React from 'react';
import PropTypes from 'prop-types';

import { getTableHeadData } from './helpers/get-table-head-data';
import { getTableBodyData } from './helpers/get-table-body-data';
import { getChartTitle } from './helpers/get-chart-title';

import { buildRow } from './helpers/build-row';

// Style
import './component.data-table.scss';

export default class extends React.Component {
	static propTypes = {
		chartConfiguration: PropTypes.object,
	};

	static defaultProps = {
		chartConfiguration: {},
	};

	static displayName = 'DataTable';

	render() {
		const { chartConfiguration } = this.props;
		const tableHeadData = getTableHeadData(chartConfiguration);
		const dataPoints = getTableBodyData(chartConfiguration);
		const chartTitle = getChartTitle(chartConfiguration);
		return (
			<div className="par-data-table">
				<table>
					{chartTitle &&
						<caption>
							{chartTitle}
						</caption>
					}
					<thead>
						<tr>
							{tableHeadData.map((seriesName, index) => {
								const key = `tbl-thead-th-0-${index}`;
								if (!seriesName)
									return <td className="empty-th" key={key} />;

								return <th scope="col" key={key}>{seriesName}</th>;
							})}
						</tr>
					</thead>
					<tbody>
						{dataPoints.map((dataPoint, index) => {
							const trKey = `tbl-tbody-tr-${index + 1}-0`;
							return (
								<tr key={trKey}>
									{buildRow(dataPoint, chartConfiguration, index + 1)}
								</tr>
							);
						})}
					</tbody>
				</table>
			</div>
		);
	}
}
