import { all, call, takeEvery, put } from 'redux-saga/effects';

import * as constants from './constants.visualization';

// API
import api from './api.visualization';

function* fetchDataForConfigurations(action) {
	const dataConfigurations = action.payload.dataConfigurations;
	try {
		const result = yield call(api.fetchDataForConfigurations.bind(api), dataConfigurations);
		yield put({
			type: constants.FETCH_VISUALIZATION_DATA_SUCCEEDED,
			payload: result,
		});
	} catch (err) {
		// err is not a JS Error object, but rather our own custom object which
		// also holds the type of the error (such as permission error)
		yield put({
			type: constants.FETCH_VISUALIZATION_DATA_FAILED,
			payload: err,
			error: true,
		});
	}
}

function* fetchDataForSysId(action) {
	const sysId = action.payload.sysId;
	try {
		const result = yield call(api.fetchDataForSysId.bind(api), sysId);
		yield put({
			type: constants.FETCH_VISUALIZATION_DATA_SUCCEEDED,
			payload: result,
		});
	} catch (err) {
		// err is not a JS Error object, but rather our own custom object which
		// also holds the type of the error (such as permission error)
		yield put({
			type: constants.FETCH_VISUALIZATION_DATA_FAILED,
			payload: err,
			error: true,
		});
	}
}

/**
 * Flow of actions for the visualization
 */
export default function* visualizationSaga() {
	// Common sagas to fork
	yield all([
		takeEvery(constants.SET_DATA_CONFIGURATIONS, fetchDataForConfigurations),
		takeEvery(constants.SET_VISUALIZATION_SYS_ID, fetchDataForSysId),
	]);
}
