import getStyleConfiguration from '../../../helpers/get-style-configuration';
import buildTickConfig from '../axis-tick/';
import { buildXFormat } from './build-x-format';
import getFrequency from 'parser/helpers/get-frequency';

/**
* Builds the xAxisConfig
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API
*/

export default (chartConfiguration) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const xAxisConfig = {
		title: mainStyleConfiguration.x_axis_title,
		orientation: mainStyleConfiguration.x_axis_opposite ? 'top' : 'bottom',
		tickConfig: buildTickConfig({
			numberOfTicks: mainStyleConfiguration.x_number_of_ticks,
			enableLabels: mainStyleConfiguration.x_axis_labels_enabled,
		}),
		autoRotateLabels: mainStyleConfiguration.x_axis_auto_rotate_labels !== false,
	};
	const formattingOptions = {
		isXAxis: true,
		frequency: getFrequency(chartConfiguration),
		type: 'short',
		fiscalYearStart: mainStyleConfiguration.fiscal_year_start,
	};

	return {
		xAxisConfig,
		xGridConfig: {
			dashPattern: mainStyleConfiguration.x_axis_grid_dotted ? '1,3' : '0',
		},
		hideXGridlines: !mainStyleConfiguration.x_axis_display_grid,
		...buildXFormat(mainStyleConfiguration, formattingOptions),
		alignTicks: mainStyleConfiguration.x_axis_align_ticks !== false,
	};
};
