import {HR_CARD_SET_SCROLLED} from './hrCardSetConstants';

export default {
	/**
	 * Internally dispatched when the card set is scrolled,
	 * either with the arrows or with the mouse
	 */
	[HR_CARD_SET_SCROLLED]: ({ state, updateState, action }) => {
		const { scrollLeft, offsetWidth, scrollWidth } = action.payload;

		// Show the right arrow if we haven't scrolled to the end
		const newShowScrollRight = scrollLeft + offsetWidth < scrollWidth;

		// Show the left arrow if we haven't scrolled to the beginning
		const newShowScrollLeft = scrollLeft > 0;

		// If the display of the left or right arrows has changed, re-render
		if (newShowScrollRight != state.showScrollRight || newShowScrollLeft != state.showScrollLeft) {
			updateState({
				showScrollRight: newShowScrollRight,
				showScrollLeft: newShowScrollLeft
			});
		}
	}
};
