import React from 'react';
// import PropTypes from 'prop-types';

// Style
import './component.score.scss';

// Helpers
import { i18nHelper } from 'par-components';

// Prop Types
import ScoreType from '../prop-type.score';
import ColorType from '../prop-type.color';
import SizeType from '../prop-type.size';
import TabIndexType from '../prop-type.tabindex';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		value: ScoreType.type,
		color: ColorType.type,
		size: SizeType.type,
		tabIndex: TabIndexType.type,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		value: ScoreType.default,
		color: ColorType.default,
		size: SizeType.default,
		tabIndex: TabIndexType.default,
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'Score';

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const { t } = i18nHelper;
		const {
			value,
			color,
			size,
		} = this.props;

		const classes = ['par-singlescore-score'];
		if (size)
			classes.push(`size-${size}`);

		const style = {};
		if (color) {
			style.color = (value) ? color.color : ColorType.default;
			style.backgroundColor = color.backgroundColor;
		}

		const displayScore = value || t('No score');

		return (
			<div className={classes.join(' ')}>
				<strong style={style} title={displayScore} aria-live="polite">
					{displayScore}
				</strong>
			</div>
		);
	}
}
