import getStyleConfiguration from './get-style-configuration';
import { get } from 'lodash';

export default (chartConfiguration = {}) => {
	const calendar = get(chartConfiguration, 'datasets.0.series.0.calendar');
	if (calendar && calendar !== 'standard')
		return 'biz_calendar_frequency';

	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);

	// If the Configuration is not in the styleConfig then we should deduce it from the dataset
	if (mainStyleConfiguration.frequency)
		return mainStyleConfiguration.frequency;
	else if (chartConfiguration.datasets && chartConfiguration.datasets[0] &&
		chartConfiguration.datasets[0].series &&
		chartConfiguration.datasets[0].series[0] &&
		chartConfiguration.datasets[0].series[0].frequency)
		return chartConfiguration.datasets[0].series[0].frequency;

	return null;
};
