import React from 'react';
import PropTypes from 'prop-types';

// Helpers
import { dataTableGenerator } from 'helpers/wcag';
import { stringHelper } from 'par-components';

// Style
import './component.data-table.scss';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		highchartsOptions: PropTypes.object,
		businessCalendarLabels: PropTypes.array,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		highchartsOptions: {},
	};

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const highchartsOptions = this.props.highchartsOptions;
		const businessCalendarLabels = this.props.businessCalendarLabels;
		const dataTable = dataTableGenerator.getDataTable(highchartsOptions, businessCalendarLabels);
		const tableHeader = dataTable.shift() || [];

		return (
			<div className="par-data-table">
				<table>
					<caption>{highchartsOptions.title.text}</caption>
					<thead>
						<tr>
							{tableHeader.map((value, index) => {
								const key = `tbl-thead-th-0-${index}`;
								if (!value)
									return <td className="empty-th" key={key} />;

								return <th scope="col" key={key}>{value}</th>;
							})}
						</tr>
					</thead>
					<tbody>
						{dataTable.map((values, index) => {
							const trKey = `tbl-tbody-tr-${index + 1}-0`;
							const thKey = `tbl-tbody-th-${index + 1}-0`;
							const th = values.shift();

							return (
								<tr key={trKey}>
									<th scope="row" key={thKey}>{th}</th>

									{values.map((value, colIndex) => {
										const key = `tbl-td-${index + 1}-${colIndex + 1}`;
										return (
											<td key={key}>{stringHelper.nl2br(value)}</td>
										);
									})}
								</tr>
							);
						})}
					</tbody>
				</table>
			</div>
		);
	}
}
