/**
* Builds the yAxis name that will be the key in yAxisConfig
* It is used to reference the yAxis else where in the Xenolith  * options
* @param {object} datasetIndex
*/

export const getYAxisName = (datasetIndex = 0, styleConfiguration = {}) => {
	let index = datasetIndex;
	if ((styleConfiguration != null) && (styleConfiguration.hasOwnProperty('y_axis_group')))
		index = styleConfiguration.y_axis_group || 0;

	if (index === 0)
		return 'primary';

	return 'y_axis_' + index.toString();
};
