/**
* Add the target points till the last data point
* @param {object} dataPoints
	Example:
	input: [
		{ x:1, y:5 },
		{ x:1, y:5, target: 3 },
		{ x:1, y:5 },
		{ x:1, y:5, personal_target: 4 },
		{ x:1, y:5 },
		{ x:1, y:5 },
	]
	output: [
		{ x:1, y:5 },
		{ x:1, y:5, target: 3 }, <--- add 'target: 3' for all the remaining data points
		{ x:1, y:5, target: 3 },
		{ x:1, y:5, personal_target: 4, target: 3 }, <--- add 'personal_target: 4' for all the remaining data points
		{ x:1, y:5, personal_target: 4, target: 3 },
		{ x:1, y:5, personal_target: 4, target: 3 },
	]
* @return {object} - dataPoints with the targets extended
*/


export const extendTargetsToAllPoints = (dataPoints) => {
	const currentTargets = {};
	return dataPoints.map((dataPoint) => {
		// check if the current dataPoint has a target point
		const targetYKeys = Object.keys(dataPoint).filter((yKey) => {
			return /target/i.test(yKey);
		});

		// dataPoint has a target point
		if (targetYKeys)
			// update currentTargets
			targetYKeys.forEach((targetYKey) => {
				currentTargets[targetYKey] = dataPoint[targetYKey];
			});

		return {
			...dataPoint,
			...currentTargets,
		};
	});
};
