import { buildOneDataset } from './build-one-dataset';
import { getTrendBySeries } from './get-trend-by-series';
/**
* Builds the datasets from the reportingTrendSeries object
* @param {object} reportTrendConfiguration - object that comes from the Trend Reporting API
* it has the following format:
- {
	styleConfigurations: [{..}] <-- similar format to the timeseries API format
	datasets: [{
		series: trendBySeries <-- trendBySeries is a string, this is parsed here
	}]
}
* @returns {array} datasets
*/

export const buildReportTrendDatasets = (reportTrendConfiguration = {}) => {
	// here trendBySeries is an object, JSON.parsed by getTrendBySeries
	const trendBySeries = getTrendBySeries(reportTrendConfiguration);
	if (!trendBySeries)
		return [];

	// hasMultipleDatasets influences the formatting of the name
	// for each series, that's why it's passed to buildOneDataset
	const hasMultipleDatasets = trendBySeries.length > 1;
	return trendBySeries.map((singleTrendSeries) => {
		return buildOneDataset(singleTrendSeries, hasMultipleDatasets);
	});
};
