import {
	Y_AXIS_INDEX,
	CHART_TYPE,
	SHOW_CHART_DATA_LABEL,
	DATALABELS_COLOR,
	SHOW_MARKER,
	STEP_LINE,
	LINE,
	paZindex,
	DATA,
	DECIMAL_PRECISION,
	UNIT,
	LOCALE,
} from '../constants.time-series';
import { getValueWithUnit } from 'helpers/value/index';

export default (datasetConfig, singleSeries, color, datasetIndex = 0, id = 'data') => {
	const hcSeries = {
		dataLabels: {
			enabled: datasetConfig[SHOW_CHART_DATA_LABEL],
			style: {
				color: datasetConfig[DATALABELS_COLOR],
			},
			formatter() {
				return getValueWithUnit(
					this.point.y,
					{
						precision: datasetConfig[DECIMAL_PRECISION],
						unit: datasetConfig[UNIT],
						locale: datasetConfig[LOCALE],
					}
				);
			},
		},
		color,
		decimalPrecision: datasetConfig[DECIMAL_PRECISION],
		yAxis: datasetConfig[Y_AXIS_INDEX],
		datasetIndex,
		data: singleSeries.data,
		name: singleSeries.name,
		id,
		zIndex: paZindex[DATA],
		...(datasetConfig.hasOwnProperty('stack_datasets') ? { stacking: datasetConfig.stack_datasets } : {}),
	};

	if (datasetConfig[SHOW_MARKER])
		hcSeries.marker = {
			radius: 4,
		};

	if (singleSeries.type)
		hcSeries.type = singleSeries.type;
	else if (datasetConfig[CHART_TYPE] === STEP_LINE) {
		hcSeries.type = LINE;
		hcSeries.step = 'center';
	} else
		hcSeries.type = datasetConfig[CHART_TYPE];

	if (datasetConfig[CHART_TYPE] === 'area') {
		if (datasetConfig.fill_opacity)
			hcSeries.fillOpacity = datasetConfig.fill_opacity;

		if (datasetConfig.fill_color)
			hcSeries.fillColor = datasetConfig.fill_color;

		if (datasetConfig.line_width)
			hcSeries.lineWidth = datasetConfig.line_width;
	}

	return hcSeries;
};
