import {
	DERIVED_SERIES,
	CONFIDENCE_BAND,
	TRENDLINE,
	TARGETS,
	THRESHOLDS,
	PREDICTION_BAND,
	CHANGE_PERCENT,
	CHANGE,
	COMMENTS,
	INITIATIVES,
} from './constants.time-series';
import {
	hasScores,
	hasDerivedSeries,
	hasForecast,
} from '../../../helpers/series/';
import buildDataSeries from './data-series/';
import buildChangePercent from './change-percent-series/';
import buildChange from './change-series/';
import buildForecastSeries from './forecast/';
import buildConfidenceBand from './confidence-band/';
import buildPredictionBand from './prediction-band/';
import buildTrendlines from './trendlines/';
import buildTargets from './targets/';
import buildThreshold from './threshold/';
import buildComments from './comments/';
import buildInitiatives from './initiatives/';

/**
* Builds one single series and its derived series for one dataset
* @param {object} params - object that contains the following:
* params.datasetStyleConfig - styleConfiguration of the dataset that contains the current series
* params.singleSeries - the current series with its derived series (confidence band, target, threshold, etc...)
* params.color - color for this series, either in hexa or rgba
* params.seriesIndex - index of the current series in the dataset
* params.datasetIndex - index of the current dataset in chartConfiguration.datasets
* @returns {Object} - returns an object like
* {
*  seriesData: {},
*  seriesConfig: {},
*  forecastData: {},
*  forecastConfig: {},
*  ...
* }
*/

export default (params = {}) => {
	const { singleSeries } = params;

	let dataSeries = {};
	let changePercentSeries = {};
	let changeSeries = {};
	let forecast = {};
	let confidenceBand = {};
	let predictionBand = {};
	let trendlines = {};
	let targets = {};
	let thresholds = {};
	let comments = {};
	let initiatives = {};

	if (hasScores(singleSeries)) {
		// data series
		dataSeries = buildDataSeries(params);
		// forecast
		if (hasForecast(singleSeries))
			forecast = buildForecastSeries(params);
	}

	if (singleSeries[DERIVED_SERIES]) {
		// change
		if (hasDerivedSeries(singleSeries, CHANGE))
			changeSeries = buildChange(params);

		// change percent
		if (hasDerivedSeries(singleSeries, CHANGE_PERCENT))
			changePercentSeries = buildChangePercent(params);

		// confidence band
		if (hasDerivedSeries(singleSeries, CONFIDENCE_BAND))
			confidenceBand = buildConfidenceBand(params);

		// prediction band
		if (hasDerivedSeries(singleSeries, PREDICTION_BAND))
			predictionBand = buildPredictionBand(params);

		// trendline
		if (hasDerivedSeries(singleSeries, TRENDLINE))
			trendlines = buildTrendlines(params);

		// targets
		if (hasDerivedSeries(singleSeries, TARGETS))
			targets = buildTargets(params);

		// threshold
		if (hasDerivedSeries(singleSeries, THRESHOLDS))
			thresholds = buildThreshold(params);

		// comments
		if (hasDerivedSeries(singleSeries, COMMENTS))
			comments = buildComments(params);

		// comments
		if (hasDerivedSeries(singleSeries, INITIATIVES))
			initiatives = buildInitiatives(params);
	}

	return {
		...dataSeries,
		...changePercentSeries,
		...changeSeries,
		...forecast,
		...confidenceBand,
		...trendlines,
		...targets,
		...thresholds,
		...predictionBand,
		...comments,
		...initiatives,
	};
};
