import {
	LEGEND_HORIZONTAL_ALIGNMENT,
	LEGEND_VERTICAL_ALIGNMENT,
	LEGEND_ITEMS_LEFT_ALIGN,
} from './constants.legend';

import {
	SHOW_CHART_TITLE,
	TITLE_VERTICAL_ALIGNMENT,
	CHART_TITLE_SIZE,
} from '../title/constants.title';
import isObjectEmpty from '../../helpers/is-object-empty';

/**
 * Applies alignment properties for Highcharts legend
 * Applicable in the Report Designer for exports
 * See STRY3849663
 * @param  {Object} [mainStyleConfiguration={}]
 * Returns legend options related to positioning: maxHeight, width and y
 * @return {Object}
 */
export default (mainStyleConfiguration = {}) => {
	if (isObjectEmpty(mainStyleConfiguration))
		return {};

	const legendOptions = {};
	if (mainStyleConfiguration[LEGEND_HORIZONTAL_ALIGNMENT] === 'right')
		// prevent legend from hitting the export button
		switch (mainStyleConfiguration[LEGEND_VERTICAL_ALIGNMENT]) {
		case 'top':
			legendOptions.y = 25;
			break;
		case 'bottom':
			legendOptions.maxHeight = 550;
			break;
		case 'middle':
			legendOptions.maxHeight = 600;
			break;
		}
	else if (mainStyleConfiguration[LEGEND_HORIZONTAL_ALIGNMENT] === 'center') {
		// prevent legend items to overflow on the left of the report in case of very long legend values
		if (mainStyleConfiguration[LEGEND_ITEMS_LEFT_ALIGN])
			legendOptions.width = 1000;

		if (mainStyleConfiguration[LEGEND_VERTICAL_ALIGNMENT] === 'top') {
			legendOptions.y = -2;
			if (mainStyleConfiguration[SHOW_CHART_TITLE] && mainStyleConfiguration[TITLE_VERTICAL_ALIGNMENT] === 'top')
				// prevent legend from covering the title
				legendOptions.y = mainStyleConfiguration[CHART_TITLE_SIZE] + 10;
		}
		legendOptions.maxHeight = 100;
	}

	return legendOptions;
};
