// httpHelper - to be replaced with the platform one
import { httpHelper } from 'par-components';

export default {
	fetchDataForConfigurations(dataConfigurations) {
		const { http } = httpHelper;
		const url = '/api/now/reporting/reports/series';

		const postBody = {
			data: {
				data_configurations: dataConfigurations,
			},
		};

		return http.post(url, postBody).then((response) => {
			return {
				styleConfigurations: response.result.style_configurations,
				datasets: response.result.datasets,
				clickBehaviors: response.result.click_behaviors,
			};
		}, (response) => {
			throw response;
		});
	},

	fetchDataForSysId(sysId) {
		const { http } = httpHelper;
		const url = '/api/now/reporting/reports/series/' + encodeURIComponent(sysId);

		return http.post(url).then((response) => {
			return {
				styleConfigurations: response.result.style_configurations,
				datasets: response.result.datasets,
				clickBehaviors: response.result.click_behaviors,
			};
		}, (response) => {
			throw response;
		});
	},
};
