import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import 'sn-component-landing-page-header';
import {preUserData} from 'sn-uxpage-presource';
import {t} from 'sn-translate';
import style from './style.scss';
import headerActions from './actions';
import userInfo from './userInfo';

const view = state => {
	const {countClosedCases} = state.properties;
	const {isLoaded} = state;
	const firstName = userInfo.getFirstName(state);

	if (!isLoaded) {
		return (
			<div className="empty-state-wrapper">
				<sn-hr-empty-state type="loading"/>
			</div>
		);
	}

	let header = t('Hello, {0}!', firstName);
	let subtitle = t('Here\'s a snapshot of what\'s important');

	if (countClosedCases > 1) {
		header = t('Nice job, {0}!', firstName);
		subtitle = t('You closed {0} cases today', countClosedCases);
	}

	return (
		<div>
			<h1>{header}</h1>
			<p>{subtitle}</p>
		</div>
	);
};

createCustomElement('sn-component-hr-landing-page-header', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		countClosedCases: {
			default: 0
		}
	},
	behaviors: [preUserData],
	actions: {
		...headerActions.actions
	},
	actionHandlers: {
		...headerActions.actionHandlers
	},
	styles: style
});
