import buildXAxis from '../x-axis/index';
import buildYAxis from '../y-axis/index';
import buildLegend from '../legend/index';
import buildChart from '../chart/index';
import buildSeries from '../series/index';
import buildTooltip from '../tooltip/index';
import buildPlotOptions from '../plot-options/index';
import buildStackedOptions from '../stacked/index';
import buildColors from '../colors/index';
import buildTitle from '../title/index';
import getDefaultOptions from '../options/index';
import smartMerge from '../../helpers/smart-merge';
import assignYAxisIndex from '../../helpers/assign-y-axis-to-dataset';
import areXAxisLabelsSlanted from '../../helpers/x-axis-labels-slanted';
import isNavigator from '../../helpers/is-navigator';
import buildNavigator from './navigator/build-navigator';
import { TIME_SERIES } from '../../constants';
import getFrequency from '../../helpers/get-frequency';

export default (chartConfiguration) => {
	const chartConfigurationWithYIndex = assignYAxisIndex(chartConfiguration);
	if (isNavigator(chartConfiguration))
		return buildNavigator(chartConfiguration);

	return smartMerge([
		getDefaultOptions(),
		buildChart(chartConfigurationWithYIndex),
		buildXAxis(chartConfigurationWithYIndex, areXAxisLabelsSlanted(chartConfigurationWithYIndex)),
		buildYAxis(chartConfigurationWithYIndex),
		buildLegend(chartConfigurationWithYIndex),
		buildSeries(chartConfigurationWithYIndex, TIME_SERIES),
		buildColors(chartConfigurationWithYIndex),
		buildTitle(chartConfigurationWithYIndex),
		buildPlotOptions(chartConfigurationWithYIndex),
		buildStackedOptions(chartConfigurationWithYIndex),
		buildTooltip(chartConfigurationWithYIndex, TIME_SERIES),
		{
			frequency: getFrequency(chartConfigurationWithYIndex),
		},
	]);
};
