import {
	DATASET_SERIES,
	DATA,
	SERIES,
	STYLE_CONFIGURATIONS,
} from '../constants';
import { X_AXIS_LABELS_SLANT_THRESHOLD } from '../highcharts/x-axis/constants.x-axis';

export default (chartConfiguration = {}) => {
	if (chartConfiguration[DATASET_SERIES]
		&& chartConfiguration[DATASET_SERIES].length > 0
		&& chartConfiguration[DATASET_SERIES][0][SERIES]
		&& chartConfiguration[DATASET_SERIES][0][SERIES].length > 0
		&& chartConfiguration[DATASET_SERIES][0][SERIES][0][DATA]
		&& chartConfiguration[DATASET_SERIES][0][SERIES][0][DATA].length) {
		const numberOfPointsFirstSeries = chartConfiguration[DATASET_SERIES][0][SERIES][0][DATA].length;
		return numberOfPointsFirstSeries >= chartConfiguration[STYLE_CONFIGURATIONS][0][X_AXIS_LABELS_SLANT_THRESHOLD];
	}

	return false;
};
