import buildAnnotationsConfig from '../annotations/build-annotations-config';
import buildAnnotationsData from '../annotations/build-annotations-data';
/**
* Builds the comments series
* @param {object} params - object that contains the following:
* params.datasetStyleConfig - styleConfiguration of the dataset that contains the current series
* params.singleSeries - the current series with its derived series (confidence band, target, threshold, etc...)
* params.color - color for this series, either in hexa or rgba
* params.seriesIndex - index of the current series in the dataset
* params.datasetIndex - index of the current dataset in chartConfiguration.datasets
*/
export default (params) => {
	return {
		commentsConfig: buildAnnotationsConfig({
			...params,
			annotationType: 'comments',
		}),
		commentsData: buildAnnotationsData({
			...params,
			annotationType: 'comments',
		}),
	};
};
