import React from 'react';
import getStyleConfiguration from 'parser/helpers/get-style-configuration';
import { buildScoreElement } from './build-score-element';
import { buildDerivedSeriesElement } from './build-derived-series-element';
import { getFormattedDate } from './get-formatted-date';
import { dataPointHasDerivedSeries } from './data-point-has-derived-series';

/**
 * In this file, dataPoint is an object that has the following format:
* {
* 	x: 1529798400000,                   -- the timestamp
*   y_data_ds_0_series_0: 34,			-- all the scores for all the datasets
*   y_data_ds_0_series_1: 2,               and series for this timestamp
*	y_data_ds_1_series_0: 22,
*   ...
*   y_change_ds_0_series_0: 4,			-- all the derived series for all the datasets
*   y_change_percent_ds_0_series_0: 2,     and series for this timestamp
*	y_target_ds_0_series_0: 22,
*	y_trendline_ds_0_series_0: 22,
*   ...
* }
*/


/**
* Builds the cell to combine the score and the derived series
* @param {object} dataPoint - see above
* @param {number} datasetIndex - used to get the right score in dataPoint
* @param {number} seriesIndex - used to get the right score in dataPoint
* @param {object} chartConfiguration
* @return {jsx}
*/
const buildCell = (dataPoint, seriesIndex, datasetIndex, chartConfiguration) => {
	const cellKey = `tbl-tbody-cell-${seriesIndex}-${datasetIndex}`;
	return (
		<div
			className="par-data-table-cell-container"
			key={cellKey}
		>
			{buildScoreElement(
				dataPoint,
				seriesIndex,
				datasetIndex,
				chartConfiguration
			)}
			{dataPointHasDerivedSeries(dataPoint, seriesIndex, datasetIndex) &&
			buildDerivedSeriesElement(
				dataPoint,
				seriesIndex,
				datasetIndex,
				chartConfiguration
			)}
		</div>
	);
};

/**
* Builds a row for the data table
* @param {object} dataPoint - see above
* @param {object} chartConfiguration - contains styleConfigurations and datasets
* @param {number} rowNumber - used to generate key (string attribute for list items in React)
* @return {jsx}
*/
export const buildRow = (dataPoint, chartConfiguration, rowNumber) => {
	const mainStyleConfiguration = 	getStyleConfiguration(chartConfiguration, 0);
	const thKey = `tbl-tbody-th-${rowNumber + 1}-0`;

	// initiate the row with the formatted date
	const row = [
		<th scope="row" key={thKey}>
			{getFormattedDate(
				dataPoint,
				mainStyleConfiguration
			)}
		</th>,
	];

	// looping through the datasets and the series makes sure that
	// the data for the row is displayed in the right order:
	chartConfiguration.datasets.forEach((dataset, datasetIndex) => {
		dataset.series.forEach((series, seriesIndex) => {
			const tdKey = `tbl-td-${rowNumber + 1}-${seriesIndex + 1}-${datasetIndex + 1}`;
			row.push(
				<td key={tdKey}>
					{buildCell(
						dataPoint,
						seriesIndex,
						datasetIndex,
						chartConfiguration,
					)}
				</td>
			);
		});
	});
	return row;
};
