/**
 * Return the data patterns flag from the chartConfiguration
 * This is used mostly for Workspace
 */
export const getDataPatternsFlag = (chartConfiguration) => {
	if (chartConfiguration &&
		chartConfiguration.style_configurations &&
		chartConfiguration.style_configurations[0] &&
		chartConfiguration.style_configurations[0].dataPatterns)
		return chartConfiguration.style_configurations[0].dataPatterns;

	return false;
};
