import getSeriesYKey from '../helpers/get-series-y-key';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
		bandType, // confidence_band or prediction_band
	} = params;
	return singleSeries.derived_series[bandType].data.map((dataPoint) => {
		return {
			x: dataPoint[0],
			[getSeriesYKey(seriesIndex, datasetIndex, bandType + '_y0')]: dataPoint[1],
			[getSeriesYKey(seriesIndex, datasetIndex, bandType + '_y1')]: dataPoint[2],
		};
	});
};
