import { getValueWithUnit } from 'helpers/value/index';

const formatTooltipLine = (seriesName, color, yValue, isChange = false, changeTranslated) => {
	if (isChange)
		return '<span style="vertical-align: sub; color:' + color + '; font-size: 22px">\u25CF</span> '
		+ changeTranslated + ': ' + seriesName + ': <span style="font-weight: 700">' + yValue + '</span></br>';

	return '<span style="vertical-align: sub; color:' + color + '; font-size: 22px">\u25CF</span> '
	+ seriesName + ': <span style="font-weight: 700">' + yValue + '</span></br>';
};

export default (points, translations, precision = false, unit = false, locale) => {
	const label = [];
	const formattingOptions = {
		precision,
		unit,
		tooltip: true,
		locale,
	};
	if (points)
		points.forEach((currentPoint) => {
			const isChange = currentPoint.series.userOptions.seriesName === 'Change';
			if (currentPoint.series.type === 'spline')
				label.push(
					formatTooltipLine(
						translations.distribution,
						currentPoint.color,
						getValueWithUnit(currentPoint.y, formattingOptions),
						isChange,
						translations.change
					)
				);
			else
				label.push(
					formatTooltipLine(
						currentPoint.x,
						currentPoint.color,
						getValueWithUnit(currentPoint.y, formattingOptions),
						isChange,
						translations.change
					)
				);
		});
	return label.join('');
};
