import { PERSONAL, GLOBAL } from '../constants.time-series';
import buildThreshold from './build-single-threshold';

export default (thresholds, datasetStyleConfig = {}) => {
	const result = [];
	if (thresholds[GLOBAL] && thresholds[GLOBAL].length > 0)
		thresholds[GLOBAL].forEach((globalThreshold, index) => {
			result.push(buildThreshold(globalThreshold, index, false, datasetStyleConfig));
		});

	if (thresholds[PERSONAL] && thresholds[PERSONAL].length > 0)
		thresholds[PERSONAL].forEach((personalThreshold, index) => {
			result.push(buildThreshold(personalThreshold, index, true, datasetStyleConfig));
		});

	return result;
};
