import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Visualization from './component.visualization';

// Actions
import { setSysId, setDataConfigurations } from './action-creators.visualization';

const mapStateToProps = (state) => {
	return {
		serverDatasets: state.datasets,
		serverStyleConfigurations: state.styleConfigurations,
		serverClickBehaviors: state.clickBehaviors,
		error: state.error,
	};
};

const mapDispatchToProps = (dispatch) => {
	return bindActionCreators({
		setDataConfigurations,
		setSysId,
	}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Visualization);
