import { getBandSeriesProperties } from './get-band-series-properties';

export default (band = {}, id = 'trend', decimalPrecision = 0) => {
	if (!band.data)
		return {};

	const bandSeriesProperties = getBandSeriesProperties();

	return {
		marker: {
			states: {
				hover: {
					enabled: false,
				},
			},
		},
		visible: true,
		decimalPrecision,
		type: bandSeriesProperties[id].type,
		enableMouseTracking: true,
		showInLegend: true,
		name: band.name || bandSeriesProperties[id].name,
		id,
		zIndex: bandSeriesProperties[id].zIndex,
		data: band.data,
		color: bandSeriesProperties[id].color,
		fillColor: null,
	};
};
