import React from 'react';
import PropTypes from 'prop-types';
import { Loading } from 'par-components';

// Prop types
import StateComponentsType from '../visualization/prop-type.state-component';

// Helpers
import { emptyDatasets } from 'helpers/api';

// Visualizations
import TimeSeries from '../time-series/component.time-series';
import SingleScore from '../singlescore/container.singlescore';
import Categories from '../categories/component.categories';
import NotSupported from '../not-supported/component.not-supported';
import NoData from '../no-data/component.no-data';

// Config
import visualizationDefaults from '../visualization/visualization-defaults.json';


export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		datasets: PropTypes.array,
		styleConfigurations: PropTypes.array,
		clickBehaviors: PropTypes.array,
		onSetExtremes: PropTypes.func,
		selectedDate: PropTypes.number,
		chartLibrary: PropTypes.string,

		// Check the prop type definition for more info regarding this type
		stateComponents: StateComponentsType.type,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		stateComponents: StateComponentsType.value,
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'VisualizationRouter';

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const {
			datasets,
			styleConfigurations,
			clickBehaviors,
			onSetExtremes,
			selectedDate,
			stateComponents,
		} = this.props;

		let realtimeWarning = null;
		if (stateComponents && stateComponents.realtimeWarning)
			realtimeWarning = stateComponents.realtimeWarning;

		if (emptyDatasets(datasets))
			// If there are no datasets, first we check if there are not style
			// configurations, because that means that the component is probably
			// loading
			if (!styleConfigurations || !styleConfigurations.length)
				if (stateComponents && stateComponents.loading)
					return stateComponents && stateComponents.loading;
				else
					return <Loading />;

			// If there are some style configurations, but no datasets then that
			// means that there is no data, so we show the empty component
			else if (stateComponents && stateComponents.empty)
				return stateComponents && stateComponents.empty;
			else
				return <NoData />;

		// Get the type to switch between components
		const type = styleConfigurations && styleConfigurations[0] ? styleConfigurations[0].chart_type : visualizationDefaults.chart_type;

		// Switch between what to render
		switch (type) {
		case 'score':
		case 'realtime':
		case 'single_score':
		case 'latest':
			return (<SingleScore
				datasets={datasets}
				styleConfigurations={styleConfigurations}
				clickBehaviors={clickBehaviors}
				onSetExtremes={onSetExtremes}
				realtimeWarning={realtimeWarning}
			/>);

		case 'pareto':
		case 'bar':
			return (<Categories
				datasets={datasets}
				styleConfigurations={styleConfigurations}
				clickBehaviors={clickBehaviors}
				onSetExtremes={onSetExtremes}
			/>);

		case 'line':
		case 'area':
		case 'step_line':
		case 'spline':
		case 'column':
		case 'trend':
		case 'line_bar':
			return (<TimeSeries
				chartLibrary={this.props.chartLibrary}
				datasets={datasets}
				styleConfigurations={styleConfigurations}
				clickBehaviors={clickBehaviors}
				onSetExtremes={onSetExtremes}
				selectedDate={selectedDate}
			/>);
		}

		// If no type matches, render that it's unsupported
		if (stateComponents && stateComponents.unsupported)
			return (
				<div className="par-visualization-state-components-container-not-supported">
					{stateComponents.unsupported}
				</div>
			);

		// Get the width and the height to be used by the not supported
		// component
		const width = styleConfigurations && styleConfigurations[0] ? styleConfigurations[0].chart_width : undefined;
		const height = styleConfigurations && styleConfigurations[0] ? styleConfigurations[0].chart_height : undefined;

		return (<NotSupported
			width={width}
			height={height}
		/>);
	}
}
