import getSeriesYKey from 'parser/xenolith/time-series/series/helpers/get-series-y-key';
import getAnnotationId from 'parser/xenolith/time-series/series/annotations/get-annotation-id';
/**
* Returns whether a data point has a derived series point such has:
	- change
	- change %
	- trendline
	- targets
	- threshold
* @param {object} dataPoint
* @return {boolean}
*/
export const dataPointHasDerivedSeries = (dataPoint, seriesIndex, datasetIndex) => {
	let result = false;
	const derivedSeriesNames = [
		'target',
		'change',
		'change_percent',
		'threshold',
		'comments',
		'trend',
		'confidence_band_y0',
		'prediction_band_y0',
	];
	derivedSeriesNames.forEach((name) => {
		if (typeof dataPoint[getSeriesYKey(seriesIndex, datasetIndex, name)] !== 'undefined' ||
			typeof dataPoint[getAnnotationId(name, seriesIndex, datasetIndex)] !== 'undefined')
			result = true;
	});
	return result;
};
