/**
 *	Creates an invisible series for each plotline to make sure that they're visible in the chart
 */
export default (yPlotLines = [], xValue = 0) => {
	const data = yPlotLines.map(plotLine => ({
		x: xValue,
		y: plotLine.value,
	}));
	return {
		id: 'invisible-threshold',
		showInLegend: false,
		enableMouseTracking: false,
		type: 'line',
		marker: {
			enabled: false,
			states: {
				hover: {
					enabled: false,
				},
			},
		},
		lineWidth: 0,
		data,
	};
};
