import buildYAxisChangePercent from './build-y-axis-change-percent';
import buildDataSeries from '../data-series/build-data-series';
import { CHANGE_PERCENT } from '../constants.time-series';

export default (datasetSeriesConfig, yoySeries, colors, changePercentOnly = false) => {
	// passing the color so the changePercent so it has the same color
	// as the scores
	const series = [];
	yoySeries.forEach((singleYearSeries, index) => {
		const color = colors[index % colors.length];
		series.push(
			{
				...buildDataSeries(datasetSeriesConfig, singleYearSeries, color),
				yAxis: changePercentOnly ? 0 : 1,
				id: CHANGE_PERCENT,
			}
		);
	});
	return {
		yAxis: buildYAxisChangePercent(datasetSeriesConfig),
		series,
	};
};
