import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import 'sn-hr-shared';
import '@servicenow/now-icon';
import '@servicenow/now-loader';

/**
 * View of the Visualization error component
 * @param  {Object} state
 */
const view = ({ properties }) => {
	const { error, error: { type = '' }, color } = properties;

	if (error.status === 403 || error.status === 401)
		return <sn-hr-empty-state type="permission-error" variant="fit" color={color} />;

	if ((error.status === 501) || (error.type === 'not-supported'))
		return <sn-hr-empty-state type="not-supported" variant="fit" color={color} />;

	return <sn-hr-empty-state type="loading-error" variant="fit" color={color} />;
};

export default createCustomElement('sn-hr-single-score-error', {
	properties: {
		error: {
			default: null,
			reflect: false
		},
		color: {
			default: '#637274'
		}
	},
	renderer: {
		type: snabbdom,
		view,
	},
});
