import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { ACTIONS } from './helper/constants';
import headerActions from './actions';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './hrDonutChart.scss';
import map from 'lodash/map';
import '@servicenow/now-chart-donut-pie';
import './helper/hrDonutChartTooltipText';
import 'sn-hr-shared';

const view = ( state, { dispatch }) => {

	if (state.isError)
		return (
			<div className={'empty-state-wrapper'}>
				<sn-hr-empty-state type={'error'} variant={'fit'} />
			</div>
		);

	if (state.isLoading)
		return (
			<div className={'empty-state-wrapper'}>
				<sn-hr-empty-state type={'loading'} variant={'fit'} />
			</div>
		);

	// report does not pass the total case number, need to add manually
	let totalCaseCount = 0;

	const { chartTitle, chartOptions } = state;
	const { seriesData, seriesConfig: {
			series1: { colorMap }
	} } = chartOptions;

	if (!seriesData.length)
		return (
			<div className={'empty-state-wrapper'}>
				<sn-hr-empty-state type={'empty'} variant={'fit'} />
			</div>
		);

	// method to dispatch drilldown event
	const openDetailList = dataValue => dispatch(ACTIONS.DETAIL_LINK_CLICKED, dataValue);

	// create list item of hr services based on series data
	const caseDetailList = map(seriesData, data => {
		let caseCount = 0;
		const { x: hrServiceName } = data;

		// dynamic key - need to grab the other key of data and rename to 'caseCount'
		Object.keys(data).forEach(key => {
			if (key !== 'x')
				caseCount = data[key];
		});

		// add the case count per hr service
		totalCaseCount += caseCount;

		return (
			<div className={'open-detail'}>
				<a href={'javascript:void(0)'} on-click={[openDetailList, {data}]} aria-label={hrServiceName}>
					<sn-text-with-tooltip
						backgroundColor={colorMap[hrServiceName]}
						caseCount={caseCount}
						hrServiceName={hrServiceName} />
				</a>
			</div>
		);
	});

	return (
		<section className={'hr-donut-chart'}>
			<h2>{chartTitle}</h2>

			<section className={'pienut-chart'}>
				<now-chart-donut-pie {...chartOptions} tabindex="0"/>
			</section>

			<h3>
				<a href={'javascript:void(0)'} on-click={openDetailList} >
					<span className={'open-detail'}>
						{t('All cases')}
					</span>
					<span className="count" aria-label={totalCaseCount}>
						{totalCaseCount}
					</span>
				</a>
			</h3>

			<section className={'hr-donut-chart-legend'}>
				{caseDetailList}
			</section>
		</section>
	);
};

createCustomElement('sn-hr-donut-chart', {
	initialState: {
		isLoading: true,
		isSubscribed: false
	},
	properties: {
		height: {default: 250},
		// report is read-only, is not optional (for now)
		reportSysId: {default: 'b275f98cb3901010f5302ddc16a8dc0d'},
	},
	actions: {
		[ACTIONS.AMB_SUBSCRIPTION_START]: { stopPropagation: true },
		[ACTIONS.FAIL]: { stopPropagation: true },
		[ACTIONS.FETCH_REPORT_DATA]: { stopPropagation: true },
		[ACTIONS.RECORD_WATCHER_SUBSCRIBED]: { stopPropagation: true },
		[ACTIONS.RECORD_WATCHER_ITEM_CHANGED]: { stopPropagation: true },
		[ACTIONS.REPORT_DATA_FETCH_SUCCESS]: { stopPropagation: true },
		[ACTIONS.DETAIL_LINK_CLICKED]: { stopPropagation: true }
	},
	actionHandlers: {
		...headerActions.actionHandlers
	},
	renderer: {
		type: snabbdom,
		view,
	},
	styles,
});
