import { DATA, DERIVED_SERIES, COMMENTS } from '../constants.time-series';

export default (singleSeries, type = COMMENTS) => {
	if (!singleSeries || !singleSeries[DATA])
		return {};

	// last point of the scores
	const lastDataPointTimestamp = singleSeries[DATA][singleSeries[DATA].length - 1].x;
	const commentsOnScores = [];
	const commentsOnForecast = [];
	const allComments = singleSeries[DERIVED_SERIES][type][DATA];
	allComments.forEach((comment) => {
		if (comment.x <= lastDataPointTimestamp)
			commentsOnScores.push(comment);
		else
			commentsOnForecast.push(comment);
	});
	return {
		commentsOnScores,
		commentsOnForecast,
	};
};
