import buildSeriesFromDataset from './build-series-from-dataset';
import mergeAllSeries from './helpers/merge-all-series';
/**
* Returns a flat object containing the data and the config of each
* derived series.
* - seriesData
* - seriesConfig
* - confidenceBandData
* - confidenceBandConfig
* - trendlinesData
* - trendlinesConfig
* - predictionBandData
* - predictionBandConfig
* - forecastData
* - forecastConfig
* - forecastRangeData
* - forecastRangeConfig
* @param {Object} chartConfiguration - chart configuration in the format of the Reporting API
*/

export default (chartConfiguration) => {
	const allDatasetSeriesBuilt = [];
	chartConfiguration.datasets.forEach((dataset, datasetIndex) => {
		allDatasetSeriesBuilt.push(buildSeriesFromDataset(
			chartConfiguration,
			datasetIndex,
		));
	});
	const mergedDatasetSeries = mergeAllSeries(allDatasetSeriesBuilt);
	return mergedDatasetSeries;
};
