import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import {get, omitBy, isNil, toString} from 'lodash';
import '../sn-hr-single-score';
import styles from './visualization.scss';
import { POINT_CLICKED } from './constants';
import rtlBehavior from '@servicenow/behavior-rtl';

const isPresent = s => !isNil(s) && s !== false;

const view = (state, { dispatch }) => {
	const {
		properties: {
			label,
			titleColor,
			backgroundColor,
			variant,
			border,
			refreshInterval,
			reportId,
			isRealtime,
			sidebarColor,
			dir
		}
	} = state;

	const refreshMilis = refreshInterval && refreshInterval * 60000;

	const dynamicStyle = {
		'background-color': backgroundColor
	};

	if (sidebarColor !== '')
		dynamicStyle[dir === 'ltr'? 'border-left' : 'border-right'] = '8px solid ' + sidebarColor;

	return (
		<div class={{'sn-component-card-wrapper': true, 'has-border': border}}>
			<div
				class={{
					'sn-component-card': true,
					[`sn-component-card-${variant && variant.toLowerCase()}`]: variant,
				}}
				style={dynamicStyle}
			>
				<div
					class={{
						'title-bar': true,
						'has-title': isPresent(label)
					}}
				>
					{isPresent(label) ? <h3 style={{'color': titleColor}}>{toString(label)}</h3> : null}

				</div>

				<sn-hr-single-score
					key={state.visKey}
					sys-id={reportId}
					variant={variant}
					refresh-interval={refreshMilis}
					is-realtime={isRealtime}
					font-color={titleColor}
				/>
			</div>
		</div>
	);
};

const handleVisualizationClick = coeffects => {
	const { payload } = coeffects.action;
	if (payload.source_type === 'table' || payload.source_type === 'source') {
		handleDrilldown(coeffects);
	}
};

const handleDrilldown = ({ action, dispatch, state }) => {
	const { payload } = action;
	const { properties } = state;
	let params = {
		listTitle: properties.label || payload.title || payload.table || '',
		table: payload.table,
		query: payload.conditions,
		columns: payload.columns,
		workspaceConfigId: get(state, 'behaviors.wsConfigSysId'),
		disableQuickEdit: true
	};
	params = omitBy(params, isNil);
	dispatch('ITEM_SELECTED', {
		renderer_type: 'layout',
		params
	});
};

const TAG_NAME = 'sn-hr-single-score-visualization';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		preWsConfigSysId,
		rtlBehavior
	],
	properties: {
		label: { default: '' },
		titleColor: { default: '#000' },
		backgroundColor: { default: '#fff' },
		variant: {},
		reportId: {},
		border: { default: false },
		refreshInterval: {},
		isRealtime: { default: false },
		sidebarColor: { default: '' }
	},
	initialState: {
		visKey: Date.now(),
	},
	actionHandlers: {
		[POINT_CLICKED]: handleVisualizationClick
	},
	styles
});

export default TAG_NAME;
