import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
const {Fragment} = snabbdom;
import uniqueId from 'lodash/uniqueId';
import '@servicenow/now-tooltip';
import styles from './hrDonutChartTooltipText.scss';

const view = (state, { dispatch }) => {
	const { properties: { backgroundColor, caseCount, hrServiceName, targetRef } } = state;
	const setTargetRef = ref => dispatch('PROPERTIES_SET', { targetRef: ref });
	const uid = `tooltip_id_${uniqueId()}`;

	return (
		<Fragment>
			<span
				className="bullet"
				style={{ backgroundColor: backgroundColor }}></span>

			<span
				className="ellipsis"
				hook-insert={({ elm }) => setTargetRef( elm )}
				hook-destroy={() => setTargetRef( null )}
				aria-describedby={ uid }>
				{ hrServiceName }</span>

			<span className="count">
				{ caseCount }</span>

			<now-tooltip
				id={uid}
				target-ref={ targetRef }
				content={ hrServiceName }
				delay={ 500 }
			/>
		</Fragment>
	);
};

export default createCustomElement('sn-text-with-tooltip', {
	properties: {
		backgroundColor: '',
		caseCount: 0,
		hrServiceName: '',
		targetRef: { default: null }
	},
	renderer: {
		type: snabbdom,
		view
	},
	styles
});
