import buildSeries from '../../series/index';
import navigatorOptions from './options.navigator.json';
import getStyleConfiguration from '../../../helpers/get-style-configuration';
import { TIME_SERIES } from '../../../constants';
import { NAVIGATOR_LINE_COLOR, NAVIGATOR_GRADIENT_COLOR_FROM, NAVIGATOR_GRADIENT_COLOR_TO } from './navigator.constants';

export default (chartConfiguration) => {
	const mainChartConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const options = Object.assign({}, navigatorOptions);
	// apply min and max
	options.xAxis[0].min = mainChartConfiguration.min;
	options.xAxis[0].max = mainChartConfiguration.max;
	options.series = buildSeries(chartConfiguration, TIME_SERIES).series;
	// line color
	if (mainChartConfiguration[NAVIGATOR_LINE_COLOR])
		options.navigator.series = {
			...options.navigator.series,
			lineColor: mainChartConfiguration[NAVIGATOR_LINE_COLOR],
		};
	// gradient colors
	if (mainChartConfiguration[NAVIGATOR_GRADIENT_COLOR_FROM] && mainChartConfiguration[NAVIGATOR_GRADIENT_COLOR_TO])
		options.navigator.series = {
			...options.navigator.series,
			color: {
				linearGradient: { y1: 0, x1: 0, y2: 1, x2: 0 },
				stops: [
					[0, mainChartConfiguration[NAVIGATOR_GRADIENT_COLOR_FROM]],
					[1, mainChartConfiguration[NAVIGATOR_GRADIENT_COLOR_TO]],
				],
			},
		};


	return options;
};
