import { COMPONENT_BOOTSTRAPPED } from '@servicenow/ui-core/lib/constants';
import { ACTIONS, CONSTANTS } from './helper/constants';
import { createAmbSubscriptionEffect } from '@seismic/effect-amb';
import { createHttpEffect } from "@servicenow/ui-effect-http";
import { filterReportDataByMonthAndAssignees, handleDrillDown, initializeBarChart } from "./helper/effects";

export default {
	actions: {
		[ACTIONS.AMB_SUBSCRIPTION_START]: { stopPropagation: true },
		[ACTIONS.FETCH_DATA]: { stopPropagation: true },
		[ACTIONS.FETCH_FAIL]: { stopPropagation: true },
		[ACTIONS.FETCH_SUCCESS]: { stopPropagation: true },
		[ACTIONS.INITIALIZE_AMB]: { stopPropagation: true },
		[ACTIONS.RECORD_WATCHER_SUBSCRIBE_STARTED]: { stopPropagation: true },
		[ACTIONS.RECORD_WATCHER_SUBSCRIBED]: { stopPropagation: true },
		[ACTIONS.RECORD_WATCHER_ITEM_CHANGED]: { stopPropagation: true }
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: initializeBarChart,

		[ACTIONS.FETCH_DATA]: createHttpEffect(
			CONSTANTS.QUERY_URL, {
				method: 'POST',
				pathParams: ['reportSysId'],
				batch: false,
				successActionType: ACTIONS.FETCH_SUCCESS,
				errorActionType: ACTIONS.FETCH_FAIL,
			}),

		[ACTIONS.NOW_CHART_BAR_CLICK]: handleDrillDown,

		[ACTIONS.FETCH_SUCCESS]: filterReportDataByMonthAndAssignees,

		[ACTIONS.FETCH_FAIL]: ({ action: { payload: { data }}, updateState }) => {
			if (data.error && data.message) {
				updateState({ isError: true });
				console.error(data.message);
			}
		},

		[ACTIONS.INITIALIZE_AMB]: ({ dispatch }) => {
			// AMB requires base-64 encoded query
			dispatch(ACTIONS.AMB_SUBSCRIPTION_START, {
				table: CONSTANTS.HR_CORE_CASE,
				filter: btoa(unescape(encodeURIComponent('active=false'))),
				subscribe: true
			});
		},

		[ACTIONS.AMB_SUBSCRIPTION_START]: createAmbSubscriptionEffect(
			CONSTANTS.AMB_SUBSCRIPTION_URL, {
				subscribeSucceededActionType: ACTIONS.RECORD_WATCHER_SUBSCRIBED,
				messageReceivedActionType: ACTIONS.RECORD_WATCHER_ITEM_CHANGED,
				subscribeFailedActionType: ACTIONS.FETCH_FAIL
			}
		),

		[ACTIONS.RECORD_WATCHER_SUBSCRIBED]: ({ updateState }) => {
			updateState({ isSubscribed: true });
		},

		[ACTIONS.RECORD_WATCHER_ITEM_CHANGED]: ({ state, dispatch }) => {
			dispatch(ACTIONS.FETCH_DATA, {
				reportSysId: state.properties.teamReportSysId
			});
		},
	}
}
