import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import styles from './snVisualCue.scss';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import { uniqueId } from 'lodash';
import { t } from 'sn-translate';

const view = (state, { dispatch }) => {
	const setTargetRef = (ref) => dispatch('PROPERTIES_SET', { targetRef: ref });
	const uid = `tooltip_id_${uniqueId()}`;

	return (
		<span className="circle-exclamation-style">
			{
				state.properties.show ? (
					<span>
						<now-tooltip
							id={uid}
							opened={false}
							target-ref={state.properties.targetRef}
							content={ t('Something went wrong with the updating, please refresh the page to get the live data') } />
						<now-icon
							icon="circle-exclamation-outline"
							size="sm"
							aria-describedby={uid}
							hook-insert={({ elm }) => setTargetRef(elm)}
							hook-destroy={() => setTargetRef(null)}>
						</now-icon>
					</span>):''
			}
		</span>
	);
};

export default createCustomElement('sn-hr-single-score-visual-cue', {
	renderer: { type: snabbdom },
	view,
	properties: {
		targetRef: { default: null },
		show: { default: false, reflect: true }
	},
	styles
});
