import { DATA } from '../constants.time-series';
/**
 * Flag series in Highcharts don't appear in the tooltip the way we want
 * therefore we each flag series we add (comments and initiatives) we add
 * an extra regular highcharts series that is invisible so the comments/initiatives
 * appear in the tooltip.
 *
 * This function returns the y value that is going to be assigned to all invisible points.
 * We make sure that the y value is within the range [etremeMin, etremeMax] defined by
 * navigator. It returns the y value of the first point in that range or the y value of the middle point.
 */

export default (singleSeries, extremeMin) => {
	if (singleSeries && singleSeries[DATA] && singleSeries[DATA].length > 0)
		if (extremeMin) {
			for (let i = 0; i < singleSeries[DATA].length; i++)
				if (singleSeries[DATA][i].x >= extremeMin)
					return singleSeries[DATA][i].y;
		} else
			return singleSeries[DATA][Math.floor((singleSeries[DATA].length - 1) / 2)].y;

	return 0;
};
