import mergeArraysByKey from './merge-arrays-by-key/merge-arrays-by-key';
/**
* Merge all the series and returns a flat object
* containing seriesData, seriesConfig, confidenceBandData, condidenceBandConfig, etc...
* @param {array} seriesBuilt - array of objects containing
* seriesData, seriesConfig, confidenceBandData, condidenceBandConfig, etc...
*/

export default (seriesFromDatasetBuilt = []) => {
	const seriesDataToMerge = [];
	let seriesConfig = {};

	const forecastDataToMerge = [];
	let forecastConfig = {};

	const confidenceBandDataToMerge = [];
	let confidenceBandConfig = {};

	const predictionBandDataToMerge = [];
	let predictionBandConfig = {};

	const trendlinesDataToMerge = [];
	let trendlinesConfig = {};

	const targetsDataToMerge = [];
	let targetsConfig = {};

	const thresholdDataToMerge = [];
	let thresholdConfig = {};

	let annotationsData = [];
	let annotationsConfig = {};

	const flatSeries = seriesFromDatasetBuilt.reduce(
		(accumulator, currentValue) => accumulator.concat(currentValue),
		[]
	);
	// flatSeries is an object like
	// [
	//   { seriesData: [], seriesConfig: {}, forecastData: [], ... }, <-- series 0, ds 0
	//   { seriesData: [], seriesConfig: {}, forecastData: [], ... }, <-- series 1, ds 0
	// ...
	//   { seriesData: [], seriesConfig: {}, forecastData: [], ... }, <-- series 0, ds 1
	//   { seriesData: [], seriesConfig: {}, forecastData: [], ... }, <-- series 1, ds 1
	// ...
	// ]

	// gather the data to merge
	flatSeries.forEach((series) => {
		// data series
		if (series.seriesData)
			seriesDataToMerge.push(series.seriesData);

		if (series.seriesConfig)
			seriesConfig = {
				...seriesConfig,
				...series.seriesConfig,
			};

		// change
		if (series.changeSeriesData)
			seriesDataToMerge.push(series.changeSeriesData);

		if (series.changeSeriesConfig)
			seriesConfig = {
				...seriesConfig,
				...series.changeSeriesConfig,
			};

		// change percent
		if (series.changePercentSeriesData)
			seriesDataToMerge.push(series.changePercentSeriesData);

		if (series.changePercentSeriesConfig)
			seriesConfig = {
				...seriesConfig,
				...series.changePercentSeriesConfig,
			};

		// forecast
		if (series.forecastData)
			forecastDataToMerge.push(series.forecastData);

		if (series.forecastConfig)
			forecastConfig = {
				...forecastConfig,
				...series.forecastConfig,
			};

		// confidence band
		if (series.confidenceBandData)
			confidenceBandDataToMerge.push(series.confidenceBandData);

		if (series.confidenceBandConfig)
			confidenceBandConfig = {
				...confidenceBandConfig,
				...series.confidenceBandConfig,
			};

		// prediction band
		if (series.predictionBandData)
			predictionBandDataToMerge.push(series.predictionBandData);

		if (series.predictionBandConfig)
			predictionBandConfig = {
				...predictionBandConfig,
				...series.predictionBandConfig,
			};

		// trendlines
		if (series.trendlinesData)
			trendlinesDataToMerge.push(series.trendlinesData);

		if (series.trendlinesConfig)
			trendlinesConfig = {
				...trendlinesConfig,
				...series.trendlinesConfig,
			};

		// targets
		if (series.targetsData)
			targetsDataToMerge.push(series.targetsData);

		if (series.targetsConfig)
			targetsConfig = {
				...targetsConfig,
				...series.targetsConfig,
			};

		// threshold
		if (series.thresholdData)
			thresholdDataToMerge.push(series.thresholdData);

		if (series.thresholdConfig)
			thresholdConfig = {
				...thresholdConfig,
				...series.thresholdConfig,
			};

		// comments
		if (series.commentsData)
			annotationsData = [
				...annotationsData,
				...series.commentsData,
			];

		if (series.commentsConfig)
			annotationsConfig = {
				...annotationsConfig,
				...series.commentsConfig,
			};

		// initiatives
		if (series.initiativesData)
			annotationsData = [
				...annotationsData,
				...series.initiativesData,
			];

		if (series.initiativesConfig)
			annotationsConfig = {
				...annotationsConfig,
				...series.initiativesConfig,
			};
	});

	const compareFunction = (point1, point2) => {
		return (point1.x - point2.x);
	};

	return {
		seriesConfig,
		seriesData: mergeArraysByKey('x', seriesDataToMerge).sort(compareFunction),
		forecastConfig,
		forecastData: mergeArraysByKey('x', forecastDataToMerge).sort(compareFunction),
		confidenceBandConfig,
		confidenceBandData: mergeArraysByKey('x', confidenceBandDataToMerge).sort(compareFunction),
		trendlinesConfig,
		trendlinesData: mergeArraysByKey('x', trendlinesDataToMerge).sort(compareFunction),
		targetsConfig,
		targetsData: mergeArraysByKey('x', targetsDataToMerge),
		thresholdConfig,
		thresholdData: mergeArraysByKey('x', thresholdDataToMerge),
		predictionBandConfig,
		predictionBandData: mergeArraysByKey('x', predictionBandDataToMerge).sort(compareFunction),
		annotationsData,
		annotationsConfig,
	};
};
