import getStyleConfiguration from 'parser/helpers/get-style-configuration';

/**
* Builds the data labels options
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API
*/
export default (chartConfiguration) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);

	return {
		showDataLabels: mainStyleConfiguration.show_chart_data_label,
		dataLabelConfig: {
			type: 'interval',
			align: 'above', // for stacked bar or grouped barchart
		},
	};
};
