import { isTrendByReportChart } from 'helpers/chart-type/is-trend-by-report';
import { parseReportingTrendByTimeseries } from 'parser/reporting-trend-by-timeseries';
/**
 * Parses the response if it's a reporting trend-by timeseries
 * @param {object} response - response from the reporting API
 */

export const parseServerChartConfiguration = (response) => {
	if (!response.result)
		return {};

	const chartConfiguration = response.result;
	if (isTrendByReportChart(chartConfiguration))
		return parseReportingTrendByTimeseries(chartConfiguration);

	return chartConfiguration;
};
