import getSeriesId from '../helpers/get-series-id';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
		isGlobalThreshold,
	} = params;
	const thresholdId = isGlobalThreshold ? 'threshold' : 'personal_threshold';
	const thresholdData = isGlobalThreshold ?
		singleSeries.derived_series.thresholds.global :
		singleSeries.derived_series.thresholds.personal;
	return thresholdData.map((singleThreshold) => {
		return {
			value: singleThreshold.value,
			label: singleThreshold.label,
			type: getSeriesId(seriesIndex, datasetIndex, thresholdId),
		};
	});
};
