export default (series) => {
	const sum = series.reduce((previousSum, element) => {
		return previousSum + element.y;
	}, 0);

	let previousSum = 0;
	return series.map((element) => {
		const value = (element.y / sum) * 100;
		previousSum += value;
		return {
			...element,
			y: previousSum,
		};
	});
};
