import { ACTIONS, CONSTANTS } from './constants';
import { t } from "sn-translate";
import map from "lodash/map";

export const filterReportDataByMonthAndAssignees = ({
	action: { payload },
	dispatch,
	state: {
		behaviors: {
			wsUserData: { userName }
		},
		isSubscribed
	},
	updateState }) => {

	const {
		accessibilityMode,
		accessibilityTable,
		accessibilityConfig,
		formattersConfig,
		seriesData,
		seriesConfig,
		xAxisConfig,
		yAxisConfig,
	} = payload.result.payload;

	// if there's no closed cases, skip all and show empty container
	if (!seriesData.length) {
		updateState({closedHrCases: seriesData});

		// if AMB hasn't subscribed, create a AMB connection
		if (!isSubscribed)
			dispatch(ACTIONS.INITIALIZE_AMB);

		return;
	}

	// get logged in user's series data key by user name
	const mySeriesKey = map(seriesConfig, (conf, key) => {
		if (conf.label === userName)
			return key;
	}).join('');

	// there's a chance user will not come back in report if he/she hasn't closed a case (i.e. new case worker)
	// if so, use first person in the series as key
	const { filterQuery } = seriesConfig[mySeriesKey || Object.keys(seriesConfig)[0]];

	// need to get query string for assignment_group
	const assignmentGroupFilterQuery = filterQuery.match(/assignment_group.+/)[0];

	// will be used for drill down query
	const aggregateQueriesByMonths = {};

	// create an array of curated series data from report to be used by chart
	const closedHrCases = map(seriesData, (series, index) => {
		let teammateCount = 0;
		const filteredSeriesData = {
			month: formattersConfig.xLabels[index],
			mine: mySeriesKey ? series[mySeriesKey] : 0,
			team: 0
		};

		// iterate through grouped data and segregate team cases from user cases
		map(series, (value, key) => {
			if (key === 'x')
				return;

			// need to create a query to display monthly view of closed cases
			// need to remove assigned_to and add group filter
			const queryString = seriesConfig[key.replace('y_', '')]
				.aggregateQueries[series.x]
				.replace(/assigned_to.+$/, '') + assignmentGroupFilterQuery;

			// assign query for month if it doesn't exist
			if (!aggregateQueriesByMonths[filteredSeriesData.month])
				aggregateQueriesByMonths[filteredSeriesData.month] = queryString;

			// add closed cases for all other than user
			if (key !== 'y_' + mySeriesKey) {
				filteredSeriesData.team += Number(value);
				// keep count of teammates not self
				teammateCount++;
			}
		});

		// get the average # of closed cases for team
		// chart is configured to round to the nearest whole number
		if (teammateCount > 0)
			filteredSeriesData.team = filteredSeriesData.team / teammateCount;

		return filteredSeriesData;
	});

	let chartTitle = t('Cases closed {0}', [closedHrCases[0].month]);

	// if more than one month, need to show duration
	if (closedHrCases.length > 1)
		chartTitle = t('Cases closed {0} - {1}', [closedHrCases[0].month ,closedHrCases[closedHrCases.length - 1].month]);

	updateState({
		accessibilityMode,
		accessibilityConfig,
		accessibilityTable,
		aggregateQueriesByMonths,
		chartTitle,
		closedHrCases,
		xLabel: xAxisConfig.title,
		yLabel: yAxisConfig.y.title
	});

	// if AMB hasn't subscribed, create a AMB connection
	if (!isSubscribed)
		dispatch(ACTIONS.INITIALIZE_AMB);
};

// use to open detail page
export const handleDrillDown = ({ state: { aggregateQueriesByMonths }, action, dispatch }) => {
	action.stopPropagation();

	const { payload: { data: { seriesDataset: { month } } } } = action;
	let params = {
		listTitle: t('Cases closed {0}', [month]),
		table: CONSTANTS.HR_CORE_CASE,
		query: aggregateQueriesByMonths[month],
		columns: 'number,state,opened_for,subject_person,hr_service,short_description,assignment_group,assigned_to',
		disableQuickEdit: true,
	};

	dispatch('ITEM_SELECTED', {
		renderer_type: 'layout',
		params
	});
};

// initial call to get report data
export const initializeBarChart = ({ properties: { teamReportSysId }, dispatch }) => {
	dispatch(ACTIONS.FETCH_DATA, { reportSysId: teamReportSysId });
};
