import React from 'react';
import getSeriesYKey from 'parser/xenolith/time-series/series/helpers/get-series-y-key';
import getAnnotationId from 'parser/xenolith/time-series/series/annotations/get-annotation-id';
import { i18nHelper } from 'par-components';
import { getValueWithUnit, getNumberValue } from 'helpers/value';
import { getPrecision } from 'parser/helpers/precision/index';
import {
	CHANGE,
	CHANGE_PERCENT,
	CONFIDENCE_BAND,
	PREDICTION_BAND,
	TREND,
	TARGET,
	PERSONAL_TARGET,
} from './series-names.constant';

/**
 * dataPoint is an object that has the following format:
* {
* 	x: 1529798400000,                   -- the timestamp
*   y_data_ds_0_series_0: 34,			-- all the scores for all the datasets
*   y_data_ds_0_series_1: 2,               and series for this timestamp
*	y_data_ds_1_series_0: 22,
*   ...
*   y_change_ds_0_series_0: 4,			-- all the derived series for all the datasets
*   y_change_percent_ds_0_series_0: 2,     and series for this timestamp
*	y_target_ds_0_series_0: 22,
*	y_trendline_ds_0_series_0: 22,
*   ...
* }
* see getSeriesYKey method for the convention of naming y keys
*/


/**
* Build the jsx element for derived series that will go into a cell
* @param {object} dataPoint - see above
* @param {number} datasetIndex - used to get the right score in dataPoint
* @param {number} seriesIndex - used to get the right score in dataPoint
* @param {object} chartConfiguration
* @return {jsx}
*/
export const buildDerivedSeriesElement = (dataPoint, seriesIndex, datasetIndex, chartConfiguration) => {
	const derivedSeries = [];
	const mainStyleConfiguration = chartConfiguration.style_configurations[0] || {};
	const decimalPrecision = getPrecision(chartConfiguration, 0, 0);
	const formattingOptions = {
		precision: decimalPrecision,
		unit: mainStyleConfiguration.unit,
		locale: mainStyleConfiguration.locale,
	};

	/**
	 * -------------- CHANGE AND CHANGE % --------------
	 */
	const changeValue = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, CHANGE)];
	const changePercentValue = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, CHANGE_PERCENT)];

	let changePercentLabel = '';
	if (typeof changePercentValue !== 'undefined')
		changePercentLabel = `(${getNumberValue(changePercentValue, formattingOptions)}%)`;

	if (typeof changeValue !== 'undefined')
		derivedSeries.push(
			<div key={`change-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Change')}: ${getValueWithUnit(changeValue, formattingOptions)} ${changePercentLabel}`}
			</div>);


	/**
	 * -------------- TREND --------------
	 */
	const trendValue = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, TREND)];
	if (typeof trendValue !== 'undefined')
		derivedSeries.push(
			<div key={`trend-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Trendline')}: ${getValueWithUnit(trendValue, formattingOptions)}`}
			</div>);

	/**
	 * -------------- TARGETS --------------
	 */
	const targetValue = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, TARGET)];
	if (typeof targetValue !== 'undefined')
		derivedSeries.push(
			<div key={`target-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Target')}: ${getValueWithUnit(targetValue, formattingOptions)}`}
			</div>);

	/**
	 * -------------- PERSONAL TARGETS --------------
	 */
	const personalTargetValue = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, PERSONAL_TARGET)];
	if (typeof personalTargetValue !== 'undefined')
		derivedSeries.push(
			<div key={`ptarget-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Personal Target')}: ${getValueWithUnit(personalTargetValue, formattingOptions)}`}
			</div>);

	/**
	 * -------------- CONFIDENCE BAND --------------
	 */
	const confidenceBandFrom = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, CONFIDENCE_BAND + '_y0')];
	const confidenceBandTo = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, CONFIDENCE_BAND + '_y1')];
	if (typeof confidenceBandFrom !== 'undefined' || typeof confidenceBandTo !== 'undefined')
		derivedSeries.push(
			<div key={`confidence-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Confidence Band')}: ${getValueWithUnit(confidenceBandFrom, formattingOptions)} - ${getValueWithUnit(confidenceBandTo, formattingOptions)}`}
			</div>);

	/**
	 * -------------- PREDICTION BAND --------------
	 */
	const predictionBandFrom = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, PREDICTION_BAND + '_y0')];
	const predictionBandTo = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, PREDICTION_BAND + '_y1')];
	if (typeof predictionBandFrom !== 'undefined' || typeof predictionBandTo !== 'undefined')
		derivedSeries.push(
			<div key={`prediction-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Prediction Band')}: ${getValueWithUnit(predictionBandFrom, formattingOptions)} - ${getValueWithUnit(predictionBandTo, formattingOptions)}`}
			</div>);

	/**
	 * -------------- THRESHOLDS --------------
	 *
	 * thresholds are attached to the
	 * dataPoints in get-table-body-data.js
	 */
	if (dataPoint.thresholds && dataPoint.thresholds.length) {
		const thresholdLabels = dataPoint.thresholds.map((threshold) => {
			return threshold.label;
		});
		derivedSeries.push(
			<div key={`threshold-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Thresholds')}: ${thresholdLabels.join(', ')}`}
			</div>);
	}

	/**
	 * -------------- COMMENTS --------------
	 *
	 */
	const comment = dataPoint[getAnnotationId('comments', seriesIndex, datasetIndex)];
	if (typeof comment !== 'undefined')
		derivedSeries.push(
			<div key={`comment-${seriesIndex}-${datasetIndex}-${dataPoint.x}`}>
				{`${i18nHelper.t('Comments')}: ${comment}`}
			</div>);

	const cellDerivedSeriesKey = `div-cell-derived-series-${seriesIndex}-${datasetIndex}-${dataPoint.x}`;
	return (
		<div key={cellDerivedSeriesKey} className="par-data-table-cell-derived-series">
			{derivedSeries}
		</div>
	);
};
