import { STACKED } from '../../constants';
import getStyleConfiguration from '../../helpers/get-style-configuration';

export default (chartConfiguration) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);

	if (!mainStyleConfiguration[STACKED])
		return {};

	return {
		plotOptions: {
			series: {
				allowPointSelect: false,
				turboThreshold: 0,
			},
			column: {
				stacking: 'normal',
			},
		},
	};
};
