/**
* Builds the series name from reporting trendby series
* @param {object} params - object that contains the following:
- singleTrendBySeries - one of the series that is in the array
reportingTrendSeries.series, each singleSeries corresponds to a dataset
- groupByValue
- hasMultipleDatasets - used to know whether we should specify the dataset name
* @returns {string} series name
*/

export const buildSeriesName = ({ seriesName, groupByValue, hasMultipleDatasets }) => {
	if (!groupByValue)
		return seriesName;

	return hasMultipleDatasets ? `[${seriesName}] ${groupByValue}` : groupByValue;
};
