import buildTickConfig from '../axis-tick/';
/**
*  Builds a yAxis config (the primary or the secondary axes)
* @param {Object} datasetStyleConfig - flat style object for one dataset
*/

export default (datasetStyleConfig, order = 0) => {
	return {
		title: datasetStyleConfig.y_axis_title,
		orientation: datasetStyleConfig.y_axis_opposite ? 'right' : 'left',
		order,
		tickConfig: buildTickConfig({
			enableLabels: datasetStyleConfig.y_axis_labels_enabled,
			...(datasetStyleConfig.label_formatter ?
				{ labelFormatter: datasetStyleConfig.label_formatter } :
				{}),
		}),
	};
};
