export default (sources) => {
	let result = {
		yAxis: [],
		series: [],
		legend: {},
		chart: {},
	};
	sources.forEach((source) => {
		if (source.yPlotLines) {
			if (result.yAxis.length >= 0)
				result.yAxis[0].plotLines = Object.assign(
					[],
					result.yAxis[0].plotLines,
					source.yPlotLines
				);
			delete source.plotLines;
		}

		if (source.xPlotLines) {
			if (result.xAxis.length >= 0)
				result.xAxis[0].plotLines = Object.assign(
					[],
					result.xAxis[0].plotLines,
					source.xPlotLines
				);
			delete source.xPlotLines;
		}

		if (source.series && source.yAxis) {
			result.series = result.series.concat(source.series);
			if (source.changePercentOnly)
				result.yAxis = source.yAxis;
			else
				result.yAxis = result.yAxis.concat(source.yAxis);

			if (source.xAxis)
				result.xAxis = source.xAxis;
		} else
			result = Object.assign(result, source);
	});
	return result;
};
