import { dateHelper } from 'par-components';

/**
* Build the xFormat Xenolith date formatter used to format timestamps on the xAxis and the tooltip
* @param {object} styleConfiguration
* @param {object} formattingOptions - contains the following:
* isXAxis: is formatting the xAxis
* type: short or long
* frequency: minutely, daily, monthly etc.
* fiscalYearStart
* see https://code.devsnc.com/dev/par-components/tree/master/src/js/helpers/date
*/

export const buildXFormat = (styleConfiguration, formattingOptions = {}) => {
	// user provided x formatter
	if (styleConfiguration.x_format &&
		typeof styleConfiguration.x_format === 'function')
		return {
			xFormat(timestamp) {
				return styleConfiguration.x_format(timestamp);
			},
		};

	// frequency based x formatter
	const { frequency, isXAxis } = formattingOptions;
	if (frequency === 'minutely') {
		if (isXAxis)
			return {};

		return {
			xFormat: '%a %d, %B, %I:%M %p',
		};
	}

	return {
		xFormat(timestamp) {
			return dateHelper.readableDate(
				timestamp,
				formattingOptions,
			);
		},
	};
};
