import { getDefaultLocale } from './get-default-locale';

/**
 * Returns a number with a certain precision
 *
 * @param  {number}  [number=0]
 * @param  {boolean} [precision=false]
 * @param  {string} [locale]
 *
 * @return {String}
 */
export const getNumberValue = (number = 0, { precision = false, locale } = {}) => {
	// if the y value is null, return null
	if (number === null)
		return null;

	// if locale is not defined, use the default locale
	const localeToUse = locale || getDefaultLocale();

	// ServiceNow format: 'en.US', toLocaleString format: 'en-US'
	const jsLocale = localeToUse.replace('.', '-');

	// Position of decimal
	const dotPos = number.toString().indexOf('.');

	// if no decimal precision, return the number as is, localized
	if (typeof precision !== 'number') {
		// number is an integer
		if (dotPos === 0)
			return number.toLocaleString(jsLocale);

		// number of fraction digits
		const numberOfFractionDigits = number.toString().slice(dotPos).length - 1;
		return number.toLocaleString(jsLocale,
			{
				maximumFractionDigits: numberOfFractionDigits,
				minimumFractionDigits: numberOfFractionDigits,
			},
		);
	}

	function bankersRoundOff(num) {
		let isNegative;
		if (num < 0)
			isNegative = true;
		if (dotPos === -1)
			return parseInt(num, 10);

		const integral = num.toString().substring(0, dotPos);
		let roundedNumber;
		if (integral % 2 === 0)
			roundedNumber = parseInt(integral, 10);
		else if (!isNegative)
			roundedNumber = parseInt(integral, 10) + 1;
		else
			roundedNumber = parseInt(integral, 10) - 1;
		return roundedNumber;
	}
	// apply the locale and decimal precision
	let localizedValue;
	const isNumberEquidistant = number.toString().substring(dotPos + 1, number.toString().length) === '5';

	if (precision === 0 && isNumberEquidistant)
		localizedValue = bankersRoundOff(number).toLocaleString(jsLocale);
	else
		localizedValue = number.toLocaleString(jsLocale,
			{
				maximumFractionDigits: precision,
				minimumFractionDigits: precision,
			},
		);

	if (localizedValue === '-0')
		return '0';

	return localizedValue;
};
