/**
* Returns the data that will be put in the data table head
* Namely: series name
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API that contains styleConfigurations and datasets
* - styleConfigurations
* - datasets
* @return {array} - returns an array that contains series names in the following order:
* series 0 from ds 0
* series 1 from ds 0
* ...
* series 0 from ds n
*/

export const getTableHeadData = (chartConfiguration = {}) => {
	const seriesNamesArray = ['']; // empty text for row 0, col 0
	chartConfiguration.datasets.forEach((dataset) => {
		dataset.series.forEach((singleSeries) => {
			seriesNamesArray.push(singleSeries.name);
		});
	});
	return seriesNamesArray;
};

