import { camelCase, upperFirst } from 'lodash';

// Helpers
import { dateHelper, i18nHelper } from 'par-components';
import { getNumberValue } from 'helpers/value';

export const dataTableGenerator = {
	/**
	 * Get the data rows for the chart
	 *
	 * @param  {Object} highchartsOptions
	 */
	getDataTable(highchartsOptions, businessCalendarLabels) {
		// If it's a navigator, don't add the data table
		if (highchartsOptions.isNavigator)
			return [];

		const dataTable = [];

		// Empty the data table (in case we're rewriting it)
		dataTable.length = 0;

		// Hold the labels in separate arrays (for uniqueness)
		// x and y refer to the table, not the x, y of the h.point
		// They're both left one empty because the table doesn't start at
		// zero (there's the top left of the table which is always empty)
		const xLabels = [0];
		const yLabels = [0];

		// Extra data while parsing
		const extraData = {};

		// Series need to be reordered, to make sure that forecast always
		// comes before anything that isn't data
		const series = this.getReorderedSeries(highchartsOptions);
		series.forEach((singleSeries) => {
			// If a series doesn't have a name, then ignore it
			if (!singleSeries.name)
				return;

			const type = camelCase(this.getSeriesId(singleSeries));
			const serieType = camelCase(singleSeries.type);
			let method = 'parse' + upperFirst(serieType) + upperFirst(type) + 'Series';

			if (typeof this[method] !== 'function')
				method = 'parse' + upperFirst(type) + 'Series';

			if (typeof this[method] !== 'function')
				method = 'parseSeries';

			if (typeof this[method] === 'function')
				this[method].call(this, {
					series: singleSeries,
					highchartsOptions,
					dataTable,
					xLabels,
					yLabels,
					extraData,
					businessCalendarLabels,
				});
		});

		// Parse the thresholds
		this.parseThresholdSeries({
			highchartsOptions,
			dataTable,
			xLabels,
			yLabels,
			extraData,
		});

		// If there is any extra data, add it
		this.handleExtraData({
			dataTable,
			xLabels,
			yLabels,
			extraData,
		});

		// Handle the empty fields before returning it
		this.handleEmptyFields({
			dataTable,
			xLabels,
			yLabels,
		});

		// If there is any extra data, add it
		this.handleTimestampLabels({
			highchartsOptions,
			dataTable,
			businessCalendarLabels,
		});

		return dataTable;
	},

	/**
	 * Append a data serie to the data table (passed as an argument in the
	 * options)
	 *
	 * @param  {Object} options
	 */
	parseSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Add the name to the serie
		options.dataTable[0].push(this.getSeriesName(options.series));

		// Get the x position in the table
		const xPosition = this.getXPosition(options);

		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			// Get the position (matched with the table position)
			let yPosition = options.yLabels.indexOf(label);
			if (yPosition < 0) {
				options.yLabels.push(label);
				yPosition = options.yLabels.length - 1;
			}

			// If the row hasn't been created yet, create it
			if (!options.dataTable[yPosition])
				options.dataTable[yPosition] = [label];

			// Set the point
			options.dataTable[yPosition][xPosition] = this.getPointValue(point, options.series);
		});
	},

	/**
	 * Append a treshold serie to the data table (passed as an argument in
	 * the options)
	 *
	 * @param  {Object} options
	 */
	parseThresholdSeries(options) {
		// Build the threshold string
		const globalThresholds = [];
		const personalThresholds = [];

		const yAxis = (options.highchartsOptions.yAxis || [])[0] || {};
		const plotLines = yAxis.plotLines || [];

		plotLines.forEach((plotLine) => {
			if (plotLine.isPersonal)
				personalThresholds.push(plotLine.label.text.trim());
			else
				globalThresholds.push(plotLine.label.text.trim());
		});

		// If no personal or global thresholds return
		if (!personalThresholds.length && !globalThresholds.length)
			return;

		options.extraData.thresholds = []
			.concat(personalThresholds)
			.concat(globalThresholds)
			.sort();
	},

	/**
	 * Append a change serie to the data table (passed as an argument in the
	 * options)
	 *
	 * @param  {Object} options
	 */
	parseChangeSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Append it to the extra data
		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			if (!options.extraData[label])
				options.extraData[label] = {};

			options.extraData[label].change = this.getPointValue(point, options.series);
		});
	},

	/**
	 * Append a change percent serie to the data table (passed as an argument in
	 * the options)
	 *
	 * @param  {Object} options
	 */
	parseChangePercentSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Append it to the extra data
		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			if (!options.extraData[label])
				options.extraData[label] = {};

			options.extraData[label].changePercent = this.getPointValue(point, options.series);
		});
	},

	/**
	 * Append a comments serie to the data table (passed as an argument in the
	 * options)
	 *
	 * @param  {Object} options
	 */
	parseCommentsTooltipSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Append it to the extra data
		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			if (!options.extraData[label])
				options.extraData[label] = {};

			options.extraData[label].comments = point.text;
		});
	},

	/**
	 * For the comments series, do nothing, so that it is ignored
	 *
	 * @param  {Object} options
	 */
	parseCommentsSeries() {},

	/**
	 * Append a trendline serie to the data table (passed as an argument in the
	 * options)
	 *
	 * @param  {Object} options
	 */
	parseTrendlineSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Append it to the extra data
		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			if (!options.extraData[label])
				options.extraData[label] = {};

			options.extraData[label].trendline = this.getPointValue(point, options.series);
		});
	},

	/**
	 * Append a prediction band serie to the data table (passed as an argument
	 * in the options)
	 *
	 * @param  {Object} options
	 */
	parsePredictionBandSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Append it to the extra data
		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			if (!options.extraData[label])
				options.extraData[label] = {};

			options.extraData[label].predictionBand = this.getPointValue(point, options.series);
		});
	},

	/**
	 * Append a confidence band serie to the data table (passed as an argument
	 * in the options)
	 *
	 * @param  {Object} options
	 */
	parseConfidenceBandSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Append it to the extra data
		options.series.data.forEach((point) => {
			const label = this.getPointXValue(point);

			if (!options.extraData[label])
				options.extraData[label] = {};

			options.extraData[label].confidenceBand = this.getPointValue(point, options.series);
		});
	},

	/**
	 * Append a target serie to the data table (passed as an argument in
	 * the options)
	 *
	 * @param  {Object} options
	 */
	parseTargetSeries(options) {
		if (!this.initializeOptions(options))
			return;

		// Get the x position in the table
		const xPosition = this.getOtherPosition(options);
		let lastTargetIndex = 0;
		let lastTarget = false;

		options.dataTable.forEach((row, index) => {
			if (index <= 0)
				return;

			if (options.series.data[lastTargetIndex] && row[0] >= options.series.data[lastTargetIndex].x) {
				lastTarget = options.series.data[lastTargetIndex];
				lastTargetIndex++;
			}

			if (!row[xPosition])
				row[xPosition] = '';

			if (lastTarget)
				row[xPosition] += (row[xPosition] ? '\n' : '') + this.getSeriesName(options.series) + ': ' + this.getPointValue(lastTarget, options.series);
		});
	},

	/**
	 * The same as targets
	 */
	parsePersonalTargetSeries() {
		return this.parseTargetSeries.apply(this, arguments);
	},

	/**
	 * Append a forecast serie to the data table (passed as an argument in
	 * the options)
	 *
	 * @param  {Object} options
	 */
	parseForecastSeries(options) {
		if (!this.initializeOptions(options))
			return;

		const { t } = i18nHelper;

		// Get the x position in the table (only the first serie)
		let xPosition = 1;

		if (options.series.datasetIndex) {
			let positionStart = 0;
			let foundPosition = false;

			options.series.forEach((singleSerie) => {
				const id = this.getSeriesId(singleSerie);
				if (id === 'data' && !foundPosition)
					positionStart++;

				if (singleSerie.datasetIndex === options.series.datasetIndex)
					foundPosition = true;
			});

			if (foundPosition && positionStart)
				xPosition = positionStart;
		}

		const businessCalendarLabels = options.businessCalendarLabels;
		options.series.data.forEach((point, index) => {
			if (index <= 0)
				return;
			const frequency = options.highchartsOptions.frequency || 'daily';
			let label;
			if (frequency === 'biz_calendar_frequency' && businessCalendarLabels)
				label = this.getPointLabel(point, options, businessCalendarLabels) + ' (' + t('forecasted') + ')';
			else
				label = this.getPointLabel(point, options) + ' (' + t('forecasted') + ')';

			// Get the position (matched with the table position)
			let yPosition = options.yLabels.indexOf(label);
			if (yPosition < 0) {
				options.yLabels.push(label);
				yPosition = options.yLabels.length - 1;
			}

			// If the row hasn't been created yet, create it
			if (!options.dataTable[yPosition])
				options.dataTable[yPosition] = [label];

			// Set the point
			options.dataTable[yPosition][xPosition] = this.getPointValue(point, options.series);
		});
	},


	/**
	 * Append a data serie to the data table (for the heatmap type only)
	 *
	 * @param  {Object} options
	 */
	parseHeatmapDataSerie(options) {
		if (!this.initializeOptions(options))
			return;

		const xCategories = options.series.xAxis.categories;
		const yCategories = options.series.yAxis.categories;

		options.series.data.forEach((point) => {
			const xLabel = xCategories[point.x] || point.x;
			const yLabel = yCategories[point.y] || point.y;

			// Get the position (matched with the table position)
			let xPosition = options.xLabels.indexOf(xLabel);
			if (xPosition <= 0) {
				options.xLabels.push(xLabel);
				xPosition = options.xLabels.length - 1;
				options.dataTable[0][xPosition] = xLabel;
			}

			// Get the position (matched with the table position)
			let yPosition = options.yLabels.indexOf(yLabel);
			if (yPosition <= 0) {
				options.yLabels.push(yLabel);
				yPosition = options.yLabels.length - 1;
			}

			// If the row hasn't been created yet, create it
			if (!options.dataTable[yPosition])
				options.dataTable[yPosition] = [yLabel];

			// Set the point
			options.dataTable[yPosition][xPosition] = point.value || this.getPointValue(point, options.series);
		});
	},

	/**
	 * Initialize the serie and return true/false if it succeeded
	 *
	 * @param  {Object} options
	 *
	 * @return {boolean}
	 */
	initializeOptions(options) {
		// If it's not visible, just ignore it
		if (options.series.visible === false)
			return false;

		// Do nothing if there is no data in the serie
		if (!options.series.data || !options.series.data.length)
			return false;

		// If navigator serie, don't put it in
		if (/^navigator.+/i.test(options.series.name))
			return false;

		// If the first series, then push the header row in the data table
		if (!options.dataTable.length)
			options.dataTable.push(['']);

		return true;
	},

	/**
	 * Get the point x value
	 *
	 * @param  {Object} point
	 *
	 * @return {string}
	 */
	getPointXValue(point) {
		if (Array.isArray(point))
			return point[0];

		return point.name || point.category || point.x || '';
	},

	/**
	 * Get the point label
	 *
	 * @param  {Object} point
	 * @param  {Object} options
	 *
	 * @return {string}
	 */
	getPointLabel(point, options, businessCalendarLabels) {
		const label = this.getPointXValue(point);
		if (businessCalendarLabels)
			return this.getDateLabel(label, options, businessCalendarLabels);
		return this.getDateLabel(label, options);
	},

	/**
	 * Get the date label (usually used for the xAxis values)
	 *
	 * @param  {string|number} timestamp
	 * @param  {Object} options
	 *
	 * @return {string}
	 */
	getDateLabel(timestamp, options, labels) {
		const frequency = options.highchartsOptions.frequency || 'daily';
		if (frequency === 'biz_calendar_frequency' && labels)
			return this.getTimestampLabelsForBusinessCalendar(timestamp, labels);

		const dateLabel = dateHelper.getMoment(timestamp);
		if (dateLabel.isValid() && !/[a-z]+/i.test(timestamp))
			return dateHelper.readableDate(dateLabel, { frequency: frequency });

		return timestamp;
	},

	/**
	 * Get the point value
	 *
	 * @param  {Object} point
	 * @param  {Object} series
	 *
	 * @return {string}
	 */
	getPointValue(point, series) {
		const precision = series.precision || series.decimalPrecision || 0;

		if (Array.isArray(point))
			if (point.length === 2)
				return getNumberValue(point[1], { precision });

			else if (point.length === 3)
				return [getNumberValue(point[1], { precision }), getNumberValue(point[2], { precision })];

		if (typeof point.valueFormatted !== 'undefined')
			return point.valueFormatted;

		if (typeof point.value_formatted !== 'undefined')
			return point.value_formatted;

		const value = typeof point.value === 'undefined' ? point.y : point.value;
		return getNumberValue(value, { precision });
	},

	/**
	 * Get the series name
	 *
	 * @param  {Object} series
	 *
	 * @return {string}
	 */
	getSeriesName(series) {
		// If it's the default name, then just return empty (apparently
		// that is better)
		if (/^series\s[0-9]+$/i.test(series.name))
			return '';

		return series.name;
	},

	/**
	 * Get the other name position
	 *
	 * @param  {Object} serie
	 *
	 * @return {number}
	 */
	getOtherName() {
		const { t } = i18nHelper;
		return t('Info');
	},

	/**
	 * Get the series id of a certain serie
	 *
	 * @param  {Object} serieOptions
	 */
	getSeriesId(serieOptions) {
		const id = serieOptions.id || 'data';
		if (/[a-z0-9]{32}/i.test(id))
			return 'data';

		if (/^forecast\s/i.test(id))
			return 'forecast';

		return id;
	},

	/**
	 * Get the other name position
	 *
	 * @param  {Object} options
	 * @param  {string} title
	 *
	 * @return {number}
	 */
	getXPosition(options, title) {
		const searchTitle = (typeof title === 'undefined' ? this.getSeriesName(options.series) : title);

		let xPosition = options.xLabels.indexOf(searchTitle);
		if (xPosition < 0) {
			options.xLabels.push(searchTitle);
			xPosition = options.xLabels.length - 1;
		}
		return xPosition;
	},

	/**
	 * Get the x position
	 *
	 * @param  {Object} options
	 *
	 * @return {number}
	 */
	getOtherPosition(options) {
		const title = this.getOtherName();
		const position = this.getXPosition(options, title);
		if (options.dataTable && options.dataTable[0])
			options.dataTable[0][position] = title;

		return position;
	},

	/**
	 * Get the sort index of the series
	 *
	 * @param  {Object} chart
	 *
	 * @return {number}
	 */
	getSeriesSortIndex(series) {
		const seriesOrder = ['data', 'forecast', 'change', 'change_percent', 'trendline', 'prediction_band', 'confidence_band', 'threshold', 'target', 'personal_target', 'comments_tooltip'];
		const seriesId = this.getSeriesId(series);
		const index = seriesOrder.indexOf(seriesId);
		if (index < 0)
			return 9999;

		return index;
	},

	/**
	 * Reorder the series and return a shallow copy of the series array
	 *
	 * @param  {Object} highchartsOptions
	 *
	 * @return {Array}
	 */
	getReorderedSeries(highchartsOptions) {
		// Shallow copy the array (don't change the default chart series
		// array)
		const series = highchartsOptions.series.slice();
		series.sort((a, b) => {
			return this.getSeriesSortIndex(a) - this.getSeriesSortIndex(b);
		});
		return series;
	},

	/**
	 * Handle the data attached as extra (comments, change, change percent,
	 * trend line, confidence band)
	 *
	 * @param  {Object} [options={}]
	 */
	handleExtraData(options = {}) {
		const { t } = i18nHelper;

		// Get the x position in the table
		const xPosition = this.getOtherPosition(options);

		// Append it to each row
		options.dataTable.forEach((row, index) => {
			if (index <= 0)
				return;

			if (!row[xPosition])
				row[xPosition] = '';

			const extraData = options.extraData[row[0]];
			if (extraData) {
				if (typeof extraData.change !== 'undefined') {
					row[xPosition] += (row[xPosition] ? '\n' : '') + t('Change') + ': ' + extraData.change;

					if (typeof extraData.changePercent !== 'undefined')
						row[xPosition] += ` (${extraData.changePercent}%)`;
				} else if (typeof extraData.changePercent !== 'undefined')
					row[xPosition] += (row[xPosition] ? '\n' : '') + t('Change %') + ': ' + extraData.changePercent;

				if (typeof extraData.trendline !== 'undefined')
					row[xPosition] += (row[xPosition] ? '\n' : '') + t('Trendline') + ': ' + extraData.trendline;

				if (typeof extraData.predictionBand !== 'undefined')
					row[xPosition] += (row[xPosition] ? '\n' : '') + t('Prediction Band') + ': ' + extraData.predictionBand.join(' - ');

				if (typeof extraData.confidenceBand !== 'undefined')
					row[xPosition] += (row[xPosition] ? '\n' : '') + t('Confidence Band') + ': ' + extraData.confidenceBand.join(' - ');
			}

			if (typeof options.extraData.thresholds !== 'undefined' && options.extraData.thresholds.length > 0)
				row[xPosition] += (row[xPosition] ? '\n' : '') + t('Thresholds') + ': ' + options.extraData.thresholds.join(', ');

			if (extraData && typeof extraData.comments !== 'undefined')
				row[xPosition] += (row[xPosition] ? '\n' : '') + t('Comments') + ': ' + extraData.comments;
		});
	},

	/**
	 * After the table has been generated, check for empty fields and apply
	 * proper labels and stuff
	 *
	 * @param  {object} options
	 */
	handleEmptyFields(options) {
		const { t } = i18nHelper;
		let emptyLabelColumn = true;
		options.yLabels.forEach((y, yIndex) => {
			if (options.dataTable[yIndex] && options.dataTable[yIndex][0] !== '')
				emptyLabelColumn = false;

			options.xLabels.forEach((x, xIndex) => {
				if (options.dataTable[yIndex] && typeof options.dataTable[yIndex][xIndex] === 'undefined')
					options.dataTable[yIndex][xIndex] = t('No score');
			});
		});

		// Check wether the other column is empty
		const otherIndex = this.getOtherPosition(options);
		const otherColumnEmpty = options.dataTable.every((row, index) => (index < 1 || !row[otherIndex]));

		if (otherColumnEmpty)
			options.dataTable.forEach((row) => {
				delete row[otherIndex];
			});

		// Since the data table is an array, it's also an object
		options.dataTable.emptyLabelColumn = emptyLabelColumn;
	},

	/**
	 * Handle the timestamp labels (parse them)
	 *
	 * @param  {Object} options
	 */
	handleTimestampLabels(options) {
		const { t } = i18nHelper;

		const forecastedText = t('forecasted');
		const labels = options.businessCalendarLabels;
		options.dataTable.forEach((row) => {
			// Forecasted labels are already readable
			if (typeof row[0] === 'string' && row[0].indexOf(forecastedText) >= 0)
				return;

			row[0] = this.getDateLabel(row[0], options, labels);
		});
	},

	/**
	 * Find dates for business calendar
	 *
	 * @param {String} timeStamp
	 */
	getTimestampLabelsForBusinessCalendar(timestamp, labels) {
		if (labels)
			for (let i = 0; i < labels.length; i++)
				if (labels[i].date === timestamp)
					return labels[i].label_name;
		return undefined;
	},
};
