import { configColors, i18nHelper } from 'par-components';
import { paZindex, TARGETS, PERSONAL_TARGETS_SERIES_ID, GLOBAL_TARGETS_SERIES_ID } from '../constants.time-series';
import extendData from './extend-data';
import { includeReviewDates } from './include-review-dates';

export default (target = {}, isPersonal = false, extremeMax) => {
	if (!target.data)
		return {};

	const { t } = i18nHelper;
	let extremeMaxDerived = extremeMax;

	if (Array.isArray(target.data) && target.data[target.data.length - 1] && target.data[target.data.length - 1].x >= extremeMax)
		extremeMaxDerived = target.data[target.data.length - 1].x;

	return {
		zIndex: paZindex[TARGETS],
		visible: true,
		data: includeReviewDates(extendData(target.data, extremeMaxDerived) || {}),
		name: isPersonal ? t('Personal Target') : t('Target'),
		step: 'left',
		id: isPersonal ? PERSONAL_TARGETS_SERIES_ID : GLOBAL_TARGETS_SERIES_ID,
		type: 'line',
		dataLabels: {
			enabled: true,
			x: 12,
			style: {
				fontSize: '12px',
				fontFamily: 'SourceSansPro',
			},
		},
		marker: {
			enabled: true,
			radius: 5,
			symbol: 'circle',
			states: {
				hover: {
					radius: 5,
				},
			},
		},
		color: target.color || (isPersonal ? configColors['chart-colors']['chart-light'] : configColors['chart-colors']['chart-dark']),
		enableMouseTracking: false,
		showInLegend: false,
	};
};
