import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {preUserData, preWsConfigSysId} from 'sn-uxpage-presource';
import {t} from 'sn-translate';
const moment = require('moment');

import { DEFAULT_CARD_SCROLL_DISTANCE } from '../sn-hr-card-set/hrCardSetConstants';
import {
	AMB_SUBSCRIPTION_CHANGE,
	AMB_SUBSCRIPTION_CHANGE_FAILED,
	AMB_ITEM_CHANGED,
	AMB_UPDATE_SUBSCRIPTION,
	DATA_LOADING,
	DATA_LOADING_ERROR,
	FETCH_DATA,
	PREFETCH_DATA,
	FETCH_DATA_SUCCEEDED,
	FETCH_DATA_FAILED
} from './hrCaseCardSetConstants';

import actionHandlers from './hrCaseCardSetActions';
import '../sn-hr-card-set';
import 'sn-hr-shared';
import styles from './hrCaseCardSet.scss';

const view = state => {
	const {
		cards,
		showViewAll,
		viewAllText,
		emptyText,
		status,
		properties: {
			cardScrollDistance,
			graphic,
			backgroundColor,
			fontColor,
			fadeColor
		}
	} = state;

	const processedCards = cards.map(card => {
		const newCard = Object.assign({}, card);
		newCard.topLabelValue = {
			label: card.number,
			value: t('Opened {0}', moment(card.opened_at).format('MMM D'))
		};

		newCard.cardTitle = card.short_description;

		newCard.bottomLabelValues = [
			{ label: t('Priority'), value: card.priority },
			{ label: t('State'), value: card.state },
			{ label: t('Last updated'), value: moment(card.sys_updated_on).fromNow() }
		];

		return newCard;
	});

	let scrollDistance = parseInt(cardScrollDistance);
	if (isNaN(scrollDistance) || scrollDistance <= 0)
		scrollDistance = DEFAULT_CARD_SCROLL_DISTANCE;

	const cardSetProps = {
		cards: processedCards,
		showViewAll,
		viewAllText,
		emptyText,
		graphic,
		backgroundColor,
		fontColor,
		fadeColor,
		cardScrollDistance: scrollDistance,
	};

	if (status === DATA_LOADING) {
		return (
			<div className="empty-state-wrapper">
				<sn-hr-empty-state type="loading" />
			</div>
		);
	}
	else if (status === DATA_LOADING_ERROR) {
		return (
			<div className="empty-state-wrapper">
				<sn-hr-empty-state type="loading-error" />
			</div>
		);
	}
	return (
		<sn-hr-card-set {...cardSetProps} />
	);
};

createCustomElement('sn-hr-case-card-set', {
	initialState: {
		/**
		 * The list of cards to display. Card objects should have the
		 * same properties as an sn-hr-card
		 *
		 * @type {Array[object]}
		 */
		cards: [],

		/**
		 * Whether to show the "view all" text
		 *
		 * @type {boolean}
		 */
		showViewAll: false,

		/**
		 * The text to display when viewAllText is true. E.g. "View all HR cases"
		 *
		 * @type {String}
		 */
		viewAllText: '',

		/**
		 * The text to display when the list is empty
		 *
		 * @type {String}
		 */
		emptyText: '',

		/**
		 * The table of the record that the cards represent
		 *
		 * @type {String}
		 */
		table: '',

		/**
		 * The query that the cards represent
		 *
		 * @type {String}
		 */
		query: null,

		/**
		 * The list of columns to show in the list if the "View all" link
		 * is clicked
		 *
		 * @type {String}
		 */
		columns: '',

		/**
		 * The loading status of the component
		 *
		 * @type {String}
		 */
		status: DATA_LOADING,

		/**
		 * Whether realtime was just turned on (used to determine if we need
		 * to resubscribe even if the query has not changed)
		 *
		 * @type {boolean}
		 */
		activatingRealtime: false
	},

	properties: {
		/**
		 * The number of cards to scroll when clicking on the left/right arrows
		 *
		 * @type {String}
		 */
		cardScrollDistance: { default: DEFAULT_CARD_SCROLL_DISTANCE + ''},

		/**
		 * Reference to sn_hr_ws_lp_card_list_config table
		 *
		 * @type {String}
		 */
		configurationId: {
			default: '',
			onChange(value, previousValue, { dispatch }) {
				dispatch(PREFETCH_DATA);
			}
		},

		/**
		 * The limit of the number of records to show
		 *
		 * @type {String}
		 */
		recordLimit: {
			default: '10',
			onChange(value, previousValue, { dispatch }) {
				dispatch(PREFETCH_DATA);
			}
		},

		/**
		 * The graphic to display when the list is empty
		 *
		 * @type {String}
		 */
		graphic: { default: '' },

		/**
		 * The background color to display when the list is empty
		 *
		 * @type {String}
		 */
		backgroundColor: { default: '' },

		/**
		 * The font color to use when the list is empty
		 *
		 * @type {String}
		 */
		fontColor: { default: '' },

		/**
		 * Whether the component should be refreshed in realtime
		 *
		 * @type {boolean}
		 */
		 isRealtime: {
			 default: true,
			 onChange(value, previousValue, { dispatch, state, updateState }) {
				 if (value) {
					 updateState({
						 ...state,
						 activatingRealtime: true
					 });
				 	dispatch(PREFETCH_DATA);
				}
				else
					dispatch(AMB_UPDATE_SUBSCRIPTION, { active: false });
			 }
		 },

		/**
 		 * The color to fade the left/right scroll arrows into (as a hex string).
 		 * If empty, no fade gradient will be shown
		 *
 		 * @type {String}
 		 */
 		fadeColor: { default: '' }
	},

	actions: {
		[AMB_ITEM_CHANGED]: { stopPropagation: true },
		[AMB_SUBSCRIPTION_CHANGE]: { stopPropagation: true },
		[AMB_SUBSCRIPTION_CHANGE_FAILED]: { stopPropagation: true },
		[AMB_UPDATE_SUBSCRIPTION]: { stopPropagation: true },
		[FETCH_DATA]: { stopPropagation: true },
		[FETCH_DATA_FAILED]: { stopPropagation: true },
		[FETCH_DATA_SUCCEEDED]: { stopPropagation: true },
		[PREFETCH_DATA]: { stopPropagation: true }
	},
	actionHandlers: actionHandlers,

	/**
	 * Helper for moment i18n (preUserData) and workspace click behavior (prewWsConfigSysId)
	 */
	behaviors: [preUserData, preWsConfigSysId],
	renderer: {type: snabbdom},
	view,
	styles
});
