import {
	DERIVED_SERIES,
	CHANGE,
	CHANGE_PERCENT,
	X_AXIS_PLOTLINES,
} from './constants.time-series';
import {
	hasScores,
	hasDerivedSeries,
} from './helpers/index';
import buildDataSeries from './data-series/build-data-series';
import buildYoyChangePercentSeries from './change-percent-series/build-yoy-change-percent-series';
import isChangePercentOnly from './helpers/is-change-percent-only';
import buildXAxisPlotlines from './x-axis-plotlines/build-all-x-axis-plotlines';
import parseYoySeries from './helpers/parse-yoy-series';
import getExtremeMax from './helpers/get-extreme-max';
import buildXAxis from '../../x-axis/index';
import { STYLE_CONFIGURATIONS } from '../../../constants';

// build the series for Year Over Year

export default (datasetStyleConfig, singleSeries, colors) => {
	let series = [];
	let xPlotLines = [];
	const yAxis = [];
	const extremeMin = datasetStyleConfig.min || 0;
	const extremeMax = datasetStyleConfig.max || getExtremeMax(singleSeries);

	const xAxis = buildXAxis({
		[STYLE_CONFIGURATIONS]: [
			{
				...datasetStyleConfig,
			},
		],
	});
	xAxis[0] = {
		...xAxis[0],
		dateTimeLabelFormats: {
			month: '%b',
		},
	};

	// data series or scores
	if (hasScores(singleSeries)) {
		const yoyScoresSeries = parseYoySeries(singleSeries, extremeMin, extremeMax);
		yoyScoresSeries.forEach((singleYearSeries, index) => {
			const color = colors[index % colors.length];
			series.push(buildDataSeries(datasetStyleConfig, singleYearSeries, color));
		});
	}

	// derived series
	if (singleSeries[DERIVED_SERIES]) {
		// change
		if (hasDerivedSeries(singleSeries, CHANGE)) {
			const yoyChangeSeries = parseYoySeries(singleSeries[DERIVED_SERIES][CHANGE], extremeMin, extremeMax);
			yoyChangeSeries.forEach((singleYearSeries, index) => {
				const color = colors[index % colors.length];
				series.push(buildDataSeries(datasetStyleConfig, singleYearSeries, color));
			});
		}

		// change percent
		if (hasDerivedSeries(singleSeries, CHANGE_PERCENT)) {
			const yoyChangePercentSeries = parseYoySeries(singleSeries[DERIVED_SERIES][CHANGE_PERCENT], extremeMin, extremeMax);
			const result = buildYoyChangePercentSeries(datasetStyleConfig, yoyChangePercentSeries, colors, isChangePercentOnly(singleSeries));
			series = series.concat(result.series);
			yAxis.push(result.yAxis);
		}

		// xAxis plotLines
		if (hasDerivedSeries(singleSeries, X_AXIS_PLOTLINES)) {
			const xAxisValues = singleSeries[DERIVED_SERIES][X_AXIS_PLOTLINES];
			xPlotLines = xPlotLines.concat(buildXAxisPlotlines(xAxisValues));
		}
	}

	const result = {
		series,
		...(xPlotLines.length > 0 ? { xPlotLines } : {}),
		...(xAxis.length > 0 ? { xAxis } : {}),
		...(yAxis.length > 0 ? { yAxis } : {}),
		changePercentOnly: isChangePercentOnly(singleSeries),
	};
	return result;
};
