import {
	CHART_HEIGHT,
	CHART_WIDTH,
	CHART_MARGIN,
	CHART_BACKGROUND_COLOR,
} from './constants.chart';
import getStyleConfiguration from '../../helpers/get-style-configuration';


export default (chartConfiguration = {}) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const chartConfig = {
		margin: mainStyleConfiguration[CHART_MARGIN],
		height: mainStyleConfiguration[CHART_HEIGHT],
		width: mainStyleConfiguration[CHART_WIDTH],
		backgroundColor: mainStyleConfiguration[CHART_BACKGROUND_COLOR] || 'transparent',
		style: {
			margin: '0 auto',
		},
	};
	if (mainStyleConfiguration && mainStyleConfiguration[CHART_MARGIN])
		chartConfig.margin = mainStyleConfiguration[CHART_MARGIN];
	return {
		chart: chartConfig,
	};
};
