import {createRef} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import '@servicenow/now-image';

import {dispatchScroll, hexToRGB} from './hrCardSetHelpers';
import {HR_CARD_SET_VIEW_ALL_CLICKED} from './hrCardSetConstants';
import '../sn-hr-card';

const view = (state, { dispatch }) => {
	const {
		properties: {
			cards,
			cardWidth,
			cardScrollDistance,
			showViewAll,
			viewAllText,
			emptyText,
			backgroundColor,
			fontColor,
			graphic,
			fadeColor
		}
	 } = state;

	const cardSetRef = createRef();

	const doScroll = dir => {
		const scrollEndLimit = cardSetRef.current.scrollWidth - cardSetRef.current.offsetWidth;
		// +16 is for the card margin (from the scss)
		const scrollDistance = (cardWidth + 16) * cardScrollDistance;
		let newPos;
		if (dir === 'left')
			newPos = Math.max(cardSetRef.current.scrollLeft - scrollDistance, 0);
		else
			newPos = Math.min(cardSetRef.current.scrollLeft + scrollDistance, scrollEndLimit);

		cardSetRef.current.scrollTo({ left: newPos, behavior: 'smooth' });
	};

	const scrollLeft = () => {
		doScroll('left');
	};

	const scrollRight = () => {
		doScroll('right');
	};

	const onScroll = () => {
		dispatchScroll(dispatch, cardSetRef);
	};

	const onViewAllClicked = () => {
		dispatch(HR_CARD_SET_VIEW_ALL_CLICKED);
	};

	const getViewAllIcon = () => {
		return (
			<now-icon icon="chevron-end-outline" size="lg" />
		);
	};

	const getEndContent = () => {
		if (!showViewAll || !viewAllText)
			return '';

		return (
			<div className="view-all">
				<a href="javascript:void(0)" onclick={onViewAllClicked}>
					<span>
						{viewAllText}
						{ getViewAllIcon() }
					</span>
				</a>
			</div>
		);
	};

	let resizeTimeout = null;

	/**
	 * Invoked when the page is resized
	 */
	const scrollOnResize = () => {
		// If resizeTimeout is not null, that means another "thread" in the
		// queue is waiting for the 100ms timeout before attempting to
		// re-render the scroll arrows. Cancel that "wait"
		if (resizeTimeout)
			clearTimeout(resizeTimeout);

		// Start the wait again
		resizeTimeout = setTimeout(() => {
			// Thread has finished waiting without being canceled.
			// Reset the timeout to null
			resizeTimeout = null;

			// Dispatch the "scroll" to re-render the arrows
			dispatchScroll(dispatch, cardSetRef);
		}, 100);
	};

	let cardModifiedTimeout = null;
	/**
	 * Invoked when a card is added to the list
	 */
	const onCardHookModified = () => {
		// If cardModifiedTimeout is not null, that means another "thread" in the
		// queue is waiting for the 650ms timeout before attempting to
		// re-render the scroll arrows. Cancel that "wait"
		if (cardModifiedTimeout)
			clearTimeout(cardModifiedTimeout);

		// Start the wait again
		cardModifiedTimeout = setTimeout(() => {

			// Thread has finished waiting without being canceled.
			// Reset the timeout to null
			cardModifiedTimeout = null;

			// Dispatch the "scroll" to re-render the arrows
			dispatchScroll(dispatch, cardSetRef);
		}, 650);
	}

	const onListHookInsert = () => {
		window.addEventListener('resize', scrollOnResize);
	};

	const onListHookDestroy = () => {
		window.removeEventListener('resize', scrollOnResize);
	};

	const getLeftContent = () => {
		if (cards.length > 0)
			return ( <now-icon icon="chevron-left-outline" onclick={scrollLeft} size="lg" /> );

		if (graphic)
			return ( <now-image src={require(`./assets/${graphic}_1.png`)} alt='' /> );

		return null;
	};

	const getMainContent = (leftScrollHidden, rightScrollHidden) => {
		if (cards.length > 0) {
			return (
				<div class={{ "card-set-wrapper": true, "left-scroll-active": !leftScrollHidden, "right-scroll-active": !rightScrollHidden }}>
					<div className="card-set" ref={cardSetRef} on-scroll={onScroll} hook-insert={onListHookInsert} hook-destroy={onListHookDestroy}>
						{cards.map((card, i) => (
							<sn-hr-card
								{...card}
								class={{last: i === cards.length - 1, first: i === 0}}
								hook-insert={onCardHookModified}
								hook-destroy={onCardHookModified}
								width={cardWidth}
								append-to-payload={{table: card.table, sys_id: card.id}}
							/>
						))}
						{getEndContent()}
					</div>
				</div>
			);
		}

		return (
			<div class={{"text-wrapper": true}}>
				<div className="text" style={{color: fontColor, backgroundColor: backgroundColor}}>
					{ emptyText }
				</div>
			</div>
		);
	};

	const getRightContent = () => {
		if (cards.length > 0)
			return ( <now-icon icon="chevron-right-outline" onclick={scrollRight} size="lg" /> );

		if (graphic)
			return ( <now-image src={require(`./assets/${graphic}_2.png`)} alt='' /> );

		return null;
	};

	const hideLeftScroll = cards.length > 0 && !state.showScrollLeft;
	const hideRightScroll = cards.length > 0 && !state.showScrollRight;
	const rgb = hexToRGB(fadeColor);

	const leftStyle = rgb == null || cards.length <= 0 ? {} : {
		"background": `linear-gradient(to left, RGBA(${rgb[0]}, ${rgb[1]}, ${rgb[2]}, 0) 0%, RGBA(${rgb[0]}, ${rgb[1]}, ${rgb[2]}, 1) 100%)`
	};
	const rightStyle = rgb == null || cards.length <= 0 ? {} : {
		"background": `linear-gradient(to right, RGBA(${rgb[0]}, ${rgb[1]}, ${rgb[2]}, 0) 0%, RGBA(${rgb[0]}, ${rgb[1]}, ${rgb[2]}, 1) 100%)`
	};

	return (
		<div class={{ container: true, empty: cards.length === 0}}>
			<div class={{
				scroll: cards.length > 0,
				img: cards.length === 0,
				left: true,
				hide: hideLeftScroll }}
				style={leftStyle}>
				{getLeftContent()}
			</div>

			{getMainContent(hideLeftScroll, hideRightScroll)}

			<div class={{
				scroll: cards.length > 0,
				img: cards.length === 0,
				right: true,
				hide: hideRightScroll }}
				style={rightStyle}>
				{getRightContent()}
			</div>
		</div>
	);
};

export default view;
