export default (params) => {
	return {
		disabled: !params.enableLabels,
		...(params.numberOfTicks ? { ticks: params.numberOfTicks } : {}),
		...(params.labelSize ? { fontSize: params.labelSize } : {}),
		...(params.labelFormatter ? { tickFormat: params.labelFormatter } : {}),
		...(params.labelColor ? { labelColor: params.labelColor } : {}),
		...(params.tickColor ? { tickColor: params.tickColor } : {}),
		...(params.tickWidth ? { strokeWidth: params.tickWidth } : {}),
		...(params.enableLabels ? { disabled: !params.enableLabels } : {}),
		...(params.fontWeight ? { fontWeight: params.fontWeight } : {}),
	};
};
