import {t} from 'sn-translate';
const moment = require('moment');

import {BREACHED_STAGE_CONFIG, OUT_OF_SCHEDULE_STAGE_CONFIG} from '../constants';
import {inSchedule, inProgress, isPaused, isBreachedStage} from './stageUtils';
import {
	formatDuration,
	formatBreachedDuration,
	formatRemainingDuration
} from './dateFormatUtils';

export default ({
	slaName,
	fullDuration,
	duration,
	hasBreached,
	breachedOn,
	breachedOnValue,
	stageDisplayValue,
	stageValue,
	schedule,
	endTime
}) => {
	const baseConfig = {
		slaName,
		displayValue: null,
		stageValue: null,
		percentage: null,
		resumes: null,
		stops: null,
		breachedOn: null,
		durationRemaining: null,
		endTime: null
	};

	if (!inProgress(stageValue) && !isBreachedStage(stageValue) && !isPaused(stageValue)) {
		return {
			...baseConfig,
			displayValue: stageDisplayValue,
			stageValue,
			endTime
		};
	}

	baseConfig.percentage = Math.round(((fullDuration - duration) / fullDuration) * 100);

	if (hasBreached || baseConfig.percentage >= 100) {
		let displayValue = null;
		if (breachedOnValue) {
			const momentBreached = moment.utc(breachedOnValue, 'YYYY-MM-DD HH:mm:ss');
			const timeSinceBreachedMillis = moment().diff(momentBreached);
			const timeSinceBreachedSecs = timeSinceBreachedMillis / 1000;
			if (timeSinceBreachedSecs >= 0)
				displayValue = formatBreachedDuration(timeSinceBreachedSecs);
		}

		displayValue = displayValue || t('Breached');

		return {
			...baseConfig,
			breachedOn,
			...BREACHED_STAGE_CONFIG,
			displayValue
		};
	}

	const durationDisplayValue = formatDuration(duration);
	const scheduleActualStart = schedule && schedule.actual_start && schedule.actual_start.display_value;
	const scheduleActualEnd = schedule && schedule.actual_end && schedule.actual_end.display_value;

	const isInSchedule = inSchedule(schedule);

	baseConfig.durationRemaining = durationDisplayValue;

	return {
		...baseConfig,
		stageValue,
		displayValue: formatRemainingDuration(duration),
		stops: isInSchedule ? scheduleActualEnd : null,
		resumes: !isInSchedule ? scheduleActualStart : null
	};
};
