import { DATASET_SERIES } from '../constants';

/**
 *
 * Returns the number of datasets
 * @param  {Object} [chartConfiguration={}]
 */
export default (chartConfiguration) => {
	if (chartConfiguration[DATASET_SERIES] && chartConfiguration[DATASET_SERIES].length > 0)
		return chartConfiguration[DATASET_SERIES].length;
	return 0;
};
