import { STYLE_CONFIGURATIONS } from '../constants';
import hasYAxis from './has-y-axis';

/**
 * Returns the y-axis config for a dataset
 * @param  {Object} [chartConfiguration={}]
 */
export default (chartConfiguration) => {
	const chartConfigurationWithY = JSON.parse(JSON.stringify(chartConfiguration));
	let yIndex = 1;
	if (chartConfigurationWithY[STYLE_CONFIGURATIONS])
		chartConfigurationWithY[STYLE_CONFIGURATIONS].forEach(
			(datasetConfig, index) => {
				if (index === 0 || !hasYAxis(chartConfiguration, index))
					chartConfigurationWithY[STYLE_CONFIGURATIONS][index].y_axis_index = 0;
				else {
					chartConfigurationWithY[STYLE_CONFIGURATIONS][index].y_axis_index = yIndex;
					yIndex++;
				}
			}
		);

	return chartConfigurationWithY;
};
