import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import 'sn-component-carousel';
import '@servicenow/now-empty-state';

import 'sn-hr-shared';

import actionHandlers from './actionHandlers';
import styles from './campaignsAnnouncements.scss';
import view from './view';

import {
	FETCH_SLIDE_DATA,
	FETCH_SLIDE_DATA_SUCCESS,
	FETCH_SLIDE_DATA_FAILURE,
	NO_SLIDE_DATA,
	status
} from './constants';

createCustomElement('sn-component-campaigns-announcements', {
	renderer: {
		type: snabbdom,
		view
	},

	properties: {
		/**
		 * The name of the identifier used to get assigned content
		 * @type {string}
		 */
		contentIdentifier: {default: ''},

		/**
		 * The slides to pass to the carousel component
		 * @type {object[]}
		 */
		slides: {default: []},

		/**
		 * The aria-label of navigation buttons to pass to the carousel component
		 * @type {object}
		 */
		navButtonLabels: {
			default: {
				next: 'Next Announcement',
				previous: 'Previous Announcement'
			}
		},

		/**
		 * The component status
		 * @type {('loading'|'ready'|'error'|'empty')}
		 */
		status: {default: status.LOADING},

		/**
		 * height, in px
		 * UI builder does not accept numbers
		 * @type {string}
		 */
		height: {default: '416'},

		/**
		 * Whether to display rounded corners or not
		 * @type {boolean}
		 */
		roundedCorners: {default: false}
	},

	actions: {
		[FETCH_SLIDE_DATA]: {stopPropagation: true},
		[FETCH_SLIDE_DATA_SUCCESS]: {stopPropagation: true},
		[FETCH_SLIDE_DATA_FAILURE]: {stopPropagation: true},
		[NO_SLIDE_DATA]: {stopPropagation: true}
	},

	actionHandlers,

	styles
});
