import { STYLE_CONFIGURATIONS } from '../constants';

/**
 * Returns whether a dataset has a y-axis
 * @param  {Object} [chartConfiguration={}, datasetIndex]
 */
export default (chartConfiguration, datasetIndex) => {
	// the first dataset always has a yAxis
	if (datasetIndex === 0)
		return true;

	return chartConfiguration[STYLE_CONFIGURATIONS]
		&& chartConfiguration[STYLE_CONFIGURATIONS].length > datasetIndex
		&& (chartConfiguration[STYLE_CONFIGURATIONS][datasetIndex].has_y_axis !== false);
};
