import React from 'react';
import PropTypes from 'prop-types';
import { isEqual, uniqueId } from 'lodash';
import * as dateFormats from './custom-dateformats';
import Highcharts from 'highcharts/highstock';

// Style
import './component.highcharts.scss';

// Helpers
import { visualPatternsEnabled, accessibilityEnabled, getPatterns, getDashStyles } from 'helpers/wcag';

// Components
import DataTable from './data-table/component.data-table';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		options: PropTypes.object,
		clickBehaviors: PropTypes.array,
		onSetExtremes: PropTypes.func,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		options: {},
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'Highcharts';

	/**
	 * The constructor of the class
	 */
	constructor(...args) {
		super(...args);

		this.handleSetExtremes = this.handleSetExtremes.bind(this);
	}

	/**
	 * When the component mounted/the dom is ready
	 */
	componentDidMount() {
		this.initializeHighcharts();
	}

	/**
	 * When the component updates (or its props)
	 */
	componentDidUpdate(prevProps) {
		if (!isEqual(prevProps.options, this.props.options))
			if (this.props.options && isEqual(prevProps.options.series, this.props.options.series) && isEqual(prevProps.options.yAxis, this.props.options.yAxis))
				this.updateHighcarts();
			else
				this.initializeHighcharts();
	}

	/**
	 * When the component unmounts
	 */
	componentWillUnmount() {
		this.destroyHighcharts();
		this.highcharts = false;
	}

	/**
	 * Get the highcharts options (with the default things)
	 *
	 * @param  {boolean} [update=false]
	 *
	 * @return {Object}
	 */
	getOptions(update = false) {
		const options = Object.assign({}, this.props.options);

		const dashStyles = getDashStyles();
		const patterns = getPatterns();

		// Add the patterns and exclude from export
		if (visualPatternsEnabled() || accessibilityEnabled())
			if (options.series) {
				options.series.forEach((series, seriesIndex) => {
					if (visualPatternsEnabled()) {
						const pattern = {
							pattern: patterns[seriesIndex % patterns.length],
						};
						series.fillColor = pattern;

						if (series.type === 'column')
							series.color = pattern;
						else
							series.color = '#000000';

						series.dashStyle = dashStyles[seriesIndex % dashStyles.length];
					}

					if (accessibilityEnabled())
						if (!series.data || !series.data.length)
							series.includeInCSVExport = false;
				});
			}

		// If it's just an update, return the rest of the options
		if (update)
			return options;

		// Add click event
		const { clickBehaviors } = this.props;
		if (!options.isNavigator &&
			clickBehaviors &&
			typeof clickBehaviors[0] === 'function' &&
			!this.isCategoryChart(options))
			options.plotOptions = {
				...options.plotOptions,
				series: {
					...options.plotOptions.series,
					point: {
						...(options.plotOptions.series || {}).point,
						events: {
							click: (event) => {
								if (!event || !event.point)
									return;

								clickBehaviors[0](event.point);
							},
						},
					},
				},
			};

		// Add set extremes event
		const { onSetExtremes } = this.props;
		if (options.isNavigator && onSetExtremes)
			options.xAxis[0].events = {
				setExtremes: this.handleSetExtremes,
			};

		return options;
	}

	/**
	* Called when extremes are changed from within highcharts
	*
	* @param  {Event} event
	*/
	handleSetExtremes(event) {
		event.preventDefault();
		this.props.onSetExtremes(event.min, event.max);
	}

	/**
	* Initializes the highcharts instance by first destroying it, if it exists
	* and then recreating it
	*/
	initializeHighcharts() {
		this.destroyHighcharts();

		// Use UTC for parsing the timestamps
		Highcharts.setOptions({
			global: {
				useUTC: true,
			},
		});

		// Set the date formats for highcharts
		Highcharts.dateFormats = dateFormats;

		// Disable keyboard events for highcharts
		if (accessibilityEnabled())
			Highcharts.Chart.prototype.addKeyboardNavEvents = () => {};

		const options = this.getOptions();

		const method = options.isHighstock ? Highcharts.stockChart : Highcharts.chart;
		this.highcharts = method(this.container, options);

		// We use for easier identification
		this.highcharts.snowId = uniqueId();
	}

	/**
	 * Update highcharts with the new options
	 */
	updateHighcarts() {
		if (visualPatternsEnabled())
			this.initializeHighcharts();
		else {
			const options = this.getOptions(true);
			this.highcharts.update(options);
			this.highcharts.reflow();
		}
	}

	/**
	 * Destroy highcharts instance
	 */
	destroyHighcharts() {
		if (this.highcharts)
			try {
				this.highcharts.destroy();
			} catch (e) {
				console.error(e);
			}
	}

	/**
	 * Checks if a chart is a category chart
	 */
	isCategoryChart(options) {
		return (options.xAxis && options.xAxis[0] && options.xAxis[0].categories);
	}

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		return (
			<div
				className="par-highcharts"
				data-test="visualization-chart-highcharts"
			>
				<div
					className="highcharts-container"
					ref={(container) => { this.container = container; }}
				/>

				{accessibilityEnabled() &&
				!this.props.options.isNavigator &&
				!this.props.options.isSingleScore &&
					<DataTable
						highchartsOptions={this.props.options}
						businessCalendarLabels={this.props.businessCalendarLabels}
					/>
				}
			</div>
		);
	}
}
