/* eslint no-restricted-globals: ["off"] */
import { configColors } from 'par-components';
import {
	THRESHOLD,
	VALUE,
	FORMATTED_VALUE,
	COLOR,
	LABEL,
	paZindex,
} from '../constants.time-series';
import { RTL } from '../../../../constants';

export default (thresholdInput = {}, index = 0, isPersonal, datasetStyleConfig = {}) => {
	if (typeof thresholdInput[VALUE] !== 'number' || isNaN(thresholdInput[VALUE]))
		return {};

	return {
		color: thresholdInput[COLOR] || (isPersonal ? configColors['chart-colors']['chart-light'] : configColors['chart-colors']['chart-dark']),
		dashStyle: 'dash',
		width: 2,
		id: THRESHOLD + index.toString(),
		label: {
			text: (thresholdInput[LABEL] || '') + ' ' + (thresholdInput[FORMATTED_VALUE] || ''),
			align: datasetStyleConfig[RTL] ? 'right' : 'left',
		},
		isPersonal,
		zIndex: paZindex[THRESHOLD],
		value: thresholdInput[VALUE] || 0,
		showInLegend: false,
		enableMouseTracking: false,
	};
};
