import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import 'sn-hr-shared';
import "@servicenow/now-empty-state";
import "@servicenow/now-icon";
import "@servicenow/now-text-link";
import {t} from 'sn-translate';
import map from "lodash/map";

import {
    SET_LINKS_FROM_PROPERTIES,
    LOAD_LINKS_USING_CONTENT_DELIVERY,
    CONTENT_DELIVERY_LOAD_SUCCEEDED,
	CONTENT_DELIVERY_LOAD_FAILED,
	MISSING_LINK_PROPERTY,
	status
} from "./constants"

import { isAbsoluteURL } from "../common/methods";
import actionHandlers from "./actionHandlers";
import style from "./linkList.scss";

const _createPlaceholder = ({status: s, roundedCorners, minHeight}) => {
	if (s === status.READY) return null;

	let placeholder = null;

	switch (s) {
		case status.EMPTY:
			placeholder = <sn-hr-empty-state type="empty" widget={t('link')} variant="auto" />
			break;
		case status.ERROR:
			placeholder = <now-empty-state type={status.ERROR} variant="auto" />;
			break;
		case status.LOADING:
			placeholder = (
				<now-empty-state
					widget={t("Links")}
					type={status.LOADING}
					variant="auto"
				/>
			);
			break;
		default:
			placeholder = <now-empty-state type={status.EMPTY} variant="auto" />;
	}

	const style = minHeight ? {"min-height": `${minHeight}px`} : {};

	return (
		<div
			class={{"placeholder": true, "rounded-corners": roundedCorners}}
			style={style}
			slot="slides">
			{placeholder}
		</div>
	);
};

const view = state => {
	const { links, properties } = state;
	const { title, roundedCorners } = properties;

	if (properties.status !== status.READY)
		return _createPlaceholder(properties);

	const style = properties.minHeight ? {"min-height": `${properties.minHeight}px`} : {};

	return (
		<section
			class={{"rounded-corners": roundedCorners}}
			style={style}>
			{title && (
				<header>
					<h1 className="heading">{title}</h1>
				</header>
			)}

			<article>
				<div>
					<ul>
						{map(links, link => (
							<li>
								<a href={link.url}
								   	target={isAbsoluteURL(link.url) ? '_blank' : undefined}
									rel={isAbsoluteURL(link.url) ? 'noopener' : undefined}
								>{link.title}</a>
							</li>
						))}
					</ul>
				</div>
			</article>
		</section>
	);
};

createCustomElement("sn-link-list", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		title: {
			reflect: true
		},
		links: {
			reflect: true
		},
		contentIdentifier: {
			reflect: true
		},
		status: {
			default: status.LOADING
		},
		roundedCorners: {
			default: false
		},
		minHeight: {
			default: ''
		}
	},
	styles: style,
	actions: {
		[CONTENT_DELIVERY_LOAD_FAILED]: { stopPropagation: true },
		[CONTENT_DELIVERY_LOAD_SUCCEEDED]: { stopPropagation: true },
		[LOAD_LINKS_USING_CONTENT_DELIVERY]: { stopPropagation: true },
		[MISSING_LINK_PROPERTY]: { stopPropagation: true },
		[SET_LINKS_FROM_PROPERTIES]: { stopPropagation: true }
	},
	actionHandlers
});
