import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-highlighted-value.scss';
import '@servicenow/now-icon';

const renderIcon = (icon, showIcon, dir) => {
	if (icon || showIcon) {
		return (
			<now-icon
				icon={icon ? icon : 'presence-fill'}
				dir={dir}
				size="sm"
				className="now-highlighted-value-icon"
				aria-hidden="true"
			/>
		);
	}
};

const view = (state) => {
	const {
		label = '',
		status,
		color,
		variant,
		icon,
		showIcon,
		showTooltip,
		dir
	} = state.properties;
	const statusOrColor = status || color;

	const spanOptions = {
		'class': {
			'now-highlighted-value': true,
			['-' + statusOrColor]: statusOrColor,
			['-' + variant]: variant
		}
	};

	if (showTooltip)
		spanOptions.title = label;

	return (
		<span {...spanOptions}>
			{renderIcon(icon, showIcon, dir)}
			<span className="now-line-height-crop">
				<span className="now-highlighted-value-label">{label}</span>
			</span>
		</span>
	);
};

/**
 * Highlighted values communicate the value of an item
 * by promoting visibility and readability.
 *
 * Use highlighted values when you want to convey the status in a certain area,
 * such as a card, list, or widget. A highlighted value may be combined with a status icon.
 *
 * ```jsx
 * <sn-hr-highlighted-value label="Value" color="warning" show-icon>
 * </sn-hr-highlighted-value>
 *
 * <sn-hr-highlighted-value label="Tag" color="blue" variant="secondary" icon="clock-outline">
 * </sn-hr-highlighted-value>
 * ```
 *
 * @seismicElement sn-hr-highlighted-value
 * @summary A styled label with an optional icon.
 */
createCustomElement('sn-hr-highlighted-value', {
	properties: {
		/**
		 * Text displayed as the highlighted value.
		 * @type {string}
		 */
		label: {required: true, schema: {type: 'string'}},
		/**
		 * Sets the highlighted value background color.
		 * @type {('critical'|'high'|'warning'|'moderate'|'positive'|'info'|'low')}
		 */
		color: {
			default: 'low',
			schema: {
				type: 'string',
				enum: [
					'critical',
					'high',
					'warning',
					'moderate',
					'positive',
					'info',
					'low'
				]
			}
		},
		/**
		 * Sets the shade of the highlighted value background and the color of
		 * the text and/or icon.
		 * @type {('primary'|'secondary'|'tertiary')}
		 */
		variant: {
			default: 'tertiary',
			schema: {type: 'string', enum: ['primary', 'secondary', 'tertiary']}
		},
		/**
		 * Flag that determines whether an icon displays with the label.
		 * @type {boolean}
		 */
		showIcon: {default: false, schema: {type: 'boolean'}},
		/**
		 * When `show-icon` is set, a solid circle will be displayed in the
		 * highlighted value. Set this property with a valid icon name from
		 * the `now-icon` set to display a custom icon instead.
		 * @type {string}
		 */
		icon: {schema: {type: 'string'}},
		/**
		 * Flag that determines whether to include the label as a title tooltip
		 */
		showTooltip: {default: false, schema: {type: 'boolean'}}
	},
	view,
	styles,
	renderer: { type: snabbdom }
});
