import buildSingleYaxisConfig from './build-single-y-axis-config';
import hasOneChangePercentSeries from '../../../helpers/has-one-change-percent-series';
import hasYAxis from '../../../helpers/has-y-axis';
import getStyleConfiguration from '../../../helpers/get-style-configuration';
import { getYAxisName } from './get-y-axis-name';
import { getPrecision } from 'parser/helpers/precision/index';
import { buildYFormat } from './build-y-format';

/**
* Builds the yAxisConfig
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API
*/
export default (chartConfiguration) => {
	let yAxisConfig = {};

	// To determine how many axis we have, just put their names in a set and check the size in the end
	const yAxisSet = new Set();
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const formattingOptions = {
		precision: getPrecision(chartConfiguration, 0, 0),
		locale: mainStyleConfiguration.locale,
		unit: mainStyleConfiguration.unit,
	};

	// build yAxis for each datasets if they have one
	chartConfiguration.style_configurations.forEach((styleConfiguration, datasetIndex) => {
		if (hasYAxis(chartConfiguration, datasetIndex)) {
			const axisName = getYAxisName(datasetIndex, styleConfiguration);
			yAxisConfig[axisName] = {
				...buildSingleYaxisConfig(
					styleConfiguration,
					datasetIndex
				),
			};
			yAxisSet.add(axisName);
		}
	});

	// change percent yAxis
	// there is always one change percent yAxis
	// => always the same name change_percent_y_axis
	if (hasOneChangePercentSeries(chartConfiguration)) {
		yAxisConfig.change_percent_y_axis = {
			...buildSingleYaxisConfig(
				{
					...chartConfiguration.style_configurations[0],
					y_axis_opposite: !chartConfiguration.style_configurations[0].y_axis_opposite,
					y_axis_title: '',
					label_formatter(value) {
						return value + ' %';
					},
				},
				yAxisSet.size + 1,
			),
		};
		yAxisSet.add('change_percent_y_axis');
	}

	// if there's only one yAxis (it has to be the primary one)
	// need to detructure its config into yAxisConfig
	// see https://code.devsnc.com/dev/emotive-xenolith/issues/134
	if (yAxisSet.size === 1)
		yAxisConfig = yAxisConfig[getYAxisName(0)];

	return {
		yAxisConfig,
		yGridConfig: {
			dashPattern: mainStyleConfiguration.y_axis_grid_dotted ? '1,3' : '0',
		},
		hideYGridlines: !mainStyleConfiguration.y_axis_display_grid,
		...buildYFormat(mainStyleConfiguration, formattingOptions),
	};
};
