// Constants
import {
	GLIDE_ACCESSIBILITY_PROPERTY,
	GLIDE_DATA_PATTERNS_PROPERTY,
} from '../constants';

/**
 * @param styleConfigurations - where to inject the preferences
 * @param prefs - user/system preferences
 */
export const injectPrefs = (styleConfigurations, prefs) => {
	if (!styleConfigurations || !styleConfigurations[0])
		return {};

	const styleConfigurationsWithPrefs = [
		{
			...styleConfigurations[0],
		},
		...styleConfigurations.slice(1),
	];

	const { locale, userPrefs } = prefs;

	// inject locale
	if (typeof locale === 'string')
		styleConfigurationsWithPrefs[0].locale = locale;

	// inject user preferences
	if (userPrefs) {
		// get the user property for accessibility
		const accessibilityPref = userPrefs.find((userProp) => {
			return userProp.name === GLIDE_ACCESSIBILITY_PROPERTY;
		});

		// inject accessibility
		if (accessibilityPref && typeof accessibilityPref.value === 'string')
			styleConfigurationsWithPrefs[0].accessibility = accessibilityPref.value === 'true' ? true : false;

		// get the user property for data patterns
		const dataPatternsPref = userPrefs.find((userProp) => {
			return userProp.name === GLIDE_DATA_PATTERNS_PROPERTY;
		});

		// inject data patterns
		if (dataPatternsPref && typeof dataPatternsPref.value === 'string')
			styleConfigurationsWithPrefs[0].dataPatterns = dataPatternsPref.value === 'true' ? true : false;
	}

	return styleConfigurationsWithPrefs;
};
