import getSeriesYKey from '../helpers/get-series-y-key';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
	} = params;
	return singleSeries.forecast.data.map((dataPoint) => {
		return {
			[getSeriesYKey(seriesIndex, datasetIndex, 'forecast')]: dataPoint.y,
			x: dataPoint.x,
		};
	});
};
