import { DECIMAL_PRECISION } from 'parser/xenolith/time-series/series/constants.time-series';
import isValidPrecision from './is-valid-precision';
import getStyleConfiguration from '../get-style-configuration';
import getDatasetSeries from '../get-dataset-series';

export default (chartConfiguration = {}, datasetIndex = 0, seriesIndex = 0) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const seriesConfiguration = getDatasetSeries(chartConfiguration, datasetIndex)[seriesIndex];

	if (seriesConfiguration && isValidPrecision(seriesConfiguration[DECIMAL_PRECISION]))
		return seriesConfiguration[DECIMAL_PRECISION];

	if (mainStyleConfiguration && isValidPrecision(mainStyleConfiguration[DECIMAL_PRECISION]))
		return mainStyleConfiguration[DECIMAL_PRECISION];

	return 0;
};
