import {
	TOOLTIP_ENABLED,
	SIMPLE_TOOLTIP,
	TOOLTIP_FONT,
	TOOLTIP_FONT_SIZE,
	TOOLTIP_BACKGROUND_COLOR,
} from './constants.tooltip';
import { i18nHelper } from 'par-components';
import timeSeriesTooltipFormatter from './time-series-formatter';
import singleScoreTooltipFormatter from './single-score-formatter';
import categoryTooltipFormatter from './category-formatter';
import getStyleConfiguration from '../../helpers/get-style-configuration';
import { TIME_SERIES, CATEGORY, RTL, FISCAL_YEAR_START } from '../../constants';
import { DECIMAL_PRECISION, UNIT, LOCALE } from '../series/time-series/constants.time-series';
import getFrequency from '../../helpers/get-frequency';
import { getTooltipText } from '../../helpers/get-tooltip-text.js';

export default (chartConfiguration, chartType = TIME_SERIES) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	let frequency = getFrequency(chartConfiguration);
	if (!frequency)
		frequency = 'daily';

	if (mainStyleConfiguration[TOOLTIP_ENABLED] === false)
		return {
			tooltip: {
				enabled: false,
			},
		};

	const simpleTooltip = mainStyleConfiguration[SIMPLE_TOOLTIP];

	const { t } = i18nHelper;
	return {
		tooltip: {
			useHTML: true,
			shared: true,
			split: false,
			followPointer: false,
			padding: simpleTooltip ? 4 : 8,
			borderWidth: 0,
			backgroundColor: mainStyleConfiguration[TOOLTIP_BACKGROUND_COLOR],
			style: {
				fontFamily: mainStyleConfiguration[TOOLTIP_FONT],
				fontSize: mainStyleConfiguration[TOOLTIP_FONT_SIZE],
				direction: mainStyleConfiguration[RTL] ? 'rtl' : 'ltr',
				textAlign: mainStyleConfiguration[RTL] ? 'right' : 'left',
			},
			formatter(e) {
				const i18Translations = {
					target: t('Target'),
					personalTarget: t('Personal Target'),
					distribution: t('Distribution'),
					year: t('Year'),
					change: t('Change'),
				};

				if (chartType === CATEGORY)
					return categoryTooltipFormatter(this.points, i18Translations, mainStyleConfiguration[DECIMAL_PRECISION], mainStyleConfiguration[UNIT], mainStyleConfiguration[LOCALE]);

				if (simpleTooltip)
					return singleScoreTooltipFormatter(this.x, this.points, e.chart.series, mainStyleConfiguration[DECIMAL_PRECISION], mainStyleConfiguration[LOCALE]);

				const tooltipText = getTooltipText(chartConfiguration, this.x);
				return timeSeriesTooltipFormatter(this.x, this.points, e.chart.series, frequency, i18Translations,
					mainStyleConfiguration.isYoy, mainStyleConfiguration[DECIMAL_PRECISION], mainStyleConfiguration[UNIT], mainStyleConfiguration[FISCAL_YEAR_START], mainStyleConfiguration[LOCALE], tooltipText);
			},
		},
	};
};
