import getSeriesYKey from '../helpers/get-series-y-key';
import getSeriesId from '../helpers/get-series-id';
import { i18nHelper } from 'par-components';

export default (params) => {
	const { t } = i18nHelper;
	const {
		datasetStyleConfig,
		color,
		seriesIndex,
		datasetIndex,
		yAxisName,
		singleSeries,
	} = params;
	const chartType = datasetStyleConfig.chart_type;
	let type;

	// bars should always be grouped, not stacked for forecast
	if (chartType === 'bar')
		type = ['groupedBar'];
	else
		type = [chartType];

	return {
		[getSeriesId(seriesIndex, datasetIndex, 'forecast')]: {
			y: getSeriesYKey(seriesIndex, datasetIndex, 'forecast'),
			type,
			color: singleSeries.forecast.color || color,
			strokeWidth: 2,
			x: 'x',
			axis: yAxisName,
			label: t('Forecast {0}', [singleSeries.name]),
		},
	};
};
