import isObjectEmpty from '../is-object-empty';
import { isColorArray,
	isNumber,
	isColor,
	COLOR_ARRAY,
	COLOR,
	NUMBER,
	BOOLEAN,
	STRING } from './types/index';
import { STYLE_CONFIGURATIONS, DATASET_SERIES, SERIES } from '../../constants';
import * as mandatoryConstants from './mandatory-contants';
import InvalidInputError from './invalid-input-error';

export default (input) => {
	const PARSING_FAILED = 'Parsing failed. ';

	// is an object
	if (typeof input !== 'object' || input === null)
		throw new InvalidInputError(PARSING_FAILED + 'The input is not an object.');

	// is not empty
	if (isObjectEmpty(input))
		throw new InvalidInputError(PARSING_FAILED + 'The input is an empty object.');

	// has keys style_configurations
	if (!input.hasOwnProperty(STYLE_CONFIGURATIONS))
		throw new InvalidInputError(PARSING_FAILED + 'The input is missing the top level property \'style_configurations\'.');

	// and dataset_series
	if (!input.hasOwnProperty(DATASET_SERIES))
		throw new InvalidInputError(PARSING_FAILED + 'The input is missing the top level property \'dataset_series\'.');

	// ********************
	// style_configurations
	// ********************

	// is a non-empty array
	if (!Array.isArray(input[STYLE_CONFIGURATIONS]) || input[STYLE_CONFIGURATIONS].length === 0)
		throw new InvalidInputError(PARSING_FAILED + '\'style_configurations\' must be a non-empty array.');

	// the first object has the mandatory keys with the correct type
	for (const key in mandatoryConstants)
		if (mandatoryConstants[key]) {
			if (!input[STYLE_CONFIGURATIONS][0].hasOwnProperty(mandatoryConstants[key].name))
				throw new InvalidInputError(PARSING_FAILED + 'Missing property: ' + mandatoryConstants[key].name + ' for the main dataset.');

			switch (mandatoryConstants[key].type) {
			case NUMBER:
				if (!isNumber(input[STYLE_CONFIGURATIONS][0][mandatoryConstants[key].name]))
					throw new InvalidInputError(PARSING_FAILED + '\'mandatoryConstants[key].name\' should be a number.');
				break;
			case COLOR_ARRAY:
				if (!isColorArray(input[STYLE_CONFIGURATIONS][0][mandatoryConstants[key].name]))
					throw new InvalidInputError(PARSING_FAILED + 'Invalid array of colors. It should be an array of hexa color codes');
				break;
			case COLOR:
				if (!isColor(input[STYLE_CONFIGURATIONS][0][mandatoryConstants[key].name]))
					throw new InvalidInputError(PARSING_FAILED + '\'mandatoryConstants[key].name\' should be a hexa color code.');
				break;
			case STRING:
				if (typeof input[STYLE_CONFIGURATIONS][0][mandatoryConstants[key].name] !== 'string')
					throw new InvalidInputError(PARSING_FAILED + '\'mandatoryConstants[key].name\' should be a string.');
				break;
			case BOOLEAN:
				if (typeof input[STYLE_CONFIGURATIONS][0][mandatoryConstants[key].name] !== 'boolean')
					throw new InvalidInputError(PARSING_FAILED + '\'mandatoryConstants[key].name\' should be a boolean.');
				break;
			}
		}

	// **************
	// dataset_series
	// **************

	// is a non-empty array
	if (!Array.isArray(input[DATASET_SERIES]) || input[DATASET_SERIES].length === 0)
		throw new InvalidInputError(PARSING_FAILED + '\'dataset_series\' must be a non-empty array.');

	// each object has 'series' key
	input[DATASET_SERIES].forEach((dataset, index) => {
		if (!dataset.hasOwnProperty(SERIES))
			throw new InvalidInputError(PARSING_FAILED + 'Element + ' + index.toString() + ' of \'dataset_series\' is missing the \'series\' property.');

		if (!Array.isArray(dataset[SERIES]))
			throw new InvalidInputError(PARSING_FAILED + '\'series\' must always be an array.');
	});

	return true;
};
