import { actionTypes } from '@seismic/core';
import adaptConfigType from './adapt-config-type';
import { isEqual, omit } from 'lodash';

import {
	RECORD_WATCHER_METADATA_REQUESTED,
	RECORD_WATCHER_SUBSCRIBED,
	SERIES_FETCH_DATACONFIG_REQUESTED,
	SERIES_FETCH_REQUESTED,
} from '../constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const handleComponentDisconnected = ({ action, dispatch, state, updateState }) => {
	unSubscribeRealTimeData({ action, dispatch, state, updateState });
};
// run on bootstrap or when component's property changes
const handlePropertyChanged = ({ action, dispatch, state, updateState }) => {
	const { dataConfigurations, sysId } = state.properties;

	if (dataConfigurations && dataConfigurations.length && !sysId) {
		const dataConfigsAdaptedType = adaptConfigType(dataConfigurations);
		const dataConfigurationsReqBody = { data_configurations: dataConfigsAdaptedType };

		dispatch(SERIES_FETCH_DATACONFIG_REQUESTED, { dataConfigurationsReqBody }, { dataConfigurationsReqBody });
	}

	handleRealTimePropertyChange({ action, dispatch, state, updateState });
};

const handleRealTimePropertyChange = ({ action: { payload: { name, value, previousValue } }, dispatch, state, updateState }) => {
	if (name !== 'isRealtime' || isEqual(value, previousValue))
		return;

	const {
		properties: { isRealtime, sysId }
	} = state;

	if (!sysId)
		return;

	if (`${isRealtime}` === 'true')
		dispatch(RECORD_WATCHER_METADATA_REQUESTED, { sysId }, { sysId });
	else
		unSubscribeRealTimeData({ dispatch, state, updateState });
};

const unSubscribeRealTimeData = ({ dispatch, state, updateState }) => {
	const { channelId, datasets, properties = {} } = state;
	const { sysId } = properties;

	if (channelId && Array.isArray(datasets) && datasets.length) {
		dispatch(RECORD_WATCHER_SUBSCRIBED, {
			channelId,
			subscribe: false,
		});

		datasets[0].score_details = omit(datasets[0].score_details, ['last_updated', 'is_realtime']);
		const lastSynched = new Date();
		updateState({
			...state,
			channelId: '',
			datasets,
			lastSynched
		});
	}
	// fetch the series again
	if (sysId)
		dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });
};

export default {
	[COMPONENT_BOOTSTRAPPED]: handlePropertyChanged,
	[COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
	[COMPONENT_DISCONNECTED]: handleComponentDisconnected,
};
