import buildTargetsData from './build-targets-data';
import buildTargetsConfig from './build-targets-config';

/**
* Builds the targets series
* @param {object} params - object that contains the following:
* datasetStyleConfig - styleConfiguration of the dataset that contains the current series
* singleSeries - the current series with its derived series (confidence band, target, threshold, etc...)
* color - color for this series, either in hexa or rgba
* seriesIndex - index of the current series in the dataset
* datasetIndex - index of the current dataset in chartConfiguration.datasets
*/
export default (params) => {
	const { singleSeries } = params;
	const targets = singleSeries.derived_series.targets;

	let globalTargets = {};
	if (targets.global && targets.global.data)
		globalTargets = {
			targetsConfig: buildTargetsConfig({
				...params,
				isGlobalTarget: true,
			}),
			targetsData: buildTargetsData({
				...params,
				isGlobalTarget: true,
			}),
		};

	let personalTargets = {};
	if (targets.personal && targets.personal.data)
		personalTargets = {
			targetsConfig: buildTargetsConfig({
				...params,
				isGlobalTarget: false,
			}),
			targetsData: buildTargetsData({
				...params,
				isGlobalTarget: false,
			}),
		};

	return {
		targetsData: [
			...(personalTargets.targetsData || []),
			...(globalTargets.targetsData || []),
		],
		targetsConfig: {
			...(personalTargets.targetsConfig || {}),
			...(globalTargets.targetsConfig || {}),
		},
	};
};
