import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import style from './hrCardSet.scss';

import {
	DEFAULT_CARD_SCROLL_DISTANCE,
	HR_CARD_SET_SCROLLED,
	HR_CARD_SET_VIEW_ALL_CLICKED
} from './hrCardSetConstants';

import {DEFAULT_CARD_WIDTH} from '../sn-hr-card/hrCardConstants';
import actionHandlers from './hrCardSetActionHandlers';
import view from './hrCardSetView';

createCustomElement('sn-hr-card-set', {
	initialState: {
		showScrollLeft: false,
		showScrollRight: false
	},

	properties: {
		/**
		 * The list of cards to display. Card objects should have the
		 * same properties as an sn-hr-card
		 *
		 * @type {Array[object]}
		 */
		cards: { default: [] },

		/**
		* The width of each card
		*
		* @type {number}
		*/
		cardWidth: { default: DEFAULT_CARD_WIDTH },

		/**
		 * The number of cards to scroll when clicking on the left/right arrows
		 *
		 * @type {number}
		 */
		cardScrollDistance: { default: DEFAULT_CARD_SCROLL_DISTANCE },

		/**
		 * The text to display when showViewAll is true. E.g. "View all HR cases"
		 *
		 * @type {String}
		 */
		viewAllText: { default:  '' },

		/**
		 * Whether to show the "view all" text
		 *
		 * @type {boolean}
		 */
		showViewAll: { default: false },

		/**
		 * The text to display when the list is empty
		 *
		 * @type {String}
		 */
		emptyText: { default: '' },

		/**
		 * The graphic to display when the list is empty
		 *
		 * @type {String}
		 */
		graphic: { default: '' },

		/**
		 * The background color to display when the list is empty
		 *
		 * @type {String}
		 */
		backgroundColor: { default: '' },

		/**
		 * The font color to use when the list is empty
		 *
		 * @type {String}
		 */
		fontColor: { default: '' },

		/**
		 * The color to fade the left/right scroll arrows into (as a hex string).
		 * If empty, no fade gradient will be shown
		 *
		 * @type {String}
		 */
		fadeColor: { default: '' }
	},

	behaviors: [{behavior: rtlBehavior}],
	actions: {
		/* Public */
		[HR_CARD_SET_VIEW_ALL_CLICKED]: {},

		/* Private */
		[HR_CARD_SET_SCROLLED]: { stopPropagation: true }
	},
	actionHandlers: actionHandlers,
	renderer: { type: snabbdom },
	styles: style,
	view
});
