import {t} from 'sn-translate';

import {status} from './constants';
import {isAbsoluteURL} from '../common/methods';

import 'sn-hr-shared';

const _createNoSlidesPlaceholder = (s, roundedCorners) => {
	if (s === status.READY)
		return null;

	let placeholder = null;

	switch (s) {
		case status.EMPTY:
			placeholder = (
				<sn-hr-empty-state type="empty" widget={t('announcement')} variant="auto" />
			);
			break;
		case status.ERROR:
			placeholder = <now-empty-state type={status.ERROR} variant="auto" />;
			break;
		case status.LOADING:
			placeholder = <now-empty-state widget={t('Announcements')} type={status.LOADING} variant="auto" />;
			break;
		default:
			placeholder = (
				<sn-hr-empty-state type="empty" widget={t('announcement')} variant="auto" />
			);
	}

	return (
		<div class={{placeholder: true, 'rounded-corners': roundedCorners}} slot="slides">
			{placeholder}
		</div>
	);
};

const view = state => {
	const {slides, status, height, roundedCorners, navButtonLabels} = state.properties;
	const placeholder = _createNoSlidesPlaceholder(status, roundedCorners);

	return (
		<sn-component-carousel
			slides={slides}
			navButtonLabels={navButtonLabels}
			height={parseInt(height)}
			handle-button-click={link => {
				if (isAbsoluteURL(link))
					window.open(link);
				else
					window.location.href = link;
			}}
			rounded-corners={roundedCorners}
		>
			{placeholder}
		</sn-component-carousel>
	);
};

export default view;
