import hasYAxis from '../../helpers/has-y-axis';
import buildSingleYAxis from './build-single-y-axis';
import getNumberOfDataset from '../../helpers/get-number-datasets';

export default (chartConfiguration = {}) => {
	const yAxis = [];
	for (let index = 0; index < getNumberOfDataset(chartConfiguration); index++)
		if (hasYAxis(chartConfiguration, index) || index === 0)
			yAxis.push(buildSingleYAxis(chartConfiguration, index));
	return {
		yAxis,
	};
};
