import { i18nHelper } from 'par-components';
import {
	SHOW_CHART_DATA_LABEL,
	DATALABELS_COLOR,
	DECIMAL_PRECISION,
	UNIT,
	FORECAST,
	paZindex,
	LOCALE,
} from '../constants.time-series';
import { getValueWithUnit } from 'helpers/value/index';

export default (datasetConfig, forecastSeries, color, datasetIndex = 0) => {
	const { t } = i18nHelper;

	return {
		dataLabels: {
			enabled: datasetConfig[SHOW_CHART_DATA_LABEL],
			style: {
				color: datasetConfig[DATALABELS_COLOR],
			},
			formatter() {
				return getValueWithUnit(
					this.point.y,
					{
						precision: datasetConfig[DECIMAL_PRECISION],
						unit: datasetConfig[UNIT],
						locale: datasetConfig[LOCALE],
					}
				);
			},
		},
		color,
		decimalPrecision: datasetConfig[DECIMAL_PRECISION],
		data: forecastSeries.data,
		visible: true,
		dashStyle: 'dot',
		type: 'line',
		enableMouseTracking: true,
		showInNavigator: true,
		getExtremesFromAll: true,
		datasetIndex,
		id: 'forecast',
		name: t('Forecast'),
		showInLegend: false,
		zIndex: paZindex[FORECAST],
		marker: {
			symbol: 'circle',
			radius: 4,
			enabled: false,
			states: {
				hover: {
					lineColor: '#FFFFFF',
					radius: 6,
					lineWidth: 2,
				},
				select: {
					lineColor: '#FFFFFF',
					radius: 6,
					lineWidth: 2,
				},
			},
		},
	};
};
