import getStyleConfiguration from '../../../helpers/get-style-configuration';
import { buildXFormat } from '../x-axis/build-x-format';
/**
* Builds the tooltipConfig
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API
*/

export default (chartConfiguration) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const formattingOptions = {
		frequency: mainStyleConfiguration.frequency,
		type: 'long',
		fiscalYearStart: mainStyleConfiguration.fiscal_year_start,
		locale: mainStyleConfiguration.locale,
	};

	// if a x tooltip formatter is specified apply it instead of the x axis formatter
	if (typeof mainStyleConfiguration.x_tooltip_format === 'function')
		return {
			tooltipConfig: {
				xFormat(timestamp) {
					return mainStyleConfiguration.x_tooltip_format(timestamp);
				},
			},
			showTooltip: true,
		};

	return {
		tooltipConfig: {
			...buildXFormat(mainStyleConfiguration, formattingOptions),
		},
		showTooltip: true,
	};
};
