/*eslint-disable */
import getStyleConfiguration from './get-style-configuration';
import { Y_AXIS_TITLE, Y_AXIS_TITLE_VISIBLE } from '../highcharts/y-axis/constants.y-axis';

export default (chartConfiguration = {}, index = 0) => {
	const currentStyleConfiguration = getStyleConfiguration(chartConfiguration, index);

	if (currentStyleConfiguration[Y_AXIS_TITLE] !== '') {
		return currentStyleConfiguration[Y_AXIS_TITLE];
	}
    
    	// For the main y-axis, if the y-axis title is empty => Set the y-axis title to the main indicator's unit name
	// If the main indicator's unit name is not in the Style Config, find it from the dataset
 	if (index === 0 && currentStyleConfiguration[Y_AXIS_TITLE_VISIBLE]) {
        	if (currentStyleConfiguration.unit && currentStyleConfiguration.unit.name) {
            		return currentStyleConfiguration.unit.name;
        	}
        	if (chartConfiguration.datasets && chartConfiguration.datasets[0] &&
                	chartConfiguration.datasets[0].series &&
                	chartConfiguration.datasets[0].series[0] &&
                	chartConfiguration.datasets[0].series[0].unit &&
 			chartConfiguration.datasets[0].series[0].unit.name) {
				return chartConfiguration.datasets[0].series[0].unit.name;
        	}
    	}
	return null;
};
