import buildTimeSeriesOptions from './highcharts/time-series/build-time-series-chart';
import buildXenolithTimeSeriesOptions from './xenolith/time-series/index';
import buildCategoryOptions from './highcharts/category/build-category-chart';
import validateInput from '../parser/helpers/input/validate-input';

export const buildTimeSeries = (chartConfiguration = {}, debugMode = false) => {
	if (debugMode)
		validateInput(chartConfiguration);

	return buildTimeSeriesOptions(chartConfiguration);
};

export const buildCategory = (chartConfiguration = {}, debugMode = false) => {
	if (debugMode)
		validateInput(chartConfiguration);

	return buildCategoryOptions(chartConfiguration);
};

export const buildXenolithTimeseries = (chartConfiguration = {}, debugMode = false) => {
	if (debugMode)
		validateInput(chartConfiguration);

	return buildXenolithTimeSeriesOptions(chartConfiguration);
};
