import React from 'react';
import PropTypes from 'prop-types';
import { buildTimeSeries } from '../../parser';
import buildXenolithTimeSeries from '../../parser/xenolith/time-series/index';
import Highcharts from '../highcharts/component.highcharts';
import Xenolith from '../xenolith/component.xenolith';
import { get } from 'lodash';

// Helpers
import shouldUseXenolith from './helpers/use-xenolith';
import getSelectedDate from './helpers/get-selected-date';
import getChartType from 'helpers/chart-type/get-chart-type';

// Style
import './component.time-series.scss';

// WCAG
import { visualPatternsEnabled, accessibilityEnabled } from 'helpers/wcag';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		datasets: PropTypes.array,
		styleConfigurations: PropTypes.array,
		clickBehaviors: PropTypes.array,
		onSetExtremes: PropTypes.func,
		selectedDate: PropTypes.number,
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'TimeSeries';

	getChartOptions(useXenolith = false) {
		const selectedDate = getSelectedDate(this.props);
		/*
			maps chart type that comes from the props or the server to
			Highcharts chart type to Xenolith chart types
			without mutating the props
		*/
		const styleConfigurations = this.props.styleConfigurations.map((styleConfiguration) => {
			return {
				...styleConfiguration,
				chart_type: getChartType(styleConfiguration.chart_type, this.props.chartLibrary),
			};
		});

		if (!this.props.datasets ||
			!this.props.styleConfigurations ||
			this.props.styleConfigurations.length === 0)
			return {};

		const datasets = Object.assign([], this.props.datasets);

		// add the selected date as a x-axis-plotline input
		if (selectedDate &&
			datasets[0] &&
			datasets[0].series &&
			datasets[0].series[0])
			datasets[0].series[0] = {
				...datasets[0].series[0],
				derived_series: {
					...datasets[0].series[0].derived_series,
					x_axis_plotlines: [selectedDate],
				},
			};
		// use Xenolith
		if (useXenolith)
			return {
				...buildXenolithTimeSeries({
					style_configurations: styleConfigurations,
					datasets: datasets,
					accessibility: accessibilityEnabled(),
					dataPatterns: visualPatternsEnabled(),
					clickBehaviors: this.props.clickBehaviors,
				}),
			};

		// use Highcharts
		return {
			...buildTimeSeries({
				style_configurations: styleConfigurations,
				datasets: datasets,
			}),
		};
	}

	/**
	 * Merge all types of labels to get single label list for business calendars
	 */
	getAllBusinessCalendarLabels(scoreLables, futureTargetLabels, forecastLabels) {
		const labels = scoreLables;
		if (Array.isArray(scoreLables) && Array.isArray(futureTargetLabels))
			for (let i = 0; i < futureTargetLabels.length; i++) {
				const obj = {};
				obj.label_name = futureTargetLabels[i].labelName;
				obj.date = futureTargetLabels[i].startDate;
				labels.push(obj);
			}
		if (Array.isArray(scoreLables) && Array.isArray(forecastLabels))
			for (let i = 0; i < forecastLabels.length; i++) {
				const obj = {};
				obj.label_name = forecastLabels[i].label_name;
				obj.date = forecastLabels[i].start_date;
				labels.push(obj);
			}
		return labels;
	}

	/**
	 * Render the react component
	 *
	 * @return {React.Component}
	 */
	render() {
		const useXenolith = shouldUseXenolith(this.props);
		const options = this.getChartOptions(useXenolith);
		const scoreLabels = get(this.props, 'datasets[0].series[0].labels');
		const futureTargetLabels = get(this.props, 'datasets[0].series[0].derived_series.targets.global.biz_calendar_post_max_date_spans');
		const forecastLabels = get(this.props, 'datasets[0].series[0].forecast.labels');
		const businessCalendarLabels = this.getAllBusinessCalendarLabels(scoreLabels, futureTargetLabels, forecastLabels);
		if (useXenolith)
			return (
				<div className="par-time-series">
					<Xenolith
						options={options}
						chartConfiguration={{
							style_configurations: this.props.styleConfigurations,
							datasets: this.props.datasets,
						}}
					/>
				</div>
			);

		return (
			<div className="par-time-series">
				<Highcharts
					options={options}
					clickBehaviors={this.props.clickBehaviors}
					onSetExtremes={this.props.onSetExtremes}
					businessCalendarLabels={businessCalendarLabels}
				/>
			</div>
		);
	}
}
