import { getTrendBySeries } from './get-trend-by-series';
import { xAxisTrendFormatter } from './formatters/x-axis-formatters';
import { dateHelper } from 'par-components';
/**
* Builds the styleConfigurations from the reportingTrendSeries object
* @param {object} reportTrendConfiguration - object that comes from the Trend Reporting API
* @returns {array} styleConfigurations
*/
export const buildReportTrendStyleConfigurations = (reportTrendConfiguration = {}) => {
	const trendBySeries = getTrendBySeries(reportTrendConfiguration);

	// 5 represents half the distance between two timestamps
	const X_OFFSET = 5;

	// category names
	const xLabels = trendBySeries[0].xvalues;

	// reporting trend by styling configurations
	const reportTrendStyles = reportTrendConfiguration.style_configurations[0];

	// display as many ticks as there are categories
	const numberOfTicks = xLabels.length;

	// for aesthetic reason adds an offset at the beggining and the end of the data
	const xMax = ((xLabels.length - 1) * 10) + X_OFFSET;

	// display legend only multiple datasets and/or the first series is grouped by
	const showLegend = trendBySeries.length > 1 || typeof trendBySeries[0].x2values !== 'undefined';

	const mainStyleConfiguration = {
		chart_type: reportTrendStyles.chart_type,
		name: reportTrendStyles.chart_title,
		show_chart_data_label: reportTrendStyles.show_chart_data_label,
		show_marker: reportTrendStyles.show_marker,
		decimal_precision: reportTrendStyles.decimal_precision,
		x_axis_title: reportTrendStyles.x_axis_title,
		x_axis_opposite: reportTrendStyles.x_axis_opposite,
		y_axis_title: reportTrendStyles.y_axis_title,
		y_axis_opposite: reportTrendStyles.y_axis_opposite,
		y_axis_min: reportTrendStyles.y_axis_min,
		y_axis_max: reportTrendStyles.y_axis_max,
		x_min: -X_OFFSET,
		x_max: xMax,
		x_format: xAxisTrendFormatter(xLabels),
		x_number_of_ticks: numberOfTicks,
		colors: trendBySeries[0].colors,
		show_gaps: false,
		show_legend: showLegend,
	};

	if (trendBySeries[0].yaxis_duration)
		mainStyleConfiguration.y_format = (seconds) => { return dateHelper.getDurationValue(seconds * 1000); };

	return trendBySeries.map((singleTrendBySeries, index) => {
		if (index === 0)
			return {
				...mainStyleConfiguration,
				colors: singleTrendBySeries.colors,
			};

		return {
			...reportTrendConfiguration.style_configurations[index],
			colors: singleTrendBySeries.colors,
		};
	});
};
