/* eslint-disable react/no-strings-ref */
import React from 'react';
import PropTypes from 'prop-types';
import { Timeseries } from 'xenolith/dist/umd';

// Style
import './component.xenolith.scss';

// Helpers
import getChartSize from './helpers/get-chart-size';

// Data table
import DataTable from '../data-table/component.data-table';

import { getAccessibilityFlag } from 'parser/helpers/get-accessibility-flag';

export default class extends React.Component {
	static propTypes = {
		options: PropTypes.object,
	};

	static defaultProps = {
		options: {},
	};

	static displayName = 'Xenolith';

	constructor(...args) {
		super(...args);
		// To move to the state ?
		this.chart = null;
	}

	componentDidMount() {
		this.initializeXenolithChart();
	}

	componentDidUpdate() {
		this.updateXenolithChart(this.props.options);
	}

	initializeXenolithChart() {
		this.chart = new Timeseries({
			node: this.refChart,
			...this.props.options,
		});
	}

	updateXenolithChart(nextOptions) {
		for (const property in nextOptions)
			if (Object.prototype.hasOwnProperty.call(nextOptions, property))
				this.chart[property] = nextOptions[property];
	}

	render() {
		// CSS width and height for the white background container
		const { width, height } = getChartSize(this.props.options);
		const style = {
			width,
			height,
		};

		return (
			<div className="par-xenolith-container" style={style}>
				<div
					style={style}
					className="par-xenolith"
					id="container"
					ref={(node) => { this.refChart = node; }}
				/>
				{getAccessibilityFlag(this.props.chartConfiguration) &&
					<DataTable
						chartConfiguration={this.props.chartConfiguration}
					/>
				}
			</div>
		);
	}
}
