import React from 'react';
import PropTypes from 'prop-types';

// Style
import './component.not-supported.scss';

// Helpers
import { i18nHelper } from 'par-components';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		width: PropTypes.number,
		height: PropTypes.number,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {};

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const { t } = i18nHelper;

		// Create the style
		const { width, height } = this.props;
		const style = {};
		if (width)
			style.width = width;

		if (height)
			style.height = height;

		return (
			<div className="par-not-supported" style={style}>
				<span>{t('This visualization is not supported.')}</span>
			</div>
		);
	}
}
