import React from 'react';

/**
 * Creates a HOC for the sake of getting the HTML direction
 *
 * @return {function}
 */
export const withDirection = () => Component => class extends React.Component {
	/**
	 * The name of the component (safe after minimization)
	 */
	static displayName = Component.displayName ? `WithDirection(${Component.displayName})` : 'WithDirection';

	/**
	 * If the component does not have the element yet, then rerender
	 */
	componentDidMount() {
		// Rerender to get the element
		this.forceUpdate();
	}

	/**
	 * Get the direction of the component (from the composed style)
	 *
	 * @return {string}
	 */
	getDirection() {
		if (!this.el && !this.direction)
			return false;

		if (this.el) {
			const style = window.getComputedStyle(this.el);
			this.direction = style.direction || 'ltr';
		}

		return this.direction;
	}

	/**
	 * Render the react component
	 *
	 * @return {React.Component}
	 */
	render() {
		const direction = this.getDirection();

		return (direction
			? <Component
				dir={direction}
				{...this.props}
			/>
			: <div ref={(el) => { this.el = el; }} />
		);
	}
};
