import buildSingleYAxis from '../../../y-axis/build-single-y-axis';
import { STYLE_CONFIGURATIONS } from '../../../../constants';

export default (datasetSeriesConfig) => {
	const mainYAxis = buildSingleYAxis({
		[STYLE_CONFIGURATIONS]: [datasetSeriesConfig],
	});
	return {
		...mainYAxis,
		opposite: !mainYAxis.opposite,
		title: {
			text: null,
		},
		labels: {
			...mainYAxis.labels,
			align: mainYAxis.opposite ? 'right' : 'left',
			formatter: function yAxisLabelFormatter() {
				return this.value.toString() + '%';
			},
		},
		id: 'change_percent_y_axis',
	};
};
