/**
 * finds the nearest target value associated to a point on the scores series
 */
export default (xValue, targetPoints, reviewDateInfo = false) => {
	let first = 0;
	let last = targetPoints.length - 1;
	// highcharts 'undefines' the points that are outside the x extremes
	const definedTargetPoints = targetPoints.filter((targetPoint) => { return targetPoint; });
	if (xValue < definedTargetPoints[0].x)
		return null;

	while (first <= last) {
		const mid = Math.floor((first + last) / 2);
		const currentTargetPoint = definedTargetPoints[mid];
		if (currentTargetPoint.x < xValue)
			first++;

		else if (currentTargetPoint.x > xValue)
			last--;

		else
			return reviewDateInfo ? { value: currentTargetPoint.y, hasReviewDate: !!currentTargetPoint.options.review_date, target: currentTargetPoint.options } : currentTargetPoint.y;
	}
	return reviewDateInfo ? { value: definedTargetPoints[first - 1].y, hasReviewDate: !!definedTargetPoints[first - 1].options.review_date, target: definedTargetPoints[first - 1].options } : definedTargetPoints[first - 1].y;
};
