import {
	TARGET_NAME,
} from '../series/time-series/constants.time-series';
import findNearestTargetValue from './find-nearest-target-value';
import { getNumberValue } from 'helpers/value/index';

const formatTooltipLine = (yValue, color) => {
	return '<div>' +
	'<span style="' +
	'height: 8px;' +
	'width: 8px;' +
	'background-color: ' + color + ';' +
	'border-radius: 50%;' +
	'display: inline-block;' +
	'vertical-align: middle;' +
	'margin-right: 3px' +
	'"></span>' +
	'<span>' + yValue + '</span>' +
	'</div>';
};

export default (xValue, points, series, decimalPrecision = 0, locale) => {
	const header = '<div style="line-height: 1.2; font-size: 12px;">';
	const label = [header];
	const footer = ['</div>'];

	// if there is a target/personal target series,
	// find the closest target/personal value for the xValue
	const targetSeries = series.filter((singleSeries) => {
		return singleSeries.name === TARGET_NAME;
	});
	let closestTarget;
	if (targetSeries.length > 0 && targetSeries[0].data)
		closestTarget = findNearestTargetValue(xValue, targetSeries[0].data);

	points.forEach((currentPoint) => {
		const yValue = getNumberValue(currentPoint.y, { precision: decimalPrecision, locale });
		label.push(formatTooltipLine(yValue, currentPoint.color));
		if (closestTarget) {
			const closestTargetWithPrecision = getNumberValue(closestTarget, { precision: decimalPrecision, locale });
			label.push(formatTooltipLine(closestTargetWithPrecision, targetSeries[0].color));
		}

		label.push(footer.join(''));
	});
	return label.join('');
};
