/* eslint-disable  camelcase, no-restricted-globals */
import { getYAxisName } from '../y-axis/get-y-axis-name';
import hasYAxis from 'parser/helpers/has-y-axis';
import hasOneChangePercentSeries from 'parser/helpers/has-one-change-percent-series';
import getStyleConfiguration from 'parser/helpers/get-style-configuration';

/**
* Returns the extents options - y extremes
* @param {object} chartConfiguration - chart configuration in the format of the Reporting API
*/
export default (chartConfiguration) => {
	const { x_min, x_max } = getStyleConfiguration(chartConfiguration, 0);
	const extents = {
		x: [(x_min || 'dynamic'), (x_max || 'dynamic')],
	};

	// To determine how many axis we have, just put their names in a set and check the size in the end
	const yAxisSet = new Set();

	chartConfiguration.style_configurations.forEach((styleConfiguration, datasetIndex) => {
		if (hasYAxis(chartConfiguration, datasetIndex)) {
			const currentYAxisName = getYAxisName(datasetIndex, styleConfiguration);

			// set the default extents
			extents[currentYAxisName] = ['dynamic', 'dynamic'];

			yAxisSet.add(currentYAxisName);

			// retrieve the extremes from the style configurations
			const { y_axis_min, y_axis_max } = styleConfiguration;

			// can apply only y_axis_min or y_axis_max or both
			if (typeof y_axis_min === 'number' && !isNaN(y_axis_min))
				extents[currentYAxisName][0] = y_axis_min;

			if (typeof y_axis_max === 'number' && !isNaN(y_axis_max))
				extents[currentYAxisName][1] = y_axis_max;
		}
	});

	/*
		the change percent axis is different from the other y axes
		that come from the datasets: there's always one change_percent_y_axis
		for all the change_percent series and it is formatted with %
	*/
	if (hasOneChangePercentSeries(chartConfiguration)) {
		extents.change_percent_y_axis = ['dynamic', 'dynamic'];
		yAxisSet.add('change_percent_y_axis');
	}

	// if only one yAxis, don't use yAxis name, use 'y'
	if (yAxisSet.size === 1) {
		extents.y = extents[getYAxisName(0)];
		delete extents[getYAxisName(0)];
	}

	return { extents };
};

