import isArrayOfArrays from './is-array-of-arrays';
/**
 * Merges two or more arrays of objects into a single array by merging objects with the same key
 *
 * Example
 * Inputs: 'x' and [[x: 1, y1: 2], [x: 1, y2: 3], [x: 2, y1: 5], [x:2, y2: 10]]
 * Outputs: [{x: 1, y1: 2, y2: 3}, {x: 2, y1: 5, y2: 10}]
 *
 * @param {string} [key]
 * @param {...[]} [Array] - array of arrays
 * @returns {Array} - return the result of the merged arrays into an array
 */

export default (key, arrayOfArrays) => {
	// validate the input
	if (typeof key !== 'string' ||
		key.length === 0 ||
		!isArrayOfArrays(arrayOfArrays))
		return arrayOfArrays;

	// if only one array in arrays
	if (arrayOfArrays.length === 1)
		return arrayOfArrays[0];

	// optimization: use for loop instead forEach
	// valueMap keeps track of objects that share the same key
	const valueMap = {};
	arrayOfArrays.forEach((innerArray) => {
		innerArray.forEach((element) => {
			// element is { x: 1, y1: 1, y2: 10 } and  key is 'x'
			const currentValue = element[key];
			if (!valueMap[currentValue])
				valueMap[currentValue] = {};

			Object.assign(valueMap[currentValue], element);
		});
	});

	// valueMap should look like this where the keys are x
	// { '1': { x: 1, y1: 1, y2: 10 }, '2': { x: 2, y1: 2, y2: 20 } }

	return Object.values(valueMap);
};

