import {t} from 'sn-translate';

const YEAR_SECONDS = 31556952;
const MONTH_SECONDS = 2592000;
const WEEK_SECONDS = 604800;
const DAY_SECONDS = 86400;
const HOUR_SECONDS = 3600;
const MINUTE_SECONDS = 60;

const padValue = value => `0${value}`.slice(-2);

const getDurationUnit = (duration, secondsCount) => ({
	value: Math.floor(duration / secondsCount),
	remainder: duration % secondsCount
});

export const formatDuration = duration => {
	const years = getDurationUnit(duration, YEAR_SECONDS);
	const months = getDurationUnit(years.remainder, MONTH_SECONDS);
	if (years.value)
		return t('{0}yr {1}mo', years.value, months.value);

	const weeks = getDurationUnit(months.remainder, WEEK_SECONDS);
	if (months.value)
		return t('{0}mo {1}wk', months.value, weeks.value);

	const days = getDurationUnit(weeks.remainder, DAY_SECONDS);
	if (weeks.value)
		return t('{0}wk {1}d', weeks.value, days.value);

	const hours = getDurationUnit(days.remainder, HOUR_SECONDS);
	if (days.value)
		return t('{0}d {1}hr', days.value, hours.value);

	let minutes = getDurationUnit(hours.remainder, MINUTE_SECONDS);
	if (hours.value)
		return t('{0}hr {1}min', hours.value, minutes.value);

	const seconds = padValue(minutes.remainder);
	minutes = padValue(minutes.value);

	return `${minutes}:${seconds}`;
};

export const formatBreachedDuration = duration => {
	const years = getDurationUnit(duration, YEAR_SECONDS);
	const months = getDurationUnit(years.remainder, MONTH_SECONDS);
	if (years.value)
		return years.value === 1 ? t('1 year breached') : t('{0} years breached', years.value);

	const weeks = getDurationUnit(months.remainder, WEEK_SECONDS);
	if (months.value)
		return months.value === 1 ? t('1 month breached') : t('{0} months breached', months.value);

	const days = getDurationUnit(weeks.remainder, DAY_SECONDS);
	if (weeks.value)
		return weeks.value === 1 ? t('1 week breached') : t('{0} weeks breached', weeks.value);

	const hours = getDurationUnit(days.remainder, HOUR_SECONDS);
	if (days.value)
		return days.value === 1 ? t('1 day breached') : t('{0} days breached', days.value);

	let minutes = getDurationUnit(hours.remainder, MINUTE_SECONDS);
	if (hours.value)
		return hours.value === 1 ? t('1 hour breached') : t('{0} hours breached', hours.value);

	if (minutes.value === 1)
		return t('1 minute breached');
	else if (minutes.value > 1)
		return t('{0} minutes breached', minutes.value);

	return t('Just breached');
};

export const formatRemainingDuration = duration => {
	const years = getDurationUnit(duration, YEAR_SECONDS);
	const months = getDurationUnit(years.remainder, MONTH_SECONDS);
	if (years.value)
		return years.value === 1 ? t('1 year remaining') : t('{0} years remaining', years.value);

	const weeks = getDurationUnit(months.remainder, WEEK_SECONDS);
	if (months.value)
		return months.value === 1 ? t('1 month remaining') : t('{0} months remaining', months.value);

	const days = getDurationUnit(weeks.remainder, DAY_SECONDS);
	if (weeks.value)
		return weeks.value === 1 ? t('1 week remaining') : t('{0} weeks remaining', weeks.value);

	const hours = getDurationUnit(days.remainder, HOUR_SECONDS);
	if (days.value)
		return days.value === 1 ? t('1 day remaining') : t('{0} days remaining', days.value);

	let minutes = getDurationUnit(hours.remainder, MINUTE_SECONDS);
	if (hours.value)
		return hours.value === 1 ? t('1 hour remaining') : t('{0} hours remaining', hours.value);

	if (minutes.value === 1)
		return t('1 minute remaining');
	else if (minutes.value > 1)
		return t('{0} minutes remaining', minutes.value);

	return t('Seconds remaining');
};
