import buildSingleTimeSeries from './build-single-time-series';
import hasYAxis from '../../../helpers/has-y-axis';
import getDatasetSeries from '../../../helpers/get-dataset-series';
import getStyleConfiguration from '../../../helpers/get-style-configuration';
import { getYAxisName } from '../y-axis/get-y-axis-name';

/**
* Builds all the series (and derived series) for one dataset
* @param {Object} chartConfiguration - chart configuration in the format of the Reporting API
* @returns {Array} returns an object like
* [ <-- for each dataset
*  { <-- for each series
*   seriesData: {},
*   seriesConfig: {},
*   forecastData: {},
*   forecastConfig: {},
*   ...
*  }
* ]
*/

export default (chartConfiguration, datasetIndex = 0) => {
	const datasetStyleConfig = getStyleConfiguration(chartConfiguration, datasetIndex);
	const datasetSeries = getDatasetSeries(chartConfiguration, datasetIndex);
	const datasetColors = datasetStyleConfig.colors;
	let yAxisName = getYAxisName(0, datasetStyleConfig); // main yAxis

	if (datasetIndex > 0 && hasYAxis(chartConfiguration, datasetIndex))
		yAxisName = getYAxisName(datasetIndex, datasetStyleConfig); // secondary yAxis

	const datasetSeriesConfigsAndData = [];
	datasetSeries.forEach((singleSeries, seriesIndex) => {
		const color = datasetColors[seriesIndex % datasetColors.length];

		const seriesConfigAndData = buildSingleTimeSeries({
			datasetStyleConfig,
			singleSeries,
			color,
			seriesIndex,
			datasetIndex,
			yAxisName,
		});

		datasetSeriesConfigsAndData.push(seriesConfigAndData);
	});

	return datasetSeriesConfigsAndData;
};
