import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import {DEFAULT_CARD_WIDTH, HR_CARD_CLICKED} from './hrCardConstants';
import style from './hrCard.scss';
import '../sn-hr-highlighted-value/now-highlighted-value';
import '../sn-hr-sla-timer';

const renderTags = tags => (
	tags.map(tag => {
		if (tag.type === 'condition')
			return ( <sn-hr-highlighted-value {...tag} /> );
		if (tag.type === 'sla')
			return ( <sn-hr-sla-timer data={tag.data} show-icon={true} /> );
	})
);

const view = (state, { dispatch }) => {
	const {
		width,
		cardTitle,
		topLabelValue,
		bottomLabelValues,
		tags
	} = state.properties;

	const onClick = () => {
		dispatch(HR_CARD_CLICKED, {});
	};

	return (
		<a href={'javascript:void(0)'} className="card" style={{width: `${width}px`}} onclick={onClick}>
			<div className="top">
				<div>
					<div className="labelValue" >
						<div className="label">{topLabelValue.label}</div>
						<div className="value">{topLabelValue.value}</div>
					</div>
				</div>
				<div>
					{ renderTags(tags) }
				</div>
			</div>
			<div className="middle">
				{cardTitle}
			</div>
			<div className="bottom">
				{ bottomLabelValues.map(({label, value}) => (
					<div className="labelValue">
						<div className="label">{label}</div>
						<div className="value">{value}</div>
					</div>
				)) }
			</div>
		</a>
	)
};

/**
 * A container for specific information in a simple, easily scannable format.
 * Provides a border, card-level interactivity, and a default slot for holding
 * card subcomponents.
 *
 * ```jsx
 * <sn-hr-card
 * 		cardTitle="title",
 * 		topLabelValue="topLabelValue"
 * 		bottomLabelValues={[]}
 * 		width={width}
 * />
 * ```
 *
 * @summary A container for specific information in a simple, easily scannable format.
 */
createCustomElement('sn-hr-card', {
	properties: {
		/**
		 * The main text to display in the vertical middle of the card
		 *
		 * @type {string}
		 */
		cardTitle: { default: '' },

		/**
		 * The label and value to display in the upper left corner.
		 * Format as { label: 'Desired label', value: 'Desired value' }
		 *
		 * @type {object}
		 */
		topLabelValue: { default: {} },

		/**
		 * The labels and values to display at the bottom of the card
		 *
		 * Format as [{ label: 'Desired label', value: 'Desired value' }, ...]
		 *
		 * @type {Array[object]}
		 */
		bottomLabelValues: { default: [] },

		/**
		 * The tags to display in the upper right hand corner of the card
		 *
		 * Format with same properties as now-highligted-value
		 * Example: [{ label: 'Tag', color: 'High', variant: 'tertiary' }]
		 *
		 * @type {Array[object]}
		 */
		tags: { default: [] },

		/**
		 * The width of the card
		 *
		 * @type {number}
		 */
		width: { default: DEFAULT_CARD_WIDTH }
	},

	actions: {
		[HR_CARD_CLICKED]: {}
	},
	behaviors: [{behavior: rtlBehavior}],
	styles: style,
	renderer: { type: snabbdom },
	view
});
