import isHexaColorCode from './is-hexa-color-code';

// A color array is an non-empty array of strings which are hex color codes like '#000000'
export default (value) => {
	if (!Array.isArray(value) || value.length === 0)
		return false;

	for (let i = 0; i < value.length; i++)
		if (typeof value[i] !== 'string' || !isHexaColorCode(value[i]))
			return false;

	return true;
};
