import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-heading';
import '../sn-component-campaigns-announcements';
import '../sn-link-list';
import '../sn-hr-image-links';
import styles from './style.scss';

const view = state => {
    const {
        announcementsHeight,
        announcementsContentId,
        announcementsHeader,
        linksContentId,
        linksHeader,
        imagesContentId,
        imagesHeader,
        roundedCorners
    } = state.properties;

    return (
        <div className="container">
            <div className="child">
                <now-heading label={announcementsHeader} variant="header-secondary" level="2"/>
                <sn-component-campaigns-announcements
                    content-identifier={announcementsContentId}
                    height={announcementsHeight}
                    rounded-corners={roundedCorners}
                />
            </div>
            <div className="child">
                <now-heading label={linksHeader} variant="header-secondary" level="2"/>
                <sn-link-List
                    min-height={announcementsHeight}
                    content-identifier={linksContentId}
                    rounded-corners={roundedCorners}
                />
            </div>
            <div className="child">
                <now-heading label={imagesHeader} variant="header-secondary" level="2"/>
                <sn-hr-image-links
                    min-height={announcementsHeight}
                    content-identifier={imagesContentId}
                    rounded-corners={roundedCorners}
                />
            </div>
        </div>
    );
};

const TAG_NAME = 'sn-hr-announcements-links-images';
createCustomElement(TAG_NAME, {
    renderer: {
        type: snabbdom,
        view
    },
    properties: {
        announcementsHeight: {
            default: '416',
        },
        announcementsContentId: {
            default: ''
        },
        announcementsHeader: {
            default: ''
        },
        linksContentId: {
            default: ''
        },
        linksHeader: {
            default: ''
        },
        imagesContentId: {
            default: ''
        },
        imagesHeader: {
            default: ''
        },
        roundedCorners: {
            default: false
        }
    },
    styles
});

export default TAG_NAME;
