/**
* Builds the x formatter that adapts the trend-by reporting charts
* into timeseries by displaying the string categories on the x Axis
* @param {array} xLabels - array of string categories
* Expects the timestampts of each series to be 0, 10, 20, etc...
* @returns {function} function that maps the timestamps to each xLabels
* example: for xLabels: ["(empty)", "2015", "2016", "2018", "2019"]
* the function return the following mapping:
* 0 -> "(empty)" index 0
* 10 -> "2015" index 1
* 20 -> "2016" index 2
* 30 -> "2018" index 3
* 40 -> "2019" index 4
* ...
*/
export const xAxisTrendFormatter = (xLabels = []) => {
	return (timestamp) => {
		if (xLabels[timestamp / 10])
			return xLabels[timestamp / 10];

		return null;
	};
};
