import buildXAxis from './x-axis/';
import buildYAxis from './y-axis/';
import buildSeries from './series/';
import buildRegister from './register/';
import buildAccessibility from './accessibility/';
import buildExtents from './extents/';
import buildDatalabels from './datalabels/';
import buildChartSize from './chart-size/';
import buildTooltip from './tooltip/';
import buildActions from './actions/';

/**
* Returns the Emotive Xenolith options to render a timeseries chart
* @param {object} chartConfiguration - chart configuration in the format of the Reporting API
*/
export default (chartConfiguration = {}) => {
	return {
		...buildXAxis(chartConfiguration),
		...buildYAxis(chartConfiguration),
		...buildSeries(chartConfiguration),
		...buildRegister(chartConfiguration),
		...buildAccessibility(chartConfiguration),
		...buildExtents(chartConfiguration),
		...buildDatalabels(chartConfiguration),
		...buildChartSize(chartConfiguration),
		...buildTooltip(chartConfiguration),
		...buildActions(chartConfiguration),
	};
};
