/**
 * Returns true if the chart is a reporting trend-by chart
 * @param {object} chartConfiguration
 */

export const isTrendByReportChart = (chartConfiguration) => {
	if (!Array.isArray(chartConfiguration.data_configurations) ||
		chartConfiguration.data_configurations.length === 0)
		return false;

	const trendByReportingChartTypes = ['line', 'spline', 'area', 'step_line', 'trend', 'tbox', 'line_bar'];
	const isMetricbaseReport = chartConfiguration.data_configurations[0].source_type !== 'metricbase';
	const isReportingTimeseries = trendByReportingChartTypes.indexOf(chartConfiguration.data_configurations[0].type) >= 0;

	return (isMetricbaseReport && isReportingTimeseries);
};
