/**
 * Return the accessibility flag from the chartConfiguration
 * This is used mostly for Workspace
 */
export const getAccessibilityFlag = (chartConfiguration) => {
	if (chartConfiguration &&
		chartConfiguration.style_configurations &&
		chartConfiguration.style_configurations[0] &&
		chartConfiguration.style_configurations[0].accessibility)
		return chartConfiguration.style_configurations[0].accessibility;

	return false;
};
