import PropTypes from 'prop-types';

/**
 * The shape of this prop type is defined by the requirements for the workspace
 * design. Check STRY50140497 for more details on which states look different
 * for workspace.
 *
 * This is used in components where we want the certain state to look different.
 * For example, in the seismic sn-component-visualization component, we will be
 * using a different component for all of these states, so that the design
 * resembles the design of workspace.
 *
 * @type {Object}
 */
export default {
	/**
	 * Defines the type
	 *
	 * @type {PropTypes}
	 */
	type: PropTypes.shape({
		empty: PropTypes.node,
		unsupported: PropTypes.node,
		needsPermission: PropTypes.node,
		error: PropTypes.node,
		loading: PropTypes.node,
		realtimeWarning: PropTypes.node,
	}),

	/**
	 * The default value
	 *
	 * @type {Object}
	 */
	default: {},
};
