import { ELEMENTS, PARETO, CATEGORY, PARETO_LINE_COLOR, CHART_TYPE } from './constants.category';
import { i18nHelper } from 'par-components';
import buildXAxis from '../../x-axis/index';
import getPareto from './helpers/get-pareto';
import { STYLE_CONFIGURATIONS } from '../../../constants';
import buildSingleYAxis from '../../y-axis/build-single-y-axis';
import isObjectEmpty from '../../../helpers/is-object-empty';
import { Y_AXIS_OPPOSITE } from '../../y-axis/constants.y-axis';

export default (datasetStyleConfig, singleSeries, color, seriesIndex = 0) => {
	const series = [];
	const chartType = datasetStyleConfig[CHART_TYPE];
	const { t } = i18nHelper;
	// build the xAxis and the yAxis only once for the first series
	let xAxis;
	let yAxis = {};
	if (seriesIndex === 0) {
		xAxis = buildXAxis(
			{
				[STYLE_CONFIGURATIONS]: [datasetStyleConfig],
			},
			false,
			CATEGORY
		).xAxis;
		xAxis[0].categories = [];
	}

	// build the categorized series from the elements
	const categorizedSeries = singleSeries[ELEMENTS].map((element) => {
		if (seriesIndex === 0 && xAxis[0].categories.indexOf(element.name) < 0)
			xAxis[0].categories.push(element.name);

		const scoreField = singleSeries.scoreField || 'score';

		return {
			name: element.name,
			y: element[scoreField],
		};
	});

	const seriesName = singleSeries.name;

	if (singleSeries.visible)
		series.push({
			type: 'column',
			data: categorizedSeries,
			name: singleSeries.name,
			seriesName,
			color,
		});

	if (chartType === PARETO) {
		// include percentage on left for pareto chart
		yAxis = buildSingleYAxis({
			[STYLE_CONFIGURATIONS]: [datasetStyleConfig],
		});
		yAxis = {
			...yAxis,
			labels: {
				...yAxis.labels,
				formatter() {
					return this.value + ' %';
				},
				align: datasetStyleConfig[Y_AXIS_OPPOSITE] ? 'right' : 'left',
			},
			opposite: !datasetStyleConfig[Y_AXIS_OPPOSITE],
			title: {
				text: null,
			},
			min: 0,
			max: 100,
			index: 1,
		};
		// add pareto series
		series.push({
			type: 'spline',
			name: t('Pareto'),
			data: getPareto(categorizedSeries),
			seriesName,
			zIndex: 10,
			color: datasetStyleConfig[PARETO_LINE_COLOR] || 'black',
			yAxis: 1,
		});
	}

	const result = {
		series,
		...(seriesIndex === 0 ? { xAxis } : {}),
		...((!isObjectEmpty(yAxis) && seriesIndex === 0) ? { yAxis: [yAxis] } : {}),
	};
	return result;
};
