import {actionTypes} from '@seismic/core';
import {createHttpEffect} from '@seismic/effect-http';
import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const moment = require('moment');
import get from 'lodash/get';

import {HR_CARD_CLICKED} from '../sn-hr-card/hrCardConstants';
import {HR_CARD_SET_VIEW_ALL_CLICKED} from '../sn-hr-card-set/hrCardSetConstants';
import * as constants from './hrCaseCardSetConstants';

const updateLocale = state => {
	const userLang = get(state, 'behaviors.wsUserData.language', '');
	if (!userLang)
		return;

	moment.locale(userLang);
};

const getRequestParamsFromProperties = properties => {
	const { configurationId, recordLimit } = properties;
	const requestParams = {};
	if (configurationId)
		requestParams.config_id = configurationId;

	if (recordLimit)
		requestParams.record_limit = recordLimit;

	return requestParams;
};

const getRecordWatcherFilterString = filter => {
	filter = filter.
		replace(/\^EQ/g, '').
		replace(/\^ORDERBY(?:DESC)?[^^]*/g, '').
		replace(/\^GOTO/, '');

	filter = filter || 'sys_id!=NONE'; // AMB subscriptions require a non-empty query
	return btoa(unescape(encodeURIComponent(filter))).replace(/=/g, '-');
};

export default {
	/**
	 * Dispatched when clicking a card
	 */
	[HR_CARD_CLICKED]: ({ dispatch, action }) => {
		action.stopPropagation();

		const { payload: { table, sys_id }} = action;

		if (!table || !sys_id)
			return;
		// Open the record represented by the card using workspace
		dispatch(constants.PREVIEW_RECORD, { table, sys_id });
	},

	/**
	 * Dispatched when clicking the "View all..." link at the end
	 * of the card set
	 */
	[HR_CARD_SET_VIEW_ALL_CLICKED]: ({ state, dispatch, action }) => {
		action.stopPropagation();

		const {
			table,
			tableLabel,
			query,
			columns
		} = state;

		const payload = {
			renderer_type: 'layout',
			params: {
				table,
				listTitle: tableLabel,
				query,
				columns,
				workspaceConfigId: get('state.behaviors.wsConfigSysId', '')
			}
		};

		// Open the list of records represented by the card set
		dispatch(constants.ITEM_SELECTED, payload);
	},

	[constants.FETCH_DATA]: createHttpEffect('/api/sn_hr_ws/landing_page_cards', {
		method: 'GET',
		queryParams: [ 'config_id', 'record_limit' ],
		successActionType: constants.FETCH_DATA_SUCCEEDED,
		errorActionType: constants.FETCH_DATA_FAILED,
	}),

	[constants.FETCH_DATA_SUCCEEDED]: ({ dispatch, state, action, updateState }) => {
		const previousQuery = state.query;
		const previousTable = state.table;
		const realtimeJustActivated = state.activatingRealtime;

		const newState = {
			...state,
			...action.payload.result,
			status: constants.LOADED,
			showViewAll: action.payload.result.hasMore,
			activatingRealtime: false
		};

		updateState(newState);

		const queryChanged = previousQuery !== newState.query;
		const needSubscription =
			state.properties.isRealtime && (queryChanged || realtimeJustActivated);

		if (!needSubscription)
			return;

		// If realtime was already on, unsubscribe from the previous query
		if (!realtimeJustActivated) {
			dispatch(constants.AMB_UPDATE_SUBSCRIPTION, {
				table: previousTable,
				query: previousQuery,
				active: false
			});
		}

		// Subscribe to the new query
		dispatch(constants.AMB_UPDATE_SUBSCRIPTION, {
			table: newState.table,
			query: newState.query,
			active: true
		});
	},

	[constants.FETCH_DATA_FAILED]: ({ state, updateState }) => {
		updateState({
			...state,
			status: constants.ERROR
		});
	},

	/**
	 * Dispatched when properties which require a data refresh are changed
	 * (e.g. recordLimit, configurationId)
	 */
	[constants.PREFETCH_DATA]: ({ dispatch, properties }) => {
		dispatch(constants.FETCH_DATA, getRequestParamsFromProperties(properties));
	},

	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties, state }) => {
		updateLocale(state);
		dispatch(constants.FETCH_DATA, getRequestParamsFromProperties(properties));
	},

	[constants.AMB_SUBSCRIPTION_CHANGE_FAILED]: () => {
		console.warn('Unable to subscribe to record watcher');
	},

	/**
	 * Dispatched when a record that's being watched is changed
	 */
	[constants.AMB_ITEM_CHANGED]: ({ dispatch, properties }) => {
		dispatch(constants.FETCH_DATA, getRequestParamsFromProperties(properties));
	},

	/**
	 * Dispatched when subscribing or unsubscribing to a record watcher query
	 */
	[constants.AMB_SUBSCRIPTION_CHANGE]: createAmbSubscriptionEffect('/rw/default/:table/:filter', {
		subscribeFailedActionType: constants.AMB_SUBSCRIPTION_CHANGE_FAILED,
		messageReceivedActionType: constants.AMB_ITEM_CHANGED
	}),

	[constants.AMB_UPDATE_SUBSCRIPTION]: ({ state, action: { payload }, dispatch }) => {
		const table = get(payload, 'table', state.table);
		const query = get(payload, 'query', state.query);
		if (!table || query === null)
			return;

		dispatch(constants.AMB_SUBSCRIPTION_CHANGE, {
			table: table,
			filter: getRecordWatcherFilterString(query),
			subscribe: payload.active
		});
	}
};
