export default (stageValue, percentage) => {
	const variant = 'tertiary';
	let color = 'gray';

	if (stageValue === 'breached')
		color = 'critical';

	else if (percentage || percentage === 0) {
		if (percentage < 75)
			color = 'positive';
		else if (percentage < 100)
			color = 'high';
		else
			color = 'critical';
	}

	return { variant, color };
};
