import {
	DERIVED_SERIES,
	CHANGE,
	CHANGE_PERCENT,
	TRENDLINE,
	COMMENTS,
	INITIATIVES,
	PREDICTION_BAND,
	CONFIDENCE_BAND,
	THRESHOLDS,
	TARGETS,
	FORECAST_RANGE,
	X_AXIS_PLOTLINES,
	DECIMAL_PRECISION,
} from './constants.time-series';
import {
	hasScores,
	hasDerivedSeries,
	hasForecast,
} from './helpers/index';
import buildDataSeries from './data-series/build-data-series';
import buildForecast from './forecast-series/build-forecast-series';
import buildBandSeries from './band-series/build-band-series';
import buildChangePercentSeries from './change-percent-series/build-change-percent-series';
import isChangePercentOnly from './helpers/is-change-percent-only';
import buildTargets from './targets/build-all-targets';
import buildTresholds from './thresholds/build-all-thresholds';
import getInvisibleThresholdSeries from './thresholds/get-invisible-threshold-series';
import getExtremeMax from './helpers/get-extreme-max';
import getExtremeMin from './helpers/get-extreme-min';
import buildComments from './comments-series/index';
import buildXAxisPlotlines from './x-axis-plotlines/build-all-x-axis-plotlines';

export default (datasetStyleConfig, singleSeries, color, datasetIndex = 0) => {
	let series = [];
	let yPlotLines = [];
	let xPlotLines = [];
	const xAxis = [];
	const yAxis = [];
	const min = datasetStyleConfig.min || getExtremeMin(singleSeries);
	const max = datasetStyleConfig.max || getExtremeMax(singleSeries);

	// data series or scores
	if (hasScores(singleSeries)) {
		series.push(buildDataSeries(datasetStyleConfig, singleSeries, color, datasetIndex));
		if (hasForecast(singleSeries))
			series.push(buildForecast(datasetStyleConfig, singleSeries.forecast, color, datasetIndex));
	}


	// derived series
	if (singleSeries[DERIVED_SERIES]) {
		// change
		if (hasDerivedSeries(singleSeries, CHANGE)) {
			const changeSeries = singleSeries[DERIVED_SERIES][CHANGE];
			series.push(buildDataSeries(datasetStyleConfig, changeSeries, color, 0, 'change'));
		}

		// change percent
		if (hasDerivedSeries(singleSeries, CHANGE_PERCENT)) {
			const result = buildChangePercentSeries(datasetStyleConfig, singleSeries, color);
			series.push(result.series);
			yAxis.push(result.yAxis);
		}

		// trendline
		if (hasDerivedSeries(singleSeries, TRENDLINE)) {
			const trendline = singleSeries[DERIVED_SERIES][TRENDLINE];
			series.push(buildBandSeries(trendline, TRENDLINE, datasetStyleConfig[DECIMAL_PRECISION]));
		}

		// prediction band
		if (hasDerivedSeries(singleSeries, PREDICTION_BAND)) {
			const predictionBand = singleSeries[DERIVED_SERIES][PREDICTION_BAND];
			series.push(buildBandSeries(predictionBand, PREDICTION_BAND, datasetStyleConfig[DECIMAL_PRECISION]));
		}

		// confidence band
		if (hasDerivedSeries(singleSeries, CONFIDENCE_BAND)) {
			const confidenceBand = singleSeries[DERIVED_SERIES][CONFIDENCE_BAND];
			series.push(buildBandSeries(confidenceBand, CONFIDENCE_BAND, datasetStyleConfig[DECIMAL_PRECISION]));
		}

		// forecast range
		if (hasDerivedSeries(singleSeries, FORECAST_RANGE)) {
			const forecastRange = singleSeries[DERIVED_SERIES][FORECAST_RANGE];
			series.push(buildBandSeries(forecastRange, FORECAST_RANGE));
		}

		// thresholds
		if (hasDerivedSeries(singleSeries, THRESHOLDS)) {
			const thresholds = singleSeries[DERIVED_SERIES][THRESHOLDS];
			yPlotLines = yPlotLines.concat(buildTresholds(thresholds, datasetStyleConfig));
			series.push(getInvisibleThresholdSeries(yPlotLines, (min + max) / 2));
		}

		// targets
		if (hasDerivedSeries(singleSeries, TARGETS)) {
			const targetSeries = singleSeries[DERIVED_SERIES][TARGETS];
			series = series.concat(buildTargets(targetSeries, getExtremeMax(singleSeries)));
		}

		// comments
		if (hasDerivedSeries(singleSeries, COMMENTS) && hasScores(singleSeries))
			series = series.concat(buildComments(singleSeries, COMMENTS));

		// initiatives
		if (hasDerivedSeries(singleSeries, INITIATIVES) && hasScores(singleSeries))
			series = series.concat(buildComments(singleSeries, INITIATIVES));

		// xAxis plotLines
		if (hasDerivedSeries(singleSeries, X_AXIS_PLOTLINES)) {
			const xAxisValues = singleSeries[DERIVED_SERIES][X_AXIS_PLOTLINES];
			xPlotLines = xPlotLines.concat(buildXAxisPlotlines(xAxisValues));
		}
	}

	const result = {
		series,
		...(yPlotLines.length > 0 ? { yPlotLines } : {}),
		...(xPlotLines.length > 0 ? { xPlotLines } : {}),
		...(xAxis.length > 0 ? { xAxis } : {}),
		...(yAxis.length > 0 ? { yAxis } : {}),
		changePercentOnly: isChangePercentOnly(singleSeries),
	};
	return result;
};
