import { getValueWithUnit } from 'helpers/value';

export const buildYFormat = (styleConfiguration, formattingOptions = {}) => {
	// user provided y formatter
	if (styleConfiguration.y_format &&
		typeof styleConfiguration.y_format === 'function')
		return {
			yFormat(timestamp) {
				return styleConfiguration.y_format(timestamp);
			},
		};

	return {
		yFormat(value) {
			return getValueWithUnit(value, formattingOptions);
		},
	};
};
