import hasScores from './has-scores';
import hasDerivedSeries from './has-derived-series';
import { CHANGE, CHANGE_PERCENT } from '../constants.time-series';

export default (singleSeries) => {
	return (!hasScores(singleSeries)
		&& !hasDerivedSeries(singleSeries, CHANGE)
		&& hasDerivedSeries(singleSeries, CHANGE_PERCENT));
};
