import buildTimeSeries from './time-series/build-time-series';
import buildYoyTimeSeries from './time-series/build-yoy-time-series';
import buildCategory from './category/build-category-series';
import { COLORS } from './time-series/constants.time-series';
import { TIME_SERIES, CATEGORY } from '../../constants';

export default (datasetStyleConfig, datasetSeries, chartType = TIME_SERIES, datasetIndex = 0) => {
	const datasetBuilt = {};
	const colors = datasetStyleConfig[COLORS];
	datasetSeries.forEach((singleSeries, index) => {
		// assgin a color to a single series
		const color = colors[index % colors.length];
		// parse a single series (that may contain derived series)
		let result;
		switch (chartType) {
		case TIME_SERIES:
			if (datasetStyleConfig.isYoy)
				result = buildYoyTimeSeries(datasetStyleConfig, singleSeries, colors);
			else
				result = buildTimeSeries(datasetStyleConfig, singleSeries, color, datasetIndex);
			break;

		case CATEGORY:
			result = buildCategory(datasetStyleConfig, singleSeries, color, index);
			break;
		}

		// get all the chart components built from the parsing
		// for instance: axes, series, plot-options
		for (const key in result)
			if (datasetBuilt[key] && Array.isArray(datasetBuilt[key]))
				datasetBuilt[key] = datasetBuilt[key].concat(result[key]);
			else
				datasetBuilt[key] = result[key];
	});
	return datasetBuilt;
};
