import { uniqueId } from 'lodash';
import { getPatterns } from './get-patterns';

/**
 * Create the general patterns. Don't use the highcharts ones, so that
 * you use patterns specific to each highcharts.
 *
 * @param {Object} highchartsOptions
 */
export const setPatterns = (highchartsOptions) => {
	highchartsOptions.uniqid = 'unpattern-' + uniqueId();

	const patterns = getPatterns(highchartsOptions.uniqid);

	if (!highchartsOptions.defs)
		highchartsOptions.defs = {};

	highchartsOptions.defs.patterns = (highchartsOptions.defs.patterns || [])
		.concat(patterns);
};
