import isChangePercentOnly from '../helpers/is-change-percent-only';
import buildYAxisChangePercent from './build-y-axis-change-percent';
import buildDataSeries from '../data-series/build-data-series';
import { DERIVED_SERIES, CHANGE_PERCENT } from '../constants.time-series';

export default (datasetSeriesConfig, singleSeries, color) => {
	// passing the color so the changePercent so it has the same color
	// as the scores
	const changePercentOnly = isChangePercentOnly(singleSeries);
	const changePercentSeries = {
		...buildDataSeries(datasetSeriesConfig, singleSeries[DERIVED_SERIES][CHANGE_PERCENT], color),
		yAxis: changePercentOnly ? 0 : 1,
		id: CHANGE_PERCENT,
	};
	return {
		yAxis: buildYAxisChangePercent(datasetSeriesConfig),
		series: changePercentSeries,
	};
};
