import {COMPONENT_BOOTSTRAPPED} from '@servicenow/ui-core/lib/constants';
import {ACTIONS} from './constants';
import {errorLogger} from '../common/effects';
import {
	getClosedCaseCount, getClosedCaseCountSuccess
} from './effects';
import userInfo from './userInfo';

const handleBootstrap = ({dispatch, state}) => {
	const userId = userInfo.getUserId(state);
	const condition = `assigned_to=${userId}^closed_atONToday@javascript:gs.beginningOfToday()@javascript:gs.endOfToday()^active=false`;
	dispatch(ACTIONS.LOAD_HR_CASES_CLOSED, {
		condition
	});

	// TODO should kill this when component is closed
	setInterval(() => {
		dispatch(ACTIONS.LOAD_HR_CASES_CLOSED, {
			condition
		});
	}, 60000);
};

export default {
	actions: {
		[ACTIONS.LOAD_HR_CASES_CLOSED]: {stopPropagation: true},
		[ACTIONS.LOAD_HR_CASES_CLOSED_SUCCESS]: {stopPropagation: true},
		[ACTIONS.LOAD_HR_CASES_CLOSED_ERROR]: {stopPropagation: true}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: handleBootstrap,
		[ACTIONS.LOAD_HR_CASES_CLOSED]: getClosedCaseCount,
		[ACTIONS.LOAD_HR_CASES_CLOSED_SUCCESS]: getClosedCaseCountSuccess,
		[ACTIONS.LOAD_HR_CASES_CLOSED_ERROR]: errorLogger
	}
};
