import hasDerivedSeries from './series/has-derived-series';

/**
 *
 * Returns true if one of the series of one of the datasets
 * has a change_percent series visible
 * @param  {Object} chartConfiguration - chart configuration
 * in the format of the Reporting API
 * @returns {boolean}
 */
export default (chartConfiguration) => {
	let hasOneChangePercentSeries = false;
	if (!chartConfiguration.datasets || !chartConfiguration.datasets[0].series)
		return hasOneChangePercentSeries;

	chartConfiguration.datasets.find((dataset) => {
		return dataset.series.find((singleSeries) => {
			const hasChangePercent = hasDerivedSeries(singleSeries, 'change_percent');
			if (hasChangePercent)
				hasOneChangePercentSeries = true;

			return hasChangePercent;
		});
	});
	return hasOneChangePercentSeries;
};
