export const emptyDatasets = (datasets = []) => {
	// If there aren't any datasets, then it's clearly empty
	if (!datasets || !datasets.length)
		return true;

	// Check if all the datasets don't have data
	return datasets.every((item) => {
		// If it's a scorecard response
		if (item.score_details)
			return false;
		// If there are no series, then, clearly this dataset is empty
		if (!item.series || !item.series.length)
			return true;

		// Check if all the series don't have data
		return item.series.every((series) => {
			// If it's a category series and there are elements
			if (series.elements && series.elements.length)
				return false;

			// Otherwise, check if there is no data
			return (!series.data || !series.data.length);
		});
	});
};
