import getAnnotationId from './get-annotation-id';

export default (params) => {
	const {
		seriesIndex,
		datasetIndex,
		annotationType, // comments or initiatives
	} = params;
	const id = getAnnotationId(annotationType, seriesIndex, datasetIndex);
	return {
		[id]: {
			symbol: 'comment',
		},
	};
};
