import { getTargetData } from './get-target-data';
import { getSinglescoreTimeSeriesDefaultStyle } from './get-singlescore-time-series-default-style';

export const getTimeSeries = (dataset = {}, styleConfig = {}) => {
	const datasets = [{
		series: [
			{
				name: dataset.name && dataset.name.split('/')[0],
				data: dataset.sparkdata,
				...getTargetData(dataset, styleConfig),
			},
		],
	}];

	return {
		datasets,
		styleConfigurations: [{
			...getSinglescoreTimeSeriesDefaultStyle(),
			decimal_precision: styleConfig.decimal_precision,
			rtl: styleConfig.isRTL,
		}],
	};
};
