import { dateHelper } from 'par-components';

export const xAxisLabelFormatterWithLabelText = (value, label, frequency = 'daily', isYoy = false, fiscalYearStart = false, i18Translations) => {
	if (frequency === 'biz_calendar_frequency' && label !== undefined)
		return label;
	if (window && window.Highcharts) {
		let customFormat = dateHelper.readableDate(value, { frequency: frequency, type: 'short', fiscalYearStart: fiscalYearStart });
		if (isYoy)
			if (frequency !== 'yearly') {
				const regexYear = /,?\s*\d{4}$/;
				customFormat = customFormat.replace(regexYear, '');
			} else
				return i18Translations.year;

		return customFormat;
	}
	return value;
};

export default (value, frequency, isYoy, fiscalYearStart, i18Translations) => {
	xAxisLabelFormatterWithLabelText(value, undefined, frequency, isYoy, fiscalYearStart, i18Translations);
};
