import hasForecast from './has-forecast';
import { FORECAST, DATA } from '../constants.time-series';
// singleSeries contains the data series
// and the derived series
export default (singleSeries) => {
	if (hasForecast(singleSeries)) {
		const dataForecastData = singleSeries[FORECAST][DATA];
		return dataForecastData[dataForecastData.length - 1].x;
	} else if (singleSeries[DATA] && singleSeries[DATA].length > 0)
		return singleSeries[DATA][singleSeries[DATA].length - 1].x;

	return null;
};

