import { createHttpEffect } from "@servicenow/ui-effect-http";
import isEmpty from "lodash/isEmpty";
import get from "lodash/get";
import { actionTypes } from '@servicenow/ui-core';
import {
    SET_LINKS_FROM_PROPERTIES,
    LOAD_LINKS_USING_CONTENT_DELIVERY,
    CONTENT_DELIVERY_LOAD_SUCCEEDED,
	CONTENT_DELIVERY_LOAD_FAILED,
	PROPERTIES_SET,
	MISSING_LINK_PROPERTY,
	status
} from "./constants"

const updateLinksEffect = ({updateProperties, updateState, action}) => {
	const links = action.payload.links || action.payload.result;

	updateState({
		links
	});

	updateProperties ({
		status: isEmpty(links) ? status.EMPTY : status.READY
	});
};

const updateLinksFailedEffect = ({ dispatch }) => dispatch(PROPERTIES_SET, { status: status.ERROR });

const missingLinksEffect = ({ dispatch }) => dispatch(PROPERTIES_SET, { status: status.EMPTY });

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const handleComponentBootstrapped = ({ properties, dispatch }) => {
	const contentIdentifier = get(properties, "contentIdentifier");
	const links = get(properties, "links");


	if (links) dispatch(SET_LINKS_FROM_PROPERTIES, { links });
	else if (contentIdentifier)
		dispatch(LOAD_LINKS_USING_CONTENT_DELIVERY, { contentIdentifier });
	else dispatch(MISSING_LINK_PROPERTY);
};


export default {
    [SET_LINKS_FROM_PROPERTIES]: updateLinksEffect,
    /* Content Delivery actions */
    [LOAD_LINKS_USING_CONTENT_DELIVERY] : createHttpEffect("/api/sn_cd/audience/getContentForWorkspaceInstance?id=:contentIdentifier", {
		method: "GET",
		pathParams: ["contentIdentifier"],
		successActionType: CONTENT_DELIVERY_LOAD_SUCCEEDED,
		errorActionType: CONTENT_DELIVERY_LOAD_FAILED
	}),
    [CONTENT_DELIVERY_LOAD_SUCCEEDED]: updateLinksEffect,
	[CONTENT_DELIVERY_LOAD_FAILED]: updateLinksFailedEffect,
	[MISSING_LINK_PROPERTY]: missingLinksEffect,
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped
}
