import { get, isEmpty } from 'lodash';
import { createHttpEffect } from '@seismic/effect-http';
import { t } from 'sn-translate';
import {
	SERIES_FETCH_REQUESTED,
	SERIES_FETCH_DATACONFIG_REQUESTED,
	SERIES_FETCH_STARTED,
	SERIES_FETCH_SUCCEEDED,
	ELEMENT_VISIBLE,
} from '../constants';
import { parseServerChartConfiguration } from 'par-visualization/seismic';

const handleSeriesFetchRequested = createHttpEffect('/api/now/v2/reporting/reports/series/:sysId', {
	method: 'POST',
	pathParams: ['sysId'],
	batch: false,
	startActionType: SERIES_FETCH_STARTED,
	successActionType: SERIES_FETCH_SUCCEEDED,
	errorActionType: SERIES_FETCH_SUCCEEDED,
});

const handleSeriesFetchDataConfigRequested = createHttpEffect('/api/now/v2/reporting/reports/series', {
	method: 'POST',
	dataParam: 'dataConfigurationsReqBody',
	batch: false,
	startActionType: SERIES_FETCH_STARTED,
	successActionType: SERIES_FETCH_SUCCEEDED,
	errorActionType: SERIES_FETCH_SUCCEEDED,
});

/**
 * This effect is called when the fetching starts
 */
const handleSeriesFetchStarted = ({ state, updateState, action }) => {
	updateState({
		...state,
		loading: true,
		error: false,
		fetchedBySysId: action.meta.sysId,
	});
};

/**
 * This effect is called when the fetching is done (regardless of an error occurring)
 */
const handleSeriesFetchFinished = ({ state, updateState, action }) => {
	if (action.error) {
		return {
			...state,
			loading: false,
			error: {
				status: get(action.payload, 'status'),
				...get(action.payload, 'data.error', t('Error fetching series'))
			},
		};
	}

	// Get the properties
	const { properties } = state;

	const {
		chartType,
		payload
	} = action.payload.result;

	const v1Payload = {
		result: payload
	};

	// Get all the needed data
	const chartConfiguration = parseServerChartConfiguration(v1Payload);
	const dataConfigurations = chartConfiguration.data_configurations || properties.dataConfigurations;
	let styleConfigurations = chartConfiguration.style_configurations || properties.styleConfigurations;
	const datasets = chartConfiguration.datasets || properties.datasets;

	if (isEmpty(chartConfiguration.style_configurations) && !isEmpty(properties.styleConfigurations))
		styleConfigurations = [ ...properties.styleConfigurations ];

	// If there is no data configurations, then don't even bother
	if (!dataConfigurations || !dataConfigurations.length) {
		return {
			...state,
			version: 2,
			loading: false,
			error: t('Series not found'),
		};
	}

	updateState({
		...state,
		version: 2,
		dataConfigurations,
		styleConfigurations,
		datasets,
		loading: false,
		error: false,
		chartType
	});
};

export default {
	[SERIES_FETCH_REQUESTED]: handleSeriesFetchRequested,
	[SERIES_FETCH_DATACONFIG_REQUESTED]: handleSeriesFetchDataConfigRequested,
	[SERIES_FETCH_STARTED]: handleSeriesFetchStarted,
	[SERIES_FETCH_SUCCEEDED]: handleSeriesFetchFinished,
	[ELEMENT_VISIBLE]: {
		effect({ dispatch, state }) {
			const { sysId } = state.properties;

			if (sysId) {
				// We resend the series fetch. Sysid needs to be both in payload and in the meta
				dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });
			}
		},
	}
};
