import buildSeries from 'parser/xenolith/time-series/series/';
import mergeArraysByKey from 'parser/xenolith/time-series/series/helpers/merge-arrays-by-key/merge-arrays-by-key';
import { extendTargetsToAllPoints } from './extend-target-to-data-points';
import { unescape } from 'lodash';
/**
* Returns the data that will be put in the data table body
* the datapoints sorted by timestamp
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API that contains styleConfigurations and datasets
* - styleConfigurations
* - datasets
* @return {array} - returns an array of dataPoints (see below for the format)
*/

/**
 * dataPoint is an object that has the following format:
* {
* 	x: 1529798400000,                   -- the timestamp
*   y_data_ds_0_series_0: 34,			-- all the scores from all the datasets
*   y_data_ds_0_series_1: 2,               and series for this timestamp
*	y_data_ds_1_series_0: 22,
*   ...
*   y_change_ds_0_series_0: 4,			-- all the derived series from all the datasets
*   y_change_percent_ds_0_series_0: 2,     and series for this timestamp
*	y_target_ds_0_series_0: 22,
*	y_trendline_ds_0_series_0: 22,
*   ...
* }
* see getSeriesYKey method for the convention of naming y keys
*/

/*
* NB: we re-use the Xenolith parser series builder because the
* output data is convenient for filling the table but this component
* is independent from Xenolith or Highcharts and can be used with both
*/

export const getTableBodyData = (chartConfiguration = {}) => {
	/* first, build unified set for all the series in the following form:
	* {
	*   seriesData: [{ x: timestamp, y_data_ds_0_series_0:0, y_data_ds_0_series_1:2, ...]
	*   targetsData: [{ x: timestamp, y0:0, y1:2, y3:2 }, ...]
	*   forecastData: [{ x: timestamp, y0:0, y1:2, y3:2 }, ...]
	*   trendlinesData: [{ x: timestamp, y0:0, y1:2, y3:2 }, ...]
	*   ...(all the derived series)
	* }
	*/
	const allSeriesBuilt = buildSeries(chartConfiguration);
	// then, merge all of the arrays (seriesData, forecastData, etc) by the key x
	let forecastData = allSeriesBuilt.forecastData;
	// Exclude the first forecasted data point
	// if it's a duplicate of the last series point
	if ((forecastData.length > 0) &&
			forecastData[0].x === allSeriesBuilt.seriesData[allSeriesBuilt.seriesData.length - 1].x)
		forecastData = forecastData.slice(1);

	const annotations = allSeriesBuilt.annotationsData.map((annotation) => {
		return {
			// message was escaped when building comments to avoid xss on comment hover.
			// Comments displayed on data table do not need to be escaped
			[annotation.type]: unescape(annotation.message),
			x: annotation.x,
		};
	});

	const dataPoints = mergeArraysByKey(
		'x',
		[
			allSeriesBuilt.seriesData,
			forecastData,
			allSeriesBuilt.targetsData,
			allSeriesBuilt.trendlinesData,
			allSeriesBuilt.predictionBandData,
			allSeriesBuilt.confidenceBandData,
			annotations,
		]
	);

	// extend target points to all data points
	const dataPointsWithTargets = extendTargetsToAllPoints(dataPoints);

	// add the thresholds to all dataPoints for convenience
	return dataPointsWithTargets.map((dataPoint) => {
		return {
			...dataPoint,
			...(allSeriesBuilt.thresholdData.length > 0 ?
				{ thresholds: allSeriesBuilt.thresholdData } :
				{}),
		};
	});
};
