import React from 'react';
import PropTypes from 'prop-types';

// Style
import './component.footer.scss';

// Helpers
import { i18nHelper } from 'par-components';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		target: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number,
			PropTypes.bool,
		]),
		gap: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number,
			PropTypes.bool,
		]),
		gapPercent: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.bool,
		]),
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'Footer';

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const { t } = i18nHelper;
		const {
			target,
			gap,
			gapPercent,
		} = this.props;

		if (!target && !gap && !gapPercent)
			return null;

		return (
			<div className="par-singlescore-footer">
				<div className="segments-container">
					{target &&
						<div className="segment">
							<label>{t('Target')}</label>
							<strong>{target}</strong>
						</div>
					}
					{(gap || gapPercent) &&
						<div className="segment">
							<label>{t('Gap')}</label>
							<strong>
								{gap}
								{gapPercent &&
									(gap ? ` (${gapPercent})` : gapPercent)
								}
							</strong>
						</div>
					}
				</div>
			</div>
		);
	}
}
