import {
	TITLE_HORIZONTAL_ALIGNMENT,
	TITLE_VERTICAL_ALIGNMENT,
	CHART_TITLE_X,
	CHART_TITLE_Y,
} from './constants.title';
import isObjectEmpty from '../../helpers/is-object-empty';

/**
 * Applies alignment properties for Highcharts legend
 * Applicable in the Report Designer for exports
 * See STRY3849663
	const mainStyleConfiguration = {};
 * @param  {Object} [mainStyleConfiguration={}]
 * Returns legend options related to alignment: maxHeight, y and layout
 * @return {Object}
 */
export default (mainStyleConfiguration = {}) => {
	if (isObjectEmpty(mainStyleConfiguration))
		return {};

	const titleOptions = {};
	// position defined by alignment
	if (!mainStyleConfiguration[CHART_TITLE_X]
		&& mainStyleConfiguration[TITLE_VERTICAL_ALIGNMENT]
		&& mainStyleConfiguration[TITLE_HORIZONTAL_ALIGNMENT]) {
		titleOptions.align = mainStyleConfiguration[TITLE_HORIZONTAL_ALIGNMENT];

		if (mainStyleConfiguration[TITLE_VERTICAL_ALIGNMENT] !== 'top')
			titleOptions.verticalAlign = mainStyleConfiguration[TITLE_VERTICAL_ALIGNMENT];

		if (mainStyleConfiguration[TITLE_VERTICAL_ALIGNMENT] === 'bottom')
			titleOptions.y = 0;

		if (mainStyleConfiguration[TITLE_HORIZONTAL_ALIGNMENT] === 'right' &&
			mainStyleConfiguration[TITLE_VERTICAL_ALIGNMENT] === 'top')
			titleOptions.x = -40;

	// custom title position (defined by x,y)
	} else if (mainStyleConfiguration[CHART_TITLE_X] && mainStyleConfiguration[CHART_TITLE_Y]) {
		titleOptions.x = mainStyleConfiguration[CHART_TITLE_X];
		titleOptions.y = mainStyleConfiguration[CHART_TITLE_Y];
	}

	return titleOptions;
};
