/**
* Transform timeseries > time type if passed in data_configurations, before sending it to the server.
* @param {Array} dataConfigurations - array of data configuration objects
* @returns {Array} new array with new type values
*/
export default (dataConfigurations) => {
	if (Array.isArray(dataConfigurations)) {
		return dataConfigurations.map((dataConfig) => {
			if (dataConfig.type && dataConfig.type === 'timeseries') {
				return {
					...dataConfig,
					type: 'time'
				};
			}

			return dataConfig;
		});
	}

	return dataConfigurations;
};
