import { i18nHelper } from 'par-components';
import {
	CONFIDENCE_BAND,
	PREDICTION_BAND,
	TRENDLINE,
	FORECAST_RANGE,
	paZindex,
} from '../constants.time-series';

export const getBandSeriesProperties = () => {
	const { t } = i18nHelper;

	return {
		[CONFIDENCE_BAND]: {
			type: 'arearange',
			color: '#FFEEBF',
			name: t('Confidence Band'),
			zIndex: paZindex[CONFIDENCE_BAND],
		},
		[PREDICTION_BAND]: {
			type: 'arearange',
			color: '#FFF6DE',
			name: t('Prediction Band'),
			zIndex: paZindex[PREDICTION_BAND],
		},
		[TRENDLINE]: {
			type: 'line',
			color: '#FF8800',
			// It's called "pa_trend" because the string "Trend" is translated
			// as starging with lowercase :(
			name: t('pa_trend'),
			zIndex: paZindex[TRENDLINE],
		},
		[FORECAST_RANGE]: {
			type: 'arearange',
			color: '#d3d3d3',
			name: t('Forecast Range'),
			zIndex: paZindex[TRENDLINE],
		},
	};
};
