import React from 'react';
import PropTypes from 'prop-types';

// Style
import './component.change.scss';

// Helpers
import { i18nHelper } from 'par-components';

// Components
import ArrowUp from './arrows/component.arrow-up';
import ArrowDown from './arrows/component.arrow-down';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		value: PropTypes.string,
		showValue: PropTypes.bool,
		percent: PropTypes.string,
		showPercent: PropTypes.bool,
		arrow: PropTypes.string,
		color: PropTypes.string,
		date: PropTypes.string,
		humanDate: PropTypes.string,
		showDate: PropTypes.bool,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'Change';

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const { t } = i18nHelper;
		const {
			value,
			showValue,
			percent,
			showPercent,
			arrow,
			color,
			date,
			humanDate,
			showDate,
		} = this.props;

		// Don't need to display anything
		if (!showValue && !showPercent)
			return null;

		const colorStyle = {
			color,
		};

		return (
			<div className="par-singlescore-change">
				{(arrow === 'up' || arrow === 'down') &&
					<div className="arrow">
						{arrow === 'up' && <ArrowUp color={color} />}
						{arrow === 'down' && <ArrowDown color={color} />}
					</div>
				}

				{showValue &&
					<strong className="change-value" style={colorStyle}>
						{value}
					</strong>
				}

				{percent && showPercent &&
					<strong className="change-percent" style={colorStyle}>
						{showValue ? `(${percent})` : percent}
					</strong>
				}

				{humanDate && date && showDate &&
					<div className="date-container">
						<span className="date-since">
							{t('since')}
						</span>

						<time dateTime={date}>
							{humanDate}
						</time>
					</div>
				}
			</div>
		);
	}
}
