import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import {ACTIONS} from './constants';
import {hrCasesClosedQuery} from './hrCasesClosedQuery';

/**
 * Create a graphql effect to fetch sn_hr_core_case closed count
 */
export const getClosedCaseCount = createGraphQLEffect(hrCasesClosedQuery, {
	variableList: ['condition'],
	successActionType: ACTIONS.LOAD_HR_CASES_CLOSED_SUCCESS,
	errorActionType: ACTIONS.LOAD_HR_CASES_CLOSED_ERROR
});


/**
 * Create effect to handle loading success.
 */
export const getClosedCaseCountSuccess = coeffects => {
	const countClosedCases = get(coeffects.action.payload, 'data.hrClosedCasesResult[0].count');

	coeffects.updateProperties({
		countClosedCases
	});

	coeffects.updateState({
		isLoaded: true
	});
};

