import { buildReportTrendDatasets } from './build-trend-datasets';
import { buildReportTrendStyleConfigurations } from './build-trend-style-configurations';

/**
* Transforms the reporting trend-by series format
* into the timeseries Reporting API format
* @param {object} reportTrendConfiguration - object that comes from the Trend Reporting API
* it has the following format:
- {
	styleConfigurations: [{..}] <-- similar format to the timeseries API format
	datasets: [{
		series: trendBySeries <-- trendBySeries is a string
	}]
}
* @returns {object} chartConfiguration - in the following format:
* {styleConfigurations: [{..}, {..}], datasets: [{..}, {..}]}
*/

export const parseReportingTrendByTimeseries = (reportTrendConfiguration = {}) => {
	return {
		...reportTrendConfiguration,
		style_configurations: buildReportTrendStyleConfigurations(reportTrendConfiguration),
		datasets: buildReportTrendDatasets(reportTrendConfiguration),
	};
};

