import React from 'react';
import PropTypes from 'prop-types';

// Defaults
import visualizationDefaults from './visualization-defaults.json';

// Prop types
import StateComponentsType from './prop-type.state-component';

// Helpers
import { withDirection } from 'helpers/direction';

// Components
import VisualizationRouter from '../visualization-router/component.visualization-router';

// Chart library names
import * as chartLibraries from './chart-library-names';

class Visualization extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		// if we pass the sys Id then the dataset and style configurations are retrieved from the backend
		// if we pass dataconfigurations  - ONLY datasets are retrieved from the backend
		sysId: PropTypes.string,
		dataConfigurations: PropTypes.array,
		datasets: PropTypes.array,
		// you can pass only the styleConfigurations to overwrite the ones that come from the server
		// they apply based on their position to all the series in the datasets
		styleConfigurations: PropTypes.array,

		// From Redux
		serverDatasets: PropTypes.array,
		serverStyleConfigurations: PropTypes.array,
		serverClickBehaviors: PropTypes.array,
		error: PropTypes.oneOfType([
			PropTypes.bool,
			PropTypes.object,
		]),

		// From the direction
		dir: PropTypes.string,

		// For the detailed scorecard
		selectedDate: PropTypes.number,
		clickBehaviors: PropTypes.array,

		// which chart library to use: Highcharts or Xenolith
		chartLibrary: PropTypes.string,

		// Check the prop type definition for more info regarding this type
		stateComponents: StateComponentsType.type,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		dir: 'ltr',
		chartLibrary: chartLibraries.HIGHCHARTS,
		stateComponents: StateComponentsType.value,
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'Visualization';

	/**
	 * Called before the component mounts
	 */
	componentWillMount() {
		// Load the data based on sysId
		if (this.props.sysId)
			this.props.setSysId(this.props.sysId);

		// Or load the data based on dataConfigurations
		else if (this.props.dataConfigurations)
			this.props.setDataConfigurations(this.props.dataConfigurations);

		// If there is an error just throw it, the error boundary will catch it
		if (this.props.error)
			throw this.props.error;
	}

	/**
	 * Called when the props change (coming from Redux or otherwise)
	 *
	 * @param  {Object} nextProps
	 */
	componentWillReceiveProps(nextProps) {
		// Load the data based on sysId
		if (nextProps.sysId !== this.props.sysId)
			this.props.setSysId(nextProps.sysId);

		// Or load the data based on dataConfigurations
		else if (nextProps.dataConfigurations !== this.props.dataConfigurations)
			this.props.setDataConfigurations(nextProps.dataConfigurations);

		// If there is an error just throw it, the error boundary will catch it
		if (nextProps.error)
			throw nextProps.error;
	}

	/**
	 * Get the click behaviors for the component
	 *
	 * @param  {Array} clickBehaviors
	 *
	 * @return {Array}
	 */
	getClickBehaviors(clickBehaviors) {
		return Array.isArray(clickBehaviors) ? clickBehaviors : visualizationDefaults.clickBehaviors;
	}

	/**
	 * Merges the styling defaults with
	 * the user provided styleConfigurations
	 * and the serverStyleConfigurations
	 */
	getStyleConfigurations(propsStyleConfigurations = [], serverStyleConfigurations = []) {
		const mainStyleConfiguration = Object.assign(
			{ rtl: (this.props.dir === 'rtl') },
			visualizationDefaults,
			(serverStyleConfigurations ? serverStyleConfigurations[0] : {}),
			(propsStyleConfigurations ? propsStyleConfigurations[0] : {}));

		const styleConfigurations = [mainStyleConfiguration];

		if (serverStyleConfigurations.length > 1)
			serverStyleConfigurations.forEach((currentServerStyleConfig, index) => {
				// specify to the parser which dataset has an yAxis
				const hasYAxis = Object.keys(currentServerStyleConfig).find(styleProperty => /^y_axis/i.test(styleProperty)) ? undefined : false;
				styleConfigurations[index] = {
					...mainStyleConfiguration,
					...currentServerStyleConfig,
					has_y_axis: hasYAxis,
				};
			});

		if (propsStyleConfigurations.length > 1)
			propsStyleConfigurations.forEach((currentUserStyleConfig, index) => {
				// specify to the parser which dataset has an yAxis
				const hasYAxis = Object.keys(currentUserStyleConfig).find(styleProperty => /^y_axis/i.test(styleProperty)) ? undefined : false;
				const existingStyleConfiguration = styleConfigurations[index] || mainStyleConfiguration;
				styleConfigurations[index] = {
					...existingStyleConfiguration,
					...currentUserStyleConfig,
					has_y_axis: hasYAxis,
				};
			});

		return styleConfigurations;
	}

	/**
	 * Merges the datasets got from the server, with the one passed to the
	 * component
	 *
	 * @param  {Array} datasets
	 * @param  {Array} serverDatasets
	 *
	 * @return {Array}
	 */
	getDatasets(datasets, serverDatasets) {
		return []
			.concat(datasets || [])
			.concat(serverDatasets || []);
	}

	/**
	 * Render the react component
	 *
	 * @return {React.Component}
	 */
	render() {
		const datasets = this.getDatasets(this.props.datasets, this.props.serverDatasets);
		const styleConfigurations = this.getStyleConfigurations(this.props.styleConfigurations, this.props.serverStyleConfigurations);
		const clickBehaviors = this.getClickBehaviors(this.props.clickBehaviors);
		return (
			<VisualizationRouter
				chartLibrary={this.props.chartLibrary}
				datasets={datasets}
				styleConfigurations={styleConfigurations}
				clickBehaviors={clickBehaviors}
				onSetExtremes={this.props.onSetExtremes}
				selectedDate={this.props.selectedDate}
				stateComponents={this.props.stateComponents}
			/>
		);
	}
}

export default withDirection()(Visualization);
