import getNumberOfDatasets from '../../helpers/get-number-datasets';
import getStyleConfiguration from '../../helpers/get-style-configuration';
import getDatasetSeries from '../../helpers/get-dataset-series';
import buildSeriesFromDataset from './build-series-from-dataset';
import { TIME_SERIES } from '../../constants';

export default (chartConfiguration, chartType = TIME_SERIES) => {
	const result = {};
	for (let datasetIndex = 0; datasetIndex < getNumberOfDatasets(chartConfiguration); datasetIndex++) {
		const styleConfiguration = getStyleConfiguration(chartConfiguration, datasetIndex);
		const datasetSeries = getDatasetSeries(chartConfiguration, datasetIndex);
		const datasetSeriesBuilt = buildSeriesFromDataset(styleConfiguration, datasetSeries, chartType, datasetIndex);
		for (const key in datasetSeriesBuilt)
			if (result[key])
				result[key] = result[key].concat(datasetSeriesBuilt[key]);
			else
				result[key] = datasetSeriesBuilt[key];
	}
	return result;
};
