import { dateHelper } from 'par-components';
import { t } from 'sn-translate';
import { createCustomElement } from '@seismic/core';
import react from "@seismic/react-renderer";
import { preUserPrefs, getProperty } from 'sn-uxpage-presource';
import visibilityBehavior from 'visibility-behavior';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { dispatchClickAction }  from './interactions';

// Helpers
import { getAttributeValue } from './helpers/domUtil';
import { injectPrefs } from './helpers/inject-user-prefs';

// Constants
import {
	CHART_TYPES,
	SERIES_FETCH_REQUESTED,
	ELEMENT_VISIBLE,
	RECORD_WATCHER_METADATA_REQUESTED,
	GLIDE_SYSTEM_LOCALE,
	POINT_CLICKED,
	SERIES_FETCH_DATACONFIG_REQUESTED,
	SERIES_FETCH_STARTED,
	SERIES_FETCH_SUCCEEDED,
	RECORD_WATCHER_SUBSCRIBED,
	RECORD_WATCHER_METADATA_FETCH_SUCCEEDED,
	RECORD_WATCHER_METADATA_FETCH_FAILED,
	AMB_SUBSCRIPTION_SUCCEEDED,
	AMB_SUBSCRIPTION_FAILED,
	AMB_SUBSCRIPTION_UNSUBSCRIBED,
	AMB_MESSAGE_RECEIVED
} from './constants';

// Action handlers
import { seriesHandlers , recordWatcherHandlers, lifeCycleHandlers } from './handlers';

// Styles
import styles from './snVisualization.scss';
import parStyles from 'par-visualization/dist/seismic.bundle.css';

// Components
import 'sn-hr-shared';
import './snVisualizationError/snVisualizationError';
import './snVisualCue/snVisualCue';
import { Visualization } from 'par-visualization/seismic'; // eslint-disable-line no-unused-vars

//TODO: There is an issue with proxying AMB for RW in dev mode, we have to remove it in future
if (process.env.NODE_ENV === 'development')
	delete window.WebSocket;

// Render the visualization component TODO: Externalize renderVisualization function, beware of the React renderer dependency of Visualization component
const renderVisualization = state => {
	const {
		properties,
		fetchedBySysId,
		dispatch,
		chartType,
		payload,
		isAmbConnectionBroken,
	} = state;

    const fontColor = properties.fontColor;

	// If there is a sysId, but the request has not run yet, then fetch the series and then render the loading state
	if (properties.sysId && fetchedBySysId !== properties.sysId)
		return (<sn-hr-empty-state type="loading" widget={t('Visualization')} variant="fit" color={fontColor}/>);

	if (!payload && !(state.datasets || properties.datasets))
		return;

	// Edge case when just a tiny subset of the widget/report is not supported it's difficult to throw 501 due to streaming.
	if (payload && payload.type === 'not-supported')
		return (<sn-hr-empty-state type="not-supported" variant="fit" color={fontColor}/>);

	// No data in seriesData
	if (payload && payload.seriesData && !payload.seriesData.length)
		return (<sn-hr-empty-state type="empty" variant="fit" color={fontColor}/>);

	const {
		dataConfigurations,
		styleConfigurations,
		datasets,
	} = (state.datasets || state.dataConfigurations ? state : properties); // These are both in the state and in the props

	// Get the first data configuration to check wether it's a list or not
	const dataConfiguration = get(dataConfigurations, '[0]', {});

	// If there are no data configurations or no datasets (also no sysId, but
	// that is checked before the code reaches this point), then the component has not yet been configured
	if (isEmpty(dataConfigurations) && isEmpty(datasets) && isEmpty(styleConfigurations))
		return (<sn-hr-empty-state type="not-configured" variant="fit" color={fontColor}/>);

	// Payload for the click action
	let clickPayload = {
		source_type: dataConfiguration.source_type,
		source_id: dataConfiguration.source_id,
		type: dataConfiguration.type
	};

	if (dataConfiguration.source_type === 'table' || dataConfiguration.source_type === 'source') {
		clickPayload = {
			...clickPayload,
			table: dataConfiguration.table_name,
			conditions: dataConfiguration.filter_query,
			columns: dataConfiguration.field_list
		};
	}

	// inject the user/system preferences into the styleConfigurations
	const styleConfigurationsWithPrefs = injectPrefs(
		styleConfigurations,
		{
			locale: getProperty(GLIDE_SYSTEM_LOCALE),
			userPrefs: preUserPrefs.getPreSource(state, {}),
		},
	);

	const clickBehaviors = dispatchClickAction(dispatch, clickPayload, {
		state,
		dataConfigurations,
		styleConfigurations: styleConfigurationsWithPrefs,
	});

	// Finally, render the React SINGLE SCORE visualization with the passed props (and by overwriting the click behaviours)
	// We only send the dataConfigurations coming from the properties, not the ones that the API receives
	return (
		<Visualization
			dataConfigurations={properties.dataConfigurations}
			styleConfigurations={ (isEmpty(styleConfigurationsWithPrefs) && chartType === CHART_TYPES.SINGLE_SCORE) ?
				[{
					chart_type: CHART_TYPES.SINGLE_SCORE,
					show_zero: true,
				}] :
				styleConfigurationsWithPrefs
			} // TODO: this single score corner case needs to be fixed on the backend
			datasets={datasets}
			clickBehaviors={clickBehaviors}
			chartLibrary="xenolith"
			stateComponents={{
				empty: (<sn-hr-empty-state type="empty" variant="fit" color={fontColor} />),
				unsupported: (<sn-hr-empty-state type="not-supported" variant="fit" color={fontColor} />),
				needsPermission: (<sn-hr-empty-state type="permission-error" variant="fit" />),
				error: (<sn-hr-empty-state type="loading-error" variant="fit" />),
				loading: (<sn-hr-empty-state type="loading" widget={t('Visualization')} variant="fit" color={fontColor} />),
				realtimeWarning: (<sn-hr-single-score-visual-cue show={isAmbConnectionBroken} />),
			}}
		/>
	);
};

/**
 * Main view of the component which renders all the subviews
 * @param  {Object} state
 */
const view = state => {
	const {
		error,
		loading,
    } = state;

	const {
		fontColor,
		variant,
	} = state.properties;

	return (
		<div className="sn-visualization">
			{loading && <sn-hr-empty-state type="loading" widget={t('Visualization')} variant="fit" color={fontColor} />}
			{error && <sn-hr-single-score-error error={error} color={fontColor} />}
			{!loading && !error &&
				<div className={`sn-visualization-content-container ${variant.toLowerCase()}`} >
					{renderVisualization(state)}
				</div>
			}
		</div>
	);
};

/**
 * A Seismic visualization component for displaying charts based on Performance Analytics and Reporting data sources.
 * It also supports visualizing your own custom datasets that can be passed in as properties along with style configurations for single score chart.
 *
 * ```
 * <sn-component-visualization sysId={sysId} />
 * ```
 * @seismicElement sn-component-visualization
 * @summary Performance Analytics and Reporting visualization component.
 */
export default createCustomElement('sn-hr-single-score', {
	renderer: {
		type: react,
		view,
	},
	properties: {
		/**
		 * Array of datasets containing series of data to be visualized. For SINGLE SCORE only.
		 *
		 * @type {Array}
		 */
		datasets: {
			reflect: false
		},
		/**
		 * Array of flat objects containing styling properties for chart's datasets. First object contains styling properties of the main dataset.
		 * @type {Array}
		 */
		styleConfigurations: {
			reflect: false,
		},
		/**
		 * String representing the sys_id of an existing report or PA widget to be retrived from PAR API.
		 * @type {String}
		 */
		sysId: {
			reflect: true,
			onChange(sysId, previousSysId, { dispatch }) {
				if (sysId)
					// We pass the sysId both as payload and meta (check the onConnect method for the reason behind that)
					dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });
			}
		},
		/**
		 * Array of objects containing data configuration settings for non-persisted data fetching from PAR V2 API.
		 * @type {Array}
		 */
		dataConfigurations: {
			reflect: false,
		},
		/**
		 * Size variant for the List report. Possible string values: 'SMALL', 'MEDIUM', 'LARGE', 'XL'
		 * @type {('SMALL'|'MEDIUM'|'LARGE'|'XL')}
		 */
		variant: {
			reflect: true,
			default: 'SMALL',
		},
		/**
		 * Number of milliseconds for the refreshing interval (e.g. on Workspace)
		 * @type {Number}
		 */
		refreshInterval: {
			reflect: true,
		},
		/**
		 * If enabled, autoClickBehavior will allow for dispatching HR_VISUALIZATION#POINT_CLICKED action with a specific datapoint's payload when clicking on a time series chart.
		 * @type {Boolean}
		 */
		autoClickBehavior: {
			reflect: true,
			default: false,
		},
		/**
		 * If enabled then we will make the component accept real time updates from AMB
		 * NOTE: Passing it to true does not guarantees that widget will be real time widget.
		 * @type {Boolean}
		 */
		isRealtime: {
			reflect: true,
			default: false,
        },
        fontColor: {
            default: '#000'
        }
	},
	styles: styles + parStyles,
	initialState: {
		isAmbConnectionBroken: false
	},
	onConnect(host, { dispatch }) {
		// inject seismic i18n resolved dictionary in our own
		//i18nHelper.setDictionary(i18n().values);

		// This is set by UXBuilder (document.documentElement.lang)
		dateHelper.setLocale(document.documentElement.lang);

		const sysId = getAttributeValue(host, 'sys-id');
		const isRealtime = `${getAttributeValue(host, 'is-realtime')}`;

		if (sysId) {
			dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });

			if (isRealtime === 'true')
				dispatch(RECORD_WATCHER_METADATA_REQUESTED, { sysId }, { sysId });
		}
	},
	behaviors: [
		{
			behavior: visibilityBehavior,
			options: {
				intervalPropName: 'refreshInterval',
				action: ELEMENT_VISIBLE,
			}
		},
		preUserPrefs,
	],
	actions: {
		/* Public */
		[POINT_CLICKED]: {},

		/* Private */
		[SERIES_FETCH_REQUESTED]: { stopPropagation: true },
		[SERIES_FETCH_DATACONFIG_REQUESTED]: { stopPropagation: true },
		[SERIES_FETCH_STARTED]: { stopPropagation: true },
		[SERIES_FETCH_SUCCEEDED]: { stopPropagation: true },
		[ELEMENT_VISIBLE]: { stopPropagation: true },
		[RECORD_WATCHER_SUBSCRIBED]: { stopPropagation: true },
		[RECORD_WATCHER_METADATA_REQUESTED]: { stopPropagation: true },
		[RECORD_WATCHER_METADATA_FETCH_SUCCEEDED]: { stopPropagation: true },
		[RECORD_WATCHER_METADATA_FETCH_FAILED]: { stopPropagation: true },
		[AMB_SUBSCRIPTION_SUCCEEDED]: { stopPropagation: true },
		[AMB_SUBSCRIPTION_FAILED]: { stopPropagation: true },
		[AMB_SUBSCRIPTION_UNSUBSCRIBED]: { stopPropagation: true },
		[AMB_MESSAGE_RECEIVED]: { stopPropagation: true }
	},
	actionHandlers:  {
		...seriesHandlers,
		...recordWatcherHandlers,
		...lifeCycleHandlers,
	}
});
