import { get } from 'lodash';

/**
 * Merge all types of spans to get single list for business calendars
 *
 * @param  {object} targetSpan
 * @param {object} forecastSpan
 *
 * @return mergedSpans
 */
const mergeForecastToTargetsObject = (targetSpan, forecastSpan) => {
	const mergedSpans = targetSpan || [];
	if (Array.isArray(forecastSpan))
		for (let i = 0; i < forecastSpan.length; i++) {
			const obj = {};
			obj.labelName = forecastSpan[i].label_name;
			obj.startDate = forecastSpan[i].start_date;
			obj.tooltipText = forecastSpan[i].tooltip_text;
			mergedSpans.push(obj);
		}
	return mergedSpans;
};

/**
 * Remove duplicate dates from merged spans object
 *
 * @param mergedSpans
 *@return futureSpans
 */
const removeOverlappedDates = (mergedSpans, forecastSpans) => {
	const futureSpans = [];
	const map = new Map();
	if (Array.isArray(mergedSpans))
		for (const item of mergedSpans)
			if (!map.has(item.startDate)) {
				map.set(item.startDate, true);
				futureSpans.push(item);
			}
	return futureSpans;
};

export const getFutureSpans = (chartConfiguration = {}) => {
	const targetSpan = get(chartConfiguration, 'datasets.0.series.0.derived_series.targets.personal.biz_calendar_future_spans')
		|| get(chartConfiguration, 'datasets.0.series.0.derived_series.targets.global.biz_calendar_future_spans');
	const forecastSpan = get(chartConfiguration, 'datasets.0.series.0.forecast.labels');
	const mergedSpans = mergeForecastToTargetsObject(targetSpan, forecastSpan);
	return removeOverlappedDates(mergedSpans);
};
