export const showNoScore = (scoreDetails = {}, styleConfig = {}) => {
	const score = scoreDetails.score;
	const showZero = (typeof scoreDetails.count === 'undefined' || scoreDetails.count > 0);
	if (!score && !styleConfig.show_zero)
		return true;

	return !styleConfig.show_zero && score === '0' && !showZero;
};
