import { dateHelper, i18nHelper } from 'par-components';
import { isForecast } from './is-forecast';

/**
 * In this file, dataPoint is an object that has the following format:
* {
* 	x: 1529798400000,                   -- the timestamp
*   y_data_ds_0_series_0: 34,			-- all the scores for all the datasets
*   y_data_ds_0_series_1: 2,               and series for this timestamp
*	y_data_ds_1_series_0: 22,
*   ...
*   y_change_ds_0_series_0: 4,			-- all the derived series for all the datasets
*   y_change_percent_ds_0_series_0: 2,     and series for this timestamp
*	y_target_ds_0_series_0: 22,
*	y_trendline_ds_0_series_0: 22,
*   ...
* }
*/

/**
* Returns formatted date (adds "(forecasted)" for forecast points)
* @param {object} dataPoint - see above
* @param {object} mainStyleConfiguration - used to get the formatting of the score value
* @return {string}
*/
export const getFormattedDate = (dataPoint, mainStyleConfiguration) => {
	const cellValue = [];
	const frequency = mainStyleConfiguration.frequency || 'daily';
	let xFormattedValue;

	// if a x-axis label formatter is provided, use it
	if (typeof mainStyleConfiguration.x_format === 'function')
		xFormattedValue = mainStyleConfiguration.x_format(dataPoint.x);
	else
		xFormattedValue = dateHelper.readableDate(dataPoint.x, { frequency, type: 'long' });

	cellValue.push(xFormattedValue);
	if (isForecast(dataPoint))
		cellValue.push(i18nHelper.t('(forecasted)'));

	return cellValue.join(' ');
};
