import { DERIVED_SERIES } from '../constants.time-series';

// singleSeries is an element of the array 'series'
// in the API response. It contains id, name, data
// and derived_series (trendline, change, change_percent, etc...) for PA
export default (singleSeries, seriesName) => {
	if (singleSeries[DERIVED_SERIES] && singleSeries[DERIVED_SERIES][seriesName]) {
		const currentDerivedSeries = singleSeries[DERIVED_SERIES][seriesName];
		if (currentDerivedSeries.visible === false)
			return false;

		return true;
	}
	return false;
};
