import { commentsSeriesProperties } from './comments-series-properties';
import { COMMENTS } from '../constants.time-series';

export default (comments, type = COMMENTS, yValue = 0) => {
	if (!comments || (Array.isArray(comments) && comments.length === 0))
		return {};

	return {
		visible: true,
		type: 'line',
		enableMouseTracking: true,
		showInLegend: false,
		name: commentsSeriesProperties[type].name + ' Tooltip',
		width: 32,
		id: 'comments_tooltip',
		states: {
			hover: {
				lineWidthPlus: 0,
			},
		},
		marker: {
			enabled: false,
			states: {
				hover: {
					enabled: false,
				},
			},
		},
		lineWidth: 0,
		data: comments.map(point => ({
			x: point.x,
			text: (point.text || point.name),
			y: yValue,
			value: point.value || undefined,
			sys_id: point.sys_id || undefined,
			user: point.user || undefined,
			title: ' ',
		})),
	};
};
