/**
* Retrieves the trendBySeries in reportTrendConfiguration
* @param {object} reportTrendConfiguration - object that comes from the Trend Reporting API
* it has the following format:
- {
	styleConfigurations: [{..}]
	datasets: [{
		series: trendBySeries <-- retrives this, JSON.parsed
	}]
}
* @returns {object} trendBySeries
*/

export const getTrendBySeries = (reportTrendConfiguration) => {
	if (!reportTrendConfiguration.datasets ||
		!reportTrendConfiguration.datasets[0] ||
		!reportTrendConfiguration.datasets[0].series)
		return {};

	return reportTrendConfiguration.datasets[0].series;
};
