import { DISABLE_INTERACTIONS } from '../../constants';
import disableInteractions from './disable-interactions';
import getStyleConfiguration from '../../helpers/get-style-configuration';
import { CHART_TYPE, AREA, CHART_AREA_GRADIENT_FROM, CHART_AREA_GRADIENT_TO } from '../series/time-series/constants.time-series';

export default (chartConfiguration, disableInteraction = false) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	if (mainStyleConfiguration[DISABLE_INTERACTIONS] === true || disableInteraction)
		return disableInteractions();

	const plotOptions = {
		series: {
			cursor: 'pointer',
			animation: false,
			allowPointSelect: true,
			turboThreshold: 0,
			marker: {
				enabled: true,
				radius: 0.1,
				symbol: 'circle',
				states: {
					hover: {
						radius: 4,
					},
				},
			},
			states: {
				inactive: {
					enabled: false,
				},
			},
		},
	};

	if (mainStyleConfiguration[CHART_TYPE] === AREA &&
		mainStyleConfiguration[CHART_AREA_GRADIENT_FROM] &&
		mainStyleConfiguration[CHART_AREA_GRADIENT_TO])
		plotOptions.series = {
			...plotOptions.series,
			fillColor: {
				...plotOptions.series.fillColor,
				linearGradient: [0, 0, 0, 300],
				stops: [
					[0, mainStyleConfiguration[CHART_AREA_GRADIENT_FROM]],
					[1, mainStyleConfiguration[CHART_AREA_GRADIENT_TO]],
				],
			},
		};

	return {
		plotOptions,
	};
};
