import { getDataPatternsFlag } from 'parser/helpers/get-data-patterns-flag';

/**
* Builds the accessibility options
* @param {object} chartConfiguration - chart configuration
* in the format of the Reporting API
* chartConfiguration.accessibility and chartConfiguration.dataPatterns are added
* in the TimeSeries component
*/

export default (chartConfiguration) => {
	return {
		accessibilityMode: getDataPatternsFlag(chartConfiguration),
		accessibilityConfig: {
			dashPatterns: true,
			monochrome: true,
			strokeWidth: true,
			opacity: true,
			markers: true,
		},
	};
};
