import { DATASET_SERIES } from '../constants';

/**
 *
 * Returns the series of the datasetIndex-th dataset
 * @param  {Object} [chartConfiguration={}, datasetIndex]
 */
export default (chartConfiguration = {}, datasetIndex = 0) => {
	if (chartConfiguration[DATASET_SERIES]
		&& chartConfiguration[DATASET_SERIES].length > datasetIndex
		&& chartConfiguration[DATASET_SERIES][datasetIndex].series)
		return chartConfiguration[DATASET_SERIES][datasetIndex].series;
	return [];
};
