import {actionTypes} from '@servicenow/ui-core';
import {createHttpEffect} from '@servicenow/ui-effect-http';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

import {
	FETCH_SLIDE_DATA,
	FETCH_SLIDE_DATA_SUCCESS,
	FETCH_SLIDE_DATA_FAILURE,
	NO_SLIDE_DATA,
	status
} from './constants';

const _buildSlides = result => {
	if (!result || !result.length)
		return [];

	return result.map(s => ({
		heading: s.heading_text,
		bodyText: s.body_text,
		buttonText: s.button_text,
		buttonLink: s.url,
		backgroundImage: s.background_image,
		richText: s.rich_text,
		useCustomHtml: s.use_custom_html
	}));
};

export const fetchSlideData = createHttpEffect('/api/sn_cd/audience/getContentForWorkspaceInstance?id=:contentIdentifier', {
	method: 'GET',
	pathParams: ['contentIdentifier'],
	successActionType: FETCH_SLIDE_DATA_SUCCESS,
	errorActionType: FETCH_SLIDE_DATA_FAILURE
});

const fetchSlideDataSuccess = ({action, updateProperties}) => {
	const {result} = action.payload;
	const slides = _buildSlides(result);

	updateProperties({
		status: slides.length ? status.READY : status.EMPTY,
		slides
	});
};

const fetchSlideDataFailure = ({updateProperties}) => {
	updateProperties({status: status.ERROR});
};

const noSlideData = ({updateProperties}) => updateProperties({status: status.EMPTY});

const handleComponentBootstrapped = ({properties, dispatch}) => {
	const {contentIdentifier} = properties;

	if (!contentIdentifier)
		dispatch(NO_SLIDE_DATA);

	dispatch(FETCH_SLIDE_DATA, {contentIdentifier});
};

export default {
	[FETCH_SLIDE_DATA]: fetchSlideData,
	[FETCH_SLIDE_DATA_SUCCESS]: fetchSlideDataSuccess,
	[FETCH_SLIDE_DATA_FAILURE]: fetchSlideDataFailure,
	[NO_SLIDE_DATA]: noSlideData,
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped
};
