import buildComments from './build-comments-series';
import buildCommentsTooltip from './build-comments-tooltip-series';
import splitCommentsPerSeries from './split-comments-per-series';
import getYValueForCommentsSeries from './get-y-value-comments';
import {
	DERIVED_SERIES,
	DATA,
	COMMENTS,
} from '../constants.time-series';
import { hasForecast } from '../helpers';

export default (singleSeries = {}, type = COMMENTS) => {
	if (!singleSeries ||
		!singleSeries[DERIVED_SERIES] ||
		!singleSeries[DERIVED_SERIES][type] ||
		!singleSeries[DERIVED_SERIES][type][DATA])
		return [];

	const yValueForCommentTooltipSeries = getYValueForCommentsSeries(singleSeries);
	// seperate the comments from the scores and the forecast
	const { commentsOnScores, commentsOnForecast } = splitCommentsPerSeries(singleSeries, type);
	// put the comments that are on the scores
	const commentsSeries = [];

	if (commentsOnScores && commentsOnScores.length > 0) {
		commentsSeries.push(buildComments(commentsOnScores, type, 'data'));
		commentsSeries.push(buildCommentsTooltip(commentsOnScores, type, yValueForCommentTooltipSeries));
	}

	// if we have comments on the forecast series
	if (hasForecast(singleSeries) && commentsOnForecast && commentsOnForecast.length > 0) {
		commentsSeries.push(buildComments(commentsOnForecast, type, 'forecast'));
		commentsSeries.push(buildCommentsTooltip(commentsOnForecast, type, yValueForCommentTooltipSeries));
	}

	return commentsSeries;
};
