import getAnnotationId from './get-annotation-id';
import { escape } from 'lodash';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
		annotationType, // comments or initiatives
	} = params;
	return singleSeries.derived_series[annotationType].map((dataPoint) => {
		return {
			x: dataPoint.x,
			type: getAnnotationId(annotationType, seriesIndex, datasetIndex),
			message: escape(dataPoint.text), // if not escaped, xss can occur upon hovering on the comment
			header: dataPoint.user_name,
		};
	});
};

