import {
	Y_AXIS_VISIBLE,
	Y_AXIS_TITLE,
	Y_AXIS_TITLE_SIZE,
	Y_AXIS_TITLE_BOLD,
	Y_AXIS_OPPOSITE,
	Y_AXIS_LABEL_BOLD,
	Y_AXIS_LABEL_SIZE,
	Y_AXIS_LABEL_COLOR,
	Y_AXIS_LABEL_FONT,
	Y_AXIS_DISPLAY_GRID,
	Y_AXIS_GRID_DOTTED,
	Y_AXIS_GRID_WIDTH,
	Y_AXIS_GRID_COLOR,
	Y_AXIS_LINE_WIDTH,
	Y_AXIS_LABELS_ENABLED,
	Y_AXIS_OFFSET,
	Y_AXIS_TICK_COLOR,
	Y_AXIS_TICK_LENGTH,
	Y_AXIS_TICK_WIDTH,
	Y_AXIS_LINE_COLOR,
} from './constants.y-axis';
import {
	UNIT,
} from '../series/time-series/constants.time-series';

import getStyleConfiguration from '../../helpers/get-style-configuration';
import isObjectEmpty from '../../helpers/is-object-empty';
import { getValueWithUnit } from 'helpers/value';
import getYAxisTitle from '../../helpers/get-y-axis-title';

export default (chartConfiguration = {}, index = 0) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);
	const currentStyleConfiguration = getStyleConfiguration(chartConfiguration, index);

	if (isObjectEmpty(mainStyleConfiguration) && index !== 0)
		return {};

	if (currentStyleConfiguration[Y_AXIS_VISIBLE] === false)
		return {
			visible: false,
		};

	// Precision configuration has been removed formattingOptions
	// as part of PRB1323168, since it creates duplicate labels on y-axis.
	const formattingOptions = {
		unit: currentStyleConfiguration[UNIT],
		locale: mainStyleConfiguration.locale,
	};

	const yAxis = {
		labels: {
			enabled: mainStyleConfiguration[Y_AXIS_LABELS_ENABLED],
			align: currentStyleConfiguration[Y_AXIS_OPPOSITE] ? 'left' : 'right',
			y: 2,
			style: {
				color: mainStyleConfiguration[Y_AXIS_LABEL_COLOR],
				fontSize: mainStyleConfiguration[Y_AXIS_LABEL_SIZE],
				fontFamily: mainStyleConfiguration[Y_AXIS_LABEL_FONT],
				fontWeight: mainStyleConfiguration[Y_AXIS_LABEL_BOLD] ? 'bold' : 'normal',
			},
			formatter() {
				return getValueWithUnit(this.value, formattingOptions);
			},
		},
		title: {
			text: getYAxisTitle(chartConfiguration, index),
			style: currentStyleConfiguration[Y_AXIS_TITLE] ? {
				fontSize: mainStyleConfiguration[Y_AXIS_TITLE_SIZE],
				fontWeight: mainStyleConfiguration[Y_AXIS_TITLE_BOLD] ? 'bold' : 'normal',
			} : {},
		},
		visible: mainStyleConfiguration[Y_AXIS_VISIBLE],
		opposite: currentStyleConfiguration[Y_AXIS_OPPOSITE],
		tickLength: mainStyleConfiguration[Y_AXIS_TICK_LENGTH],
		tickColor: mainStyleConfiguration[Y_AXIS_TICK_COLOR],
		tickWidth: mainStyleConfiguration[Y_AXIS_TICK_WIDTH],
		lineColor: mainStyleConfiguration[Y_AXIS_LINE_COLOR],
		lineWidth: mainStyleConfiguration[Y_AXIS_LINE_WIDTH],
	};
	if (mainStyleConfiguration[Y_AXIS_LINE_WIDTH])
		yAxis.lineWidth = mainStyleConfiguration[Y_AXIS_LINE_WIDTH];

	if (mainStyleConfiguration[Y_AXIS_LINE_COLOR])
		yAxis.lineColor = mainStyleConfiguration[Y_AXIS_LINE_COLOR];

	if (mainStyleConfiguration[Y_AXIS_TICK_WIDTH])
		yAxis.tickWidth = mainStyleConfiguration[Y_AXIS_TICK_WIDTH];

	if (mainStyleConfiguration[Y_AXIS_OFFSET])
		yAxis.offset = mainStyleConfiguration[Y_AXIS_OFFSET];

	if (mainStyleConfiguration[Y_AXIS_DISPLAY_GRID]) {
		yAxis.gridLineWidth = mainStyleConfiguration[Y_AXIS_GRID_WIDTH];
		yAxis.gridLineColor = mainStyleConfiguration[Y_AXIS_GRID_COLOR];
		if (mainStyleConfiguration[Y_AXIS_GRID_DOTTED])
			yAxis.gridLineDashStyle = 'dot';
	} else
		yAxis.gridLineWidth = 0;

	return yAxis;
};
