import React from 'react';
import { Provider } from 'react-redux';

// Style
import './provider.visualization.scss';

import { storeHelper, i18nHelper } from 'par-components';

import reducers from './reducers.visualization';
import sagas from './sagas.visualization';
import defaultStore from './default-store.visualization';

import StateComponentsType from './prop-type.state-component';

import Visualization from './container.visualization';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		// Check the prop type definition for more info regarding this type
		stateComponents: StateComponentsType.type,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		stateComponents: StateComponentsType.value,
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'VisualizationProvider';

	/**
	 * The constructor of the class
	 */
	constructor(...args) {
		super(...args);

		// We create the store only once (this way we don't destroy the store if
		// there is an error, or if we change the props somehow)
		this.store = storeHelper.getStore(defaultStore, reducers, sagas);
		this.handleRerenderClick = this.handleRerenderClick.bind(this);
	}

	/**
	 * Initial state
	 *
	 * @type {Object}
	 */
	state = {
		error: false,
	};

	/**
	 * Error boundary method
	 *
	 * @param  {Object} error
	 * @param  {Object} info
	 */
	componentDidCatch(error) {
		this.setState({
			error,
		});
	}

	/**
	 * Called when there is an error and the user clicks on rerender
	 *
	 * @return {[type]}
	 */
	handleRerenderClick() {
		this.setState({
			error: false,
		});
	}

	/**
	 * Render the error (since this component is also an error boundary)
	 *
	 * @return {React.Component}
	 */
	renderError() {
		const { t } = i18nHelper;
		const { stateComponents } = this.props;
		const { error } = this.state;

		// If the error is a permission error and there is a provided permission
		// error component, then render that
		if (error !== true && error.type === 'permission-error' && stateComponents && stateComponents.needsPermission)
			return (
				<div className="par-visualization-state-components-container-needs-permission">
					{stateComponents.needsPermission}
				</div>
			);

		// For more explanations on this logic, check the prop type definition
		if (stateComponents && stateComponents.error)
			return (
				<div className="par-visualization-state-components-container-error">
					{stateComponents.error}
				</div>
			);

		return (
			<div className="par-visualization-error-message">
				<div className="error-content">
					<p>{t('An error occurred. The visualization component did not render')}</p>
					<p>{t('Check the console for more details')}</p>
					{error !== true && error.message ? <p>{error.message}</p> : ''}
				</div>

				<button type="button" onClick={this.handleRerenderClick}>
					{t('Rerender')}
				</button>
			</div>
		);
	}

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		if (this.state.error)
			return this.renderError();

		return (
			<Provider store={this.store}>
				<Visualization {...this.props} />
			</Provider>
		);
	}
}
