import moment from 'moment';

export const nowDetailedQuarter = (timestamp) => {
	const periodStart = moment(timestamp);
	// For quarter use only 2 M instead of 1 Q since the date after 1 Q will be actually in the next Q
	const periodEnd = moment(timestamp).add(2, 'M');
	return `Q${periodStart.format('Q')} (${periodStart.format('MMM')} - ${periodEnd.format('MMM YYYY')})`;
};
