import * as constants from './constants.visualization';

export function setDataConfigurations(dataConfigurations) {
	return {
		type: constants.SET_DATA_CONFIGURATIONS,
		payload: {
			dataConfigurations,
		},
	};
}

export function setSysId(sysId) {
	return {
		type: constants.SET_VISUALIZATION_SYS_ID,
		payload: {
			sysId,
		},
	};
}
