import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';

import {isBreachedStage} from './utils/stageUtils';
import getStageHighlightedValueConfig from './utils/getStageHighlightedValueConfig';
import getTooltipMessage from './utils/getTooltipMessage';
import setTargetRef from './utils/setTargetRef';
import convertStringToBoolean from './utils/convertStringToBoolean';

import '../sn-hr-highlighted-value/now-highlighted-value';

const getIcon = (percentage, stageValue) => {
	if ((typeof percentage) === 'number')
		return percentage < 100 ? 'clock-outline' : 'circle-exclamation-outline';

	return !isBreachedStage(stageValue) ? 'clock-outline' : 'circle-exclamation-outline';
}

export default (state, {updateState}) => {
	const {displayValue, stageValue, percentage, targetRef, properties: {showIcon}} = state;

	const {variant, color} = getStageHighlightedValueConfig(stageValue, percentage);
	const tooltipMessage = getTooltipMessage(state);

	const booleanShowIcon = typeof showIcon === 'boolean' ? showIcon : convertStringToBoolean(showIcon);

	return (
		<Fragment>
			{stageValue !== 'loading' ?
				<Fragment>
					<sn-hr-highlighted-value
						className="sn-sla-timer"
						variant={variant}
						color={color}
						label={displayValue}
						aria-label={tooltipMessage || displayValue}
						icon={booleanShowIcon ? getIcon(percentage, stageValue) : ''}
						hook-insert={({elm}) => setTargetRef(elm, updateState)}
						hook-destroy={() => setTargetRef(null, updateState)}
					/>
					{tooltipMessage ? <now-tooltip content={tooltipMessage} target-ref={targetRef} /> : null}
				</Fragment> : null
			}
		</Fragment>
	);
};
