import React from 'react';
import PropTypes from 'prop-types';
import { buildCategory } from '../../parser';
import Highcharts from '../highcharts/component.highcharts';

// Style
import './component.categories.scss';

export default class extends React.Component {
	/**
	 * Define the prop types for the component
	 *
	 * @type {Object}
	 */
	static propTypes = {
		datasets: PropTypes.array,
		styleConfigurations: PropTypes.array,
		clickBehaviors: PropTypes.func,
	};

	/**
	 * The name of the component (safe after minimization)
	 *
	 * @type {string}
	 */
	static displayName = 'Categories';

	/**
	 * Initial state
	 *
	 * @type {Object}
	 */
	state = {
		hcOptions: {},
	};

	/**
	 * Called before the component mounts
	 */
	componentWillMount() {
		if (this.props.datasets && this.props.styleConfigurations && this.props.styleConfigurations.length > 0) {
			const hcOptions = buildCategory({
				style_configurations: this.props.styleConfigurations,
				datasets: this.props.datasets,
			});

			this.setState({
				hcOptions,
			});
		}
	}

	/**
	 * Called when the props change
	 *
	 * @param  {Object} nextProps
	 */
	componentWillReceiveProps(nextProps) {
		if (nextProps.datasets && nextProps.styleConfigurations && this.props.styleConfigurations.length > 0) {
			const hcOptions = buildCategory({
				style_configurations: nextProps.styleConfigurations,
				datasets: nextProps.datasets,
			});

			this.setState({
				hcOptions,
			});
		}
	}

	/**
	 * Render the react component
	 *
	 * @return {React.Component}
	 */
	render() {
		const { hcOptions } = this.state;
		hcOptions.isHighstock = false;
		return (
			<div className="par-categories">
				<Highcharts
					options={hcOptions}
					clickBehaviors={this.props.clickBehaviors}
				/>
			</div>
		);
	}
}
