import { buildSeriesName } from './build-series-name';
/**
* Builds a dataset from from reporting trendby series
* @param {object} singleTrendBySeries - one of the series that is in the array
* reportingTrendSeries.series, each singleSeries corresponds to a dataset
* @param {boolean} hasMultipleDatasets - if the chart has more than one dataset
* parameter used to format the series names in buildSeriesName
* @returns {array} dataset - one dataset from the chart
*/

export const buildOneDataset = (singleTrendBySeries, hasMultipleDatasets = false) => {
	/*
	* series are grouped by => groupby values are x2Values
	*/
	if (singleTrendBySeries.x2values) {
		// using Map to keep the order of the series
		// important for the mapping color-series
		const seriesX2ValueMap = new Map();
		/*
		* create seriesX2ValueMap to easily get the series later
		* initiate a series for each groupBy value and set all yValues to 0
		* Example: x2values: [{displayValue: 'John Petrucci'}, {displayValue: 'Jordan Rudess'}]
		* and xvalues: ["2013", "2014", "2015"] <-- x axis values
		* then seriesX2ValueMap: {
		*	'John Petrucci': { data: [x: 0, y: 0], [x: 10, y: 0], [x: 20, y: 0], name: "[Incident] John Petrucci"}
		*	'Jordan Rudess': { data: [x: 0, y: 0], [x: 10, y: 0], [x: 20, y: 0], name: "[Incident] Jordan Rudess"}
		* }
		*/
		singleTrendBySeries.x2values.forEach((x2value) => {
			seriesX2ValueMap.set(x2value.displayValue, {
				name: buildSeriesName({
					seriesName: singleTrendBySeries.series_name,
					groupByValue: x2value.displayValue,
					hasMultipleDatasets,
				}),
				data: singleTrendBySeries.xvalues.map((xvalue, xValueIndex) => {
					return { x: (xValueIndex * 10), y: 0 };
				}),
			});
		});
		/*
		* then get the actual yValues from singleTrendBySeries.sub_series for each series
		* seriesX2ValueMap becomes: {
		*	'John Petrucci': { data: [x: 0, y: 34], [x: 10, y: 2], [x: 20, y: 45], name: "[Incident] John Petrucci"}
		*	'Jordan Rudess': { data: [x: 0, y: 56], [x: 10, y: 12], [x: 20, y: 32], name: "[Incident] Jordan Rudess"}
		* }
		*/
		singleTrendBySeries.sub_series.forEach((dataPoint, dataPointIndex) => {
			dataPoint.xvalues.forEach((xValue, xValueIndex) => {
				const value = typeof xValue === 'object' ? xValue.displayValue : xValue;
				seriesX2ValueMap.get(value).data[dataPointIndex].y = parseFloat(dataPoint.yvalues[xValueIndex], 10);
			});
		});
		// return the values seriesX2ValueMap
		return {
			series: [...seriesX2ValueMap.values()],
		};
	}

	/*
	* series are not grouped by
	*/
	return {
		series: [
			{
				name: buildSeriesName({
					seriesName: singleTrendBySeries.series_name,
					hasMultipleDatasets,
				}),
				data: singleTrendBySeries.yvalues.map((yValue, yValueIndex) => {
					return {
						x: yValueIndex * 10,
						y: parseFloat(yValue, 10),
					};
				}),
			},
		],
	};
};
