import { dateHelper } from 'par-components';

export default (series, rangeMin, rangeMax) => {
	const seriesPerYear = {};
	const yoySeries = [];
	const yearsBetweenRanges = dateHelper.getMoment(rangeMax).diff(dateHelper.getMoment(rangeMin), 'y');
	if (series && series.data) {
		const seriesDataClone = [...series.data];
		seriesDataClone.forEach((point) => {
			const pointMoment = dateHelper.getMoment(point.x);
			let seriesKey = pointMoment.year();

			// If the interval is >= 1 year and the point is outside the interval, skip it,
			// DO NOT include it in the series to be displayed because we do not care about it
			if (yearsBetweenRanges >= 1 && !(pointMoment.isSameOrAfter(dateHelper.getMoment(rangeMin)) && pointMoment.isSameOrBefore(dateHelper.getMoment(rangeMax))))
				return;

			// If the interval is < 1 year and the month of this point is < the month of the first selected date
			// put the point in the series of the previous year (looking at the chart you can understand why)
			if (yearsBetweenRanges < 1 && pointMoment.month() < dateHelper.getMoment(rangeMin).month()) {
				pointMoment.add((dateHelper.getMoment(rangeMin).year() + 1) - pointMoment.year(), 'y');
				seriesKey -= 1;
			// Else just put the point in the series of the current year
			} else
				pointMoment.add(dateHelper.getMoment(rangeMin).year() - pointMoment.year(), 'y');

			// If the interval is >= 1 year or the point already shifted is in our range
			// include it in the series, otherwise ignore it
			if (yearsBetweenRanges >= 1 || (pointMoment.isSameOrAfter(dateHelper.getMoment(rangeMin)) && pointMoment.isSameOrBefore(dateHelper.getMoment(rangeMax)))) {
				if (!seriesPerYear[seriesKey])
					seriesPerYear[seriesKey] = [];

				seriesPerYear[seriesKey].push({
					...point,
					x: pointMoment.valueOf(),
					scoresName: series.name,
				});
			}
		});
	}
	Object.keys(seriesPerYear).forEach((yearKey) => {
		yoySeries.push({
			name: (yearsBetweenRanges < 1 && dateHelper.getMoment(rangeMax).month() < dateHelper.getMoment(rangeMin).month()) ? yearKey + ' - ' + (parseInt(yearKey, 10) + 1) : yearKey,
			data: seriesPerYear[yearKey],
		});
	});
	return yoySeries;
};
