import moment from 'moment';

export const nowDetailedTwoWeeks = (timestamp) => {
	const periodStart = moment(timestamp);
	const periodEnd = moment(timestamp).add(1, 'w');
	return `W${periodStart.format('W')} - W${periodEnd.format('W Y')} (${periodStart.format('DD MMM')} - ${periodEnd.format('DD MMM')})`;
};
