import { getDefaultColor } from 'helpers/singlescore';

export default {
	/**
	 * Defines the type
	 *
	 * @type {PropTypes}
	 */
	type: PropTypes.shape({
		color: PropTypes.string,
		backgroundColor: PropTypes.string,
	}),

	/**
	 * The default value
	 *
	 * @type {Object}
	 */
	default: {
		color: getDefaultColor().color,
		backgroundColor: getDefaultColor().backgroundColor,
	},
};
