import { createHttpEffect } from '@seismic/effect-http';
import { createAmbSubscriptionEffect } from '@seismic/effect-amb';
import get from 'lodash/get';
import {
	PAR_AMB_CHANNEL_PREFIX,
	RECORD_WATCHER_METADATA_URL,
	RECORD_WATCHER_SUBSCRIBED,
	RECORD_WATCHER_METADATA_REQUESTED,
	RECORD_WATCHER_METADATA_FETCH_SUCCEEDED,
	RECORD_WATCHER_METADATA_FETCH_FAILED,
	AMB_SUBSCRIPTION_FAILED,
	AMB_SUBSCRIPTION_SUCCEEDED,
	AMB_SUBSCRIPTION_UNSUBSCRIBED,
	AMB_MESSAGE_RECEIVED,
	PAR_AMB_CHANNEL_PAD_CHAR,
} from '../constants';

/**
 * This HTTP effect is used to fetch Meta Data for Record Watcher
 * component
 */
const recordWatcherMetaDataHandler = createHttpEffect(`${RECORD_WATCHER_METADATA_URL}:sysId`, {
	method: 'GET',
	pathParams: ['sysId'],
	successActionType: RECORD_WATCHER_METADATA_FETCH_SUCCEEDED,
	errorActionType: RECORD_WATCHER_METADATA_FETCH_FAILED,
});

/**
 *  This will create an amb effect for subscribing to PAR responder
 */
const recordWatcherHandler = createAmbSubscriptionEffect(`${PAR_AMB_CHANNEL_PREFIX}:channelId`, {
	messageReceivedActionType: AMB_MESSAGE_RECEIVED,
	subscribeSucceededActionType: AMB_SUBSCRIPTION_SUCCEEDED,
	subscribeFailedActionType: AMB_SUBSCRIPTION_FAILED,
	unsubscribeSucceededActionType: AMB_SUBSCRIPTION_UNSUBSCRIBED,
	encodeURIComponentForChannelId: false, // while encoding if it generates + (which is valid btoa encoded char) then AMB server throws error
});

/**
 * This effect is called when the record wather AMB meta data is failed
 */
const recordWatcherMetaDataFailure = ({ state, updateState, action }) => {
	if (action.error) {
		updateState({
			...state
		});
	}
};

/**
 * This effect is called when the fetching is done (regardless of an error
 * occurring)
 */
const recordWatcherMetaDataSuccess = ({ dispatch, action, updateState }) => {
	const result = action.payload.result;
	const orderedResponse = {};
	// JSON property does not follow order due to this channel name differs for same widget so sort it alphabetically
	Object.keys(result).sort().forEach(key => orderedResponse[key] = result[key]);
	const channelId = btoa(JSON.stringify(orderedResponse)).replace(/=/g, PAR_AMB_CHANNEL_PAD_CHAR); // eslint-disable-line no-undef

	dispatch(RECORD_WATCHER_SUBSCRIBED, {
		channelId,
		subscribe: true,
	});
	updateState({channelId});
};

/**
 * Handle AMB Subscription Success
 */
const ambSubscriptionSucceeded = ({ state, updateState }) => {
	const {
		datasets,
		properties
	} = state;

	const { isRealtime } = properties;
	//TODO: For latest score we dont have to show timestamp for initial load, need to fix this
	if (isRealtime && Array.isArray(datasets) && datasets.length)
		datasets[0].score_details = Object.assign({}, datasets[0].score_details, { is_realtime : !!datasets[0].score_details.is_realtime });

	updateState({
		...state,
		datasets,
		isAmbConnectionBroken: false
	});
}

/**
 * handle AMB Subscription failure
 */
const ambSubscriptionFailed = ({ state, updateState }) => {
	updateState({
		...state,
		isAmbConnectionBroken: true
	});
};

/**
 * handle real time update
 */
const ambMessageReceived = ({ state, action, updateState }) => {
	const {
		datasets
	} = state;
	if (Array.isArray(datasets) && datasets.length)
		datasets[0].score_details = Object.assign({}, datasets[0].score_details, {...get(action.payload, 'data'), is_realtime : true});
	// TODO: seismic is not re-rendering the component unless we add this timestamp
	const lastSynched = new Date();
	updateState({
		...state,
		datasets,
		lastSynched
	});
};

/**
 * Unsubscribe record watcher when component is removed from the view
 */
const recordWatcherDisconnected = {
	effect({ dispatch, state }) {
		if (state.channelId) {
			dispatch(RECORD_WATCHER_SUBSCRIBED, {
				channelId: state.channelId,
				subscribe: false,
			});
		}
		return {
			...state
		};
	}
};

export default {
	[RECORD_WATCHER_METADATA_REQUESTED]: recordWatcherMetaDataHandler,
	[RECORD_WATCHER_METADATA_FETCH_FAILED]: recordWatcherMetaDataFailure,
	[RECORD_WATCHER_METADATA_FETCH_SUCCEEDED]: recordWatcherMetaDataSuccess,
	[AMB_MESSAGE_RECEIVED]: ambMessageReceived,
	[AMB_SUBSCRIPTION_SUCCEEDED]: ambSubscriptionSucceeded,
	[AMB_SUBSCRIPTION_FAILED]: ambSubscriptionFailed,
	[AMB_SUBSCRIPTION_UNSUBSCRIBED]: recordWatcherDisconnected,
	[RECORD_WATCHER_SUBSCRIBED]: {
		...recordWatcherHandler,
		// TODO : remove this interceptor when seismic fix multiple AMB connection issue
		interceptors: [{
			after(context) {
				const containerId = get(context, 'coeffects.action.meta.id');
				const subscriptionId = get(context, 'effects[0].args[1]');

				if (!containerId || !subscriptionId) {
					console.warn('There might be issue with multiple PAR AMB connections...'); // eslint-disable-line no-console
					return context;
				}
				context.effects[0].args[1] = `${containerId}-${subscriptionId}`; // eslint-disable-line no-param-reassign
				return context;
			}
		}]
	}
};
