/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React from 'react';
import PropTypes from 'prop-types';

// Style
import './component.singlescore.scss';

// Helpers
import { i18nHelper } from 'par-components';

// Prop Types
import ScoreType from './prop-type.score';
import ColorType from './prop-type.color';
import SizeType from './prop-type.size';
import AlignType from './prop-type.align';

// Components
import Score from './score/component.score';
import Change from './change/component.change';
import Footer from './footer/component.footer';
import TimeSeries from '../time-series/component.time-series';

export default class extends React.Component {
	/**
     * Define the prop types for the component
     *
     * @type {Object}
     */
	static propTypes = {
		size: SizeType.type,
		align: AlignType.type,
		onClick: PropTypes.func,

		// Group 2
		realTime: PropTypes.bool,
		date: PropTypes.shape({
			humanDate: PropTypes.string,
			date: PropTypes.string,
		}),
		score: ScoreType.type,
		scoreColor: ColorType.type,
		title: PropTypes.string,
		showTitle: PropTypes.bool,
		titlePosition: PropTypes.oneOf([
			'top', 'bottom',
		]),
		change: PropTypes.shape(Change.propTypes),

		// Group 3
		timeseries: PropTypes.object,
		target: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number,
			PropTypes.bool,
		]),
		gap: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number,
			PropTypes.bool,
		]),
		gapPercent: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.bool,
		]),
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		size: SizeType.default,
		align: AlignType.default,
		score: ScoreType.default,
		scoreColor: ColorType.default,
		titlePosition: 'top',
		showTitle: true,
	};

	/**
     * The name of the component (safe after minimization)
     *
     * @type {string}
     */
	static displayName = 'SingleScore';

	constructor(...args) {
		super(...args);
		this.handleKeyDown = this.handleKeyDown.bind(this);
	}

	/**
	* Event handler for for key-down event on the entire score
	*/
	handleKeyDown(e) {
		// triggers onClick for ENTER and SPACE key
		if (typeof this.props.onClick === 'function' &&
			(e.keyCode === 32 || e.keyCode === 13))
			this.props.onClick();
	}

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const { t } = i18nHelper;
		const {
			size,
			align,
			onClick,
			realtimeWarning,

			// Group 1
			realTime,
			date,
			score,
			scoreColor,
			title,
			showTitle,
			titlePosition,
			change,
			lastUpdated,

			// Group 2
			timeseries,
			target,
			gap,
			gapPercent,
		} = this.props;

		const classes = ['par-singlescore'];
		if (size)
			classes.push(`size-${size}`);

		if (align)
			classes.push(`align-${align}`);

		if (!timeseries)
			classes.push('exception-no-timeseries');

		if (onClick)
			classes.push('clickable');

		// TODO: This is a temporary solution! WCAG and other stuff still need to be discussed
		// If the button is clickable or not
		const role = onClick ? 'link' : 'presentation';
		const ariaLabel = title ? `${title} ${score}` : score;

		// We may need to modify the title position if it's realtime or if there
		// is a date or if the title position is center
		const modifiedTitlePosition = realTime || (date && date.humanDate && date.date) || (titlePosition !== 'top' && titlePosition !== 'bottom') ? 'bottom' : titlePosition;

		// The reason why this layout is split in groups is because of a design
		// decision/requirement
		return (
			<article
				className={classes.join(' ')}
				role={role}
				onClick={onClick}
				// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
				tabIndex={onClick ? 0 : -1}
				aria-label={ariaLabel}
				onKeyDown={this.handleKeyDown}
			>
				<div className="group group-1">
					<div className="parking-spot">
						{showTitle && modifiedTitlePosition === 'top' &&
							<h1>{title}</h1>
						}
						{realTime && lastUpdated &&
							<h3 className="last-updated-time" role="region" aria-live="polite">
								{t('At')}
								<time
									dateTime={lastUpdated}
								>
									{lastUpdated}
								</time>
								<span className="real-time-warning">{realtimeWarning}</span>
							</h3>
						}
						{!realTime && date && date.humanDate && date.date &&
							<time
								className="date"
								dateTime={date.date}
							>
								{date.humanDate}
							</time>
						}
					</div>

					<Score
						value={score}
						color={scoreColor}
						size={size}
					/>
					<div className="parking-spot">
						{change && (change.showValue || change.showPercent) &&
							<Change {...change} />
						}
						{showTitle && modifiedTitlePosition === 'bottom' &&
							<h1>{title}</h1>
						}
					</div>
				</div>

				{(timeseries || target || gap || gapPercent) &&
					<footer className="group group-2">
						{timeseries &&
							<div className="par-singlescore-timeseries">
								<TimeSeries
									datasets={timeseries.datasets}
									styleConfigurations={timeseries.styleConfigurations}
								/>
							</div>
						}
						{(target || gap || gapPercent) &&
							<Footer
								target={target}
								gap={gap}
								gapPercent={gapPercent}
							/>
						}
					</footer>
				}
			</article>
		);
	}
}
