import isSingleScore from 'helpers/chart-type/is-single-score';
import * as chartLibraries from 'component/visualization/chart-library-names';
/**
* Determines when to use Xenolith or Highcharts for time series
* @param {object} props - TimeSeries component props
*/
export default (props) => {
	return props.chartLibrary === chartLibraries.XENOLITH &&
	!isSingleScore(props.styleConfigurations[0].chart_type);
};
