import { i18nHelper } from 'par-components';
import {
	X_AXIS_VISIBLE,
	X_AXIS_TITLE_SIZE,
	X_AXIS_TITLE_BOLD,
	X_AXIS_TITLE_COLOR,
	X_AXIS_TITLE,
	X_AXIS_LABEL_COLOR,
	X_AXIS_LABEL_BOLD,
	X_AXIS_LABEL_SIZE,
	X_AXIS_LABEL_FONT,
	X_AXIS_DISPLAY_GRID,
	X_AXIS_GRID_WIDTH,
	X_AXIS_GRID_DOTTED,
	X_AXIS_GRID_COLOR,
	X_AXIS_OPPOSITE,
	X_AXIS_LINE_WIDTH,
	X_AXIS_LINE_COLOR,
	X_AXIS_TICK_LENGTH,
	X_AXIS_TICK_WIDTH,
	X_AXIS_TICK_COLOR,
	X_AXIS_LABEL_ENABLED,
} from './constants.x-axis';
import getStyleConfiguration from '../../helpers/get-style-configuration';
import { TIME_SERIES, FISCAL_YEAR_START } from '../../constants';
import { xAxisLabelFormatterWithLabelText } from './x-axis-label-formatter';
import getFrequency from '../../helpers/get-frequency';
import { getXaxisLabelName } from '../../helpers/get-label';
import { getFutureSpans } from '../../helpers/get-future-spans';
import { get } from 'lodash';

export default (chartConfiguration = {}, labelSlanted = false, chartType = TIME_SERIES) => {
	const mainStyleConfiguration = getStyleConfiguration(chartConfiguration, 0);

	const xAxis = {
		visible: mainStyleConfiguration[X_AXIS_VISIBLE],
		min: mainStyleConfiguration.min,
		max: mainStyleConfiguration.max,
		crosshair: {
			width: mainStyleConfiguration.crosshair_width,
			color: mainStyleConfiguration.crosshair_color,
			zIndex: mainStyleConfiguration.crosshair_z_index,
		},
		labels: {
			enabled: mainStyleConfiguration[X_AXIS_LABEL_ENABLED],
			style: {
				color: mainStyleConfiguration[X_AXIS_LABEL_COLOR],
				fontSize: mainStyleConfiguration[X_AXIS_LABEL_SIZE],
				fontFamily: mainStyleConfiguration[X_AXIS_LABEL_FONT],
				fontWeight: mainStyleConfiguration[X_AXIS_LABEL_BOLD] ? 'bold' : 'normal',
			},
			rotation: labelSlanted ? -45 : 0,
		},
		title: {
			text: mainStyleConfiguration[X_AXIS_TITLE] || null,
			style: mainStyleConfiguration[X_AXIS_TITLE] ? {
				color: mainStyleConfiguration[X_AXIS_TITLE_COLOR],
				fontSize: mainStyleConfiguration[X_AXIS_TITLE_SIZE],
				fontWeight: mainStyleConfiguration[X_AXIS_TITLE_BOLD] ? 'bold' : 'normal',
			} : {},
		},
		type: chartType === TIME_SERIES ? 'datetime' : 'category',
		opposite: mainStyleConfiguration[X_AXIS_OPPOSITE],
		tickLength: mainStyleConfiguration[X_AXIS_TICK_LENGTH],
		tickColor: mainStyleConfiguration[X_AXIS_TICK_COLOR],
		tickWidth: mainStyleConfiguration[X_AXIS_TICK_WIDTH],
		lineColor: mainStyleConfiguration[X_AXIS_LINE_COLOR],
		lineWidth: mainStyleConfiguration[X_AXIS_LINE_WIDTH],
		endOnTick: false,
		maxPadding: 0,
		startOnTick: false,
		minPadding: 0,
	};

	if (mainStyleConfiguration[X_AXIS_LINE_WIDTH])
		xAxis.lineWidth = mainStyleConfiguration[X_AXIS_LINE_WIDTH];

	if (mainStyleConfiguration[X_AXIS_LINE_COLOR])
		xAxis.lineColor = mainStyleConfiguration[X_AXIS_LINE_COLOR];

	if (mainStyleConfiguration[X_AXIS_TICK_WIDTH])
		xAxis.tickWidth = mainStyleConfiguration[X_AXIS_TICK_WIDTH];

	if (!mainStyleConfiguration.isYoy) {
		if (mainStyleConfiguration.min)
			xAxis.min = mainStyleConfiguration.min;

		if (mainStyleConfiguration.max)
			xAxis.max = mainStyleConfiguration.max;
	}

	if (mainStyleConfiguration[X_AXIS_DISPLAY_GRID]) {
		xAxis.gridLineWidth = mainStyleConfiguration[X_AXIS_GRID_WIDTH];
		xAxis.gridLineColor = mainStyleConfiguration[X_AXIS_GRID_COLOR];
		if (mainStyleConfiguration[X_AXIS_GRID_DOTTED])
			xAxis.gridLineDashStyle = 'dot';
	} else
		xAxis.gridLineWidth = 0;

	if (chartType === TIME_SERIES) {
		const frequency = getFrequency(chartConfiguration, mainStyleConfiguration);
		if (frequency) {
			const { t } = i18nHelper;
			const i18Translations = {
				year: t('Year'),
			};

			// TODO: See how we can apply tick when there are to many points in the chart, and HC will only display ...
			// const frequencyPeriod = dateHelper.frequencyPeriod(mainStyleConfiguration.frequency);
			// if (mainStyleConfiguration.frequency !== 'daily') {
			// 	const tickInterval = moment.duration(frequencyPeriod.value, frequencyPeriod.name).asMilliseconds();
			// 	xAxis.tickInterval = tickInterval;
			// }
			xAxis.labels.formatter = function formatter() {
				const label = getXaxisLabelName(chartConfiguration, this.value);
				return xAxisLabelFormatterWithLabelText(this.value, label, frequency, mainStyleConfiguration.isYoy, mainStyleConfiguration[FISCAL_YEAR_START], i18Translations);
			};
			if (frequency === 'biz_calendar_frequency')
				xAxis.tickPositioner = function tickPositioner() {
					const positions = [];
					const processedXData = get(this, 'chart.series[0].processedXData');
					const futureSpans = getFutureSpans(chartConfiguration);

					// remove duplicate span added for space in targets and forecast
					if (Array.isArray(futureSpans) && Array.isArray(processedXData))
						for (let i = 0; i < futureSpans.length; i++)
							for (let j = 0; j < processedXData.length; j++)
								if (processedXData[j] === futureSpans[i].startDate)
									processedXData.splice(j, 1);

					if (processedXData && Array.isArray(futureSpans))
						for (const span of futureSpans)
							processedXData.push(span.startDate);

					if (processedXData) {
						const len = processedXData.length / 10;
						const iter = parseInt(len, 10) + 1;

						for (let i = processedXData.length - 1; i >= 0; i -= iter)
							positions.push(processedXData[i]);
					}
					return positions.reverse();
				};
		}
	}

	return {
		xAxis: [xAxis],
	};
};
