import { getDefaultColor } from './get-default-color';
import { showNoScore } from './show-no-score';

export const getScoreColor = (scoreDetails = {}, styleConfig = {}) => {
	const color = {
		color: getDefaultColor().color,
		backgroundColor: getDefaultColor().backgroundColor,
	};

	// Only if a score color is passed (the rules have been removed from here
	// and reside in the backend)
	if (scoreDetails.score_color)
		color.color = scoreDetails.score_color;

	// If there is no score, display them as the default colors (b&w)
	if (showNoScore(scoreDetails, styleConfig)) {
		color.color = getDefaultColor().color;
		color.backgroundColor = getDefaultColor().backgroundColor;
	}

	return color;
};
