import getSeriesYKey from '../helpers/get-series-y-key';
import getSeriesId from '../helpers/get-series-id';

export default (params) => {
	const {
		seriesName,
		seriesIndex,
		datasetIndex,
		yAxisName,
	} = params;
	return {
		[getSeriesId(seriesIndex, datasetIndex, 'trend')]: {
			y: getSeriesYKey(seriesIndex, datasetIndex, 'trend'),
			strokeWidth: 2,
			x: 'x',
			axis: yAxisName,
			label: seriesName,
		},
	};
};
