import { i18nHelper, dateHelper } from 'par-components';

import { getNumberValue } from './get-number-value';

/**
 * Returns a value with unit included in it
 *
 * @param  {number}  [number=0]
 * @param  {Object}  [options={}]
 *
 * @return {String}
 */
export const getValueWithUnit = (number = 0, options = {}) => {
	// options.precision
	// options.unit
	// options.tooltip

	const { replaceVars } = i18nHelper;

	if (options.unit && options.unit.is_glide_duration)
		return dateHelper.getDurationValue(number, {
			includeEmptyValues: !options.tooltip,
			short: !options.tooltip,
		});

	const numberValue = getNumberValue(number, {
		precision: options.precision,
		locale: options.locale,
	});

	if (options.unit && options.unit.format)
		return replaceVars(options.unit.format, [numberValue]);

	return numberValue;
};
