import { PERSONAL, GLOBAL, DATA, GLOBAL_FLAGS, PERSONAL_TARGETS_SERIES_ID, GLOBAL_TARGETS_SERIES_ID, PERSONAL_FLAGS } from '../constants.time-series';
import buildTarget from './build-single-target';
import { buildTargetFlags } from './build-target-flags';

export default (targets, extremeMax) => {
	const result = [];
	if (targets[GLOBAL] && targets[GLOBAL][DATA])
		result.push(buildTarget(targets[GLOBAL], false, extremeMax));

	if (targets[GLOBAL_FLAGS] && targets[GLOBAL_FLAGS][DATA])
		result.push(buildTargetFlags(targets[GLOBAL_FLAGS], GLOBAL_TARGETS_SERIES_ID, extremeMax));

	if (targets[PERSONAL] && targets[PERSONAL][DATA])
		result.push(buildTarget(targets[PERSONAL], true, extremeMax));

	if (targets[PERSONAL_FLAGS] && targets[PERSONAL_FLAGS][DATA])
		result.push(buildTargetFlags(targets[PERSONAL_FLAGS], PERSONAL_TARGETS_SERIES_ID, extremeMax));

	return result;
};
