import getSeriesYKey from '../helpers/get-series-y-key';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
		isGlobalTarget,
	} = params;
	const targetId = isGlobalTarget ? 'target' : 'personal_target';
	const targetData = isGlobalTarget ?
		singleSeries.derived_series.targets.global.data :
		singleSeries.derived_series.targets.personal.data;
	return targetData.map((dataPoint) => {
		return {
			x: dataPoint.x,
			[getSeriesYKey(seriesIndex, datasetIndex, targetId)]: dataPoint.y,
		};
	});
};
