import getSeriesYKey from '../helpers/get-series-y-key';

export default (params) => {
	const {
		singleSeries,
		seriesIndex,
		datasetIndex,
	} = params;
	return singleSeries.derived_series.trendline.data.map((dataPoint) => {
		return {
			x: dataPoint[0],
			[getSeriesYKey(seriesIndex, datasetIndex, 'trend')]: dataPoint[1],
		};
	});
};
