import map from "lodash/map";

export const handleDrillDown = ({ state, action, dispatch }) => {
	action.stopPropagation();

	const { payload: { data }} = action;
	const {
		chartOptions: { chartTitle, seriesConfig: { series1: { aggregateQueries, filterQuery, table_name: table } } }
	} = state;

	const listTitle = data ? data.x : chartTitle;

	// query to be used by drilldown (open details page)
	let query = aggregateQueries[listTitle] || '';

	// 'other' returns a query string of nomatch. if so, need to create a query that filters out all hr services
	if (query === 'zzyynomatchaabb')
		query = map(aggregateQueries, q => {
			if (q !== 'zzyynomatchaabb')
				return q.replace('=', '!=');
		}).join('^');

	query = filterQuery + (data && query ? `^${query}` : '');

	let params = {
		listTitle,
		table,
		query,
		columns: 'number,state,opened_for,subject_person,hr_service,short_description,assignment_group,assigned_to',
		disableQuickEdit: true,
	};

	dispatch('ITEM_SELECTED', {
		renderer_type: 'layout',
		params
	});
};
