import getSeriesYKey from '../helpers/get-series-y-key';
import getSeriesId from '../helpers/get-series-id';
import { i18nHelper } from 'par-components';

export default (params) => {
	const {
		seriesIndex,
		datasetIndex,
		isGlobalTarget,
		yAxisName,
		singleSeries,
	} = params;
	const { t } = i18nHelper;
	const targetId = isGlobalTarget ? 'target' : 'personal_target';
	const stroke = isGlobalTarget ? singleSeries.derived_series.targets.global.color : singleSeries.derived_series.targets.personal.color; // line color
	return {
		[getSeriesId(seriesIndex, datasetIndex, targetId)]: {
			y: getSeriesYKey(seriesIndex, datasetIndex, targetId),
			strokeWidth: 2,
			x: 'x',
			stroke,
			fontSize: 11,
			showLabels: true,
			axis: yAxisName,
			label: isGlobalTarget ? t('Target') : t('Personal Target'),
		},
	};
};
