import React from 'react';
import getSeriesYKey from 'parser/xenolith/time-series/series/helpers/get-series-y-key';
import { i18nHelper } from 'par-components';
import { getValueWithUnit } from 'helpers/value';
import { getPrecision } from 'parser/helpers/precision/index';
import { isForecast } from './is-forecast';
import {
	DATA,
	FORECAST,
} from './series-names.constant';

/**
 * dataPoint is an object that has the following format:
* {
* 	x: 1529798400000,                   -- the timestamp
*   y_data_ds_0_series_0: 34,			-- all the scores for all the datasets
*   y_data_ds_0_series_1: 2,               and series for this timestamp
*	y_data_ds_1_series_0: 22,
*   ...
*   y_change_ds_0_series_0: 4,			-- all the derived series for all the datasets
*   y_change_percent_ds_0_series_0: 2,     and series for this timestamp
*	y_target_ds_0_series_0: 22,
*	y_trendline_ds_0_series_0: 22,
*   ...
* }
* see getSeriesYKey method for the convention of naming y keys
*/

/**
* Returns the jsx element for the formatted score/forecast value
* @param {object} dataPoint - see above
* @param {number} seriesIndex - used to get the right score in dataPoint
* @param {number} datasetIndex - used to get the right score in dataPoint
* @param {object} chartConfiguration
* @return {jsx}
*/
export const buildScoreElement = (dataPoint, seriesIndex, datasetIndex, chartConfiguration) => {
	const seriesType = isForecast(dataPoint) ? FORECAST : DATA;
	const mainStyleConfiguration = chartConfiguration.style_configurations[0] || {};
	const formattingOptions = {
		precision: getPrecision(chartConfiguration, 0, 0),
		unit: mainStyleConfiguration.unit,
		locale: mainStyleConfiguration.locale,
	};
	const yValue = dataPoint[getSeriesYKey(seriesIndex, datasetIndex, seriesType)];
	if (typeof yValue === 'undefined' || yValue === null)
		return (
			<div className="par-data-table-cell-score">
				{i18nHelper.t('No Score')}
			</div>
		);

	return (
		<div className="par-data-table-cell-score">
			{getValueWithUnit(yValue, formattingOptions)}
		</div>
	);
};
