import { get } from 'lodash';
import { getFutureSpans } from '../helpers/get-future-spans.js';

export const getXaxisLabelName = (chartConfiguration = {}, day) => {
	const labels = get(chartConfiguration, 'datasets.0.series.0.labels');
	if (labels)
		for (let i = 0; i < labels.length; i++)
			if (labels[i].date === day)
				return labels[i].label_name;

	const futureSpanLabels = getFutureSpans(chartConfiguration);
	if (Array.isArray(futureSpanLabels))
		for (const span of futureSpanLabels)
			if (span.startDate === day)
				return span.labelName;

	return undefined;
};
