// the target series needs to go until the end
// of the x-axis range, it adds the extremeMax as a point
export default (data = [], extremeMax) => {
	if (!extremeMax || !Array.isArray(data) || data.length === 0)
		return data;

	const newData = [...data];
	const newPoint = {
		y: data[data.length - 1].y,
		x: extremeMax,
		dataLabels: {
			enabled: false,
		},
		marker: {
			enabled: false,
		},
	};
	newData.push(newPoint);
	return newData;
};
