import React from 'react';
import PropTypes from 'prop-types';

// Style
import './component.singlescore.scss';

// Helpers
import { getScoreColor, getTimeSeries, showNoScore } from 'helpers/singlescore';
import { i18nHelper } from 'par-components';

// Components
import SingleScore from './component.singlescore';

export default class extends React.Component {
	/**
     * Define the prop types for the component
     *
     * @type {Object}
     */
	static propTypes = {
		datasets: PropTypes.array,
		styleConfigurations: PropTypes.array,
		clickBehaviors: PropTypes.array,
		realtimeWarning: PropTypes.node,
	};

	/**
	 * Define the default prop values for the component
	 *
	 * @type {Object}
	 */
	static defaultProps = {
		datasets: [],
		styleConfigurations: [],
		clickBehaviors: [],
	};

	/**
     * The name of the component (safe after minimization)
     *
     * @type {string}
     */
	static displayName = 'SingleScoreContainer';

	/**
	 * Render the component
	 *
	 * @return {React.Component}
	 */
	render() {
		const {
			datasets,
			styleConfigurations,
			// TODO: Change this and everywhere accross the frontend code to clickBehaviors
			clickBehaviors,
			realtimeWarning,
		} = this.props;

		const styleConfig = styleConfigurations[0] || {};
		const dataset = datasets[0] || {};

		const scoreDetails = dataset.score_details || {};

		const color = getScoreColor(scoreDetails, styleConfig);
		const scoreValue = showNoScore(scoreDetails, styleConfig) ? i18nHelper.t('No Score') : scoreDetails.score + '';
		const timeseries = getTimeSeries(dataset, styleConfig);

		// True for PA and "always" for Reporting
		const showChartTitle = styleConfig.show_chart_title === true || styleConfig.show_chart_title === 'always';

		return (
			<SingleScore
				align={styleConfig.alignment}
				size={styleConfig.size}
				onClick={clickBehaviors[0]}

				realTime={scoreDetails.is_realtime}
				realtimeWarning={realtimeWarning}
				date={{
					humanDate: scoreDetails.human_date,
					date: scoreDetails.score_date,
				}}
				score={scoreValue}
				scoreColor={color}
				title={styleConfig.chart_title}
				showTitle={showChartTitle && Boolean(styleConfig.chart_title)}
				titlePosition={styleConfig.title_vertical_alignment}
				change={{
					value: scoreDetails.change,
					showValue: styleConfig.show_change,
					percent: scoreDetails.change_percent,
					showPercent: styleConfig.show_change_percent,
					arrow: scoreDetails.change_arrow,
					color: scoreDetails.change_color,
					date: scoreDetails.previous_date,
					humanDate: scoreDetails.human_previous_date,
					showDate: styleConfig.show_comparison_date,
				}}
				lastUpdated={scoreDetails.last_updated}

				timeseries={styleConfig.show_trend && timeseries}
				target={styleConfig.show_target && scoreDetails.target}
				gap={styleConfig.show_gap && scoreDetails.gap}
				gapPercent={styleConfig.show_gap_percent && scoreDetails.gap_percent}
			/>
		);
	}
}
