import { commentsSeriesProperties } from './comments-series-properties';
import { COMMENTS } from '../constants.time-series';

export default (comments, type = COMMENTS, idSeriesToFollow = 'data') => {
	if (!comments || (Array.isArray(comments) && comments.length === 0))
		return {};

	return {
		onSeries: idSeriesToFollow,
		visible: true,
		shape: commentsSeriesProperties[type].shape,
		color: '#888888',
		y: -25,
		allowOverlapX: true,
		type: 'flags',
		enableMouseTracking: false,
		showInLegend: false,
		name: commentsSeriesProperties[type].name,
		id: commentsSeriesProperties[type].id,
		data: comments.map(point => ({
			x: point.x,
			value: point.value || undefined,
			sys_id: point.sys_id || undefined,
			user: point.user || undefined,
			title: ' ',
		})),
	};
};
