import buildSeriesData from '../data-series/build-series-data';
import buildSeriesConfig from '../data-series/build-series-config';

/**
* Builds the change percent series
* @param {object} params - object that contains the following:
* datasetStyleConfig - styleConfiguration of the dataset that contains the current series
* singleSeries - the current series with its derived series (confidence band, target, threshold, etc...)
* color - color for this series, either in hexa or rgba
* seriesIndex - index of the current series in the dataset
* datasetIndex - index of the current dataset in chartConfiguration.datasets
*/
export default (params) => {
	const changePercentSeries = params.singleSeries.derived_series.change_percent;
	const { name, color } = changePercentSeries;
	return {
		changePercentSeriesData: buildSeriesData({
			...params,
			singleSeries: changePercentSeries,
			seriesType: 'change_percent',
		}),
		changePercentSeriesConfig: buildSeriesConfig({
			...params,
			seriesName: name,
			...(color ? { color } : {}),
			seriesType: 'change_percent',
			yAxisName: 'change_percent_y_axis',
		}),
	};
};
